import {trim} from 'lodash';
import Option from './option';

export default class Customer {

  constructor (customerData = {}) {
    if (customerData.options) customerData.options = Option.initializeResourceOptions(customerData.options);
    Object.assign(this, customerData);
  }

  getFullName () {
    const firstName = this.getOptionValue('Private', 'FirstName');
    const middleName = this.getOptionValue('Private', 'MiddleName');
    const lastName = this.getOptionValue('Private', 'LastName');

    return [firstName, middleName, lastName].join(' ');
  }

  getOptionValue (entity, subEntity) {
    const option = (this.options || []).find(option => option.id === entity + '_1_' + subEntity);
    return option ? option.value : null;
  }

  getSummary (answers) {
    return (this.options || []).reduce((result, option) => {
      if (trim(answers[option.id]).length) {
        let value = trim(answers[option.id]);

        if (option.type === 'Boolean') {
          value = value === 'true' ? 'ja' : 'nee'; // todo: translations
        }

        if (option.type === 'Selection') {
          var selectedItem = option.items.find(item => item.key === value);
          if (selectedItem !== null && selectedItem !== undefined) {
            value = selectedItem.value;
          }
        }

        result.push({key: trim(option.questionText), value});
      }

      return result;
    }, []);
  }
}



