import {PropTypes} from 'react';
import {defineMessages, injectIntl, FormattedMessage} from 'react-intl';
import DynamicOptionsFormEnhancer from 'enhancers/DynamicOptionsFormEnhancer';
import Dropzone from 'react-dropzone';
import config from 'lib/config';
import {getActiveContractsByDate} from 'lib/contract';
import {connect} from 'react-redux';
import DateInput from 'components/InputTypes/Date/input';
import SelectionInput from 'components/InputTypes/Selection/input';
import BooleanInput from 'components/InputTypes/Boolean/input';
import CfeComponent from 'components/CfeComponent';
import LoadingButton from 'components/buttons/LoadingButton';
import {submit} from 'redux-form';

class FNOL extends CfeComponent {
  static displayName = 'FNOL';

  constructor (props) {
    super(props, 'components/FNOL/index');

    this.state = this.initialState = {
      isContractSelected: false,
      isSubmitDisabled: true,
      activeContractsOnDate: [],
      date: null,
      files: [],
      uploadedFiles: [],
      maxFilesError: false,
      filesWithErrors: [],
      fileIdentifiers: [],
      filesWithFailedUploadErrors: []
    };

    window.addEventListener('dragover', this.preventDefaultExcept([this.isDropzone]));
    window.addEventListener('drop', this. preventDefaultExcept([this.isDropzone]));
  }

  componentWillReceiveProps (nextProps) {
    if (nextProps.shouldReset) {
      this.setState(this.initialState);
    }
  }

  handleChangeDate = (date) => {
    const {props: {contracts}} = this;
    const activeContractsOnDate = getActiveContractsByDate(contracts, date);
    this.setState({date, activeContractsOnDate});
  };

  handleChangeContract = (contractNr) => {
    const {state: {date}} = this;
    this.setState({isContractSelected: true});
    this.props.onChangeContract(contractNr, date);
  };

  handleChangeConfirmation = (isChecked) => {
    this.setState({isSubmitDisabled: !isChecked});
  };

  handleSubmit = () => {
    const {props: {handleSubmit}} = this;
    handleSubmit();
    this.setState(this.initialState);
  };

  removeFile = (fileGuid) => {
    const fileIdentifiers = this.state.fileIdentifiers.filter(f => f.fileGuid !== fileGuid);
    this.props.onChangeFiles(fileIdentifiers);
    this.setState({
      files: this.state.files.filter(f => f.preview !== fileGuid),
      fileIdentifiers
    });
  };

  handleDrop = (files) => {
    const filesToUpload = files.filter(f => f.size <= config.maxFileSize);
    const filesWithErrors = files.filter(f => f.size > config.maxFileSize);

    this.setState({filesWithFailedUploadErrors: []});

    if (this.state.files.length + filesToUpload.length <= config.maxFnolFiles) {
      this.setState({files: this.state.files.concat(filesToUpload)});
      this.props.uploadFiles(filesToUpload, 'incident').then((fileIdentifiers) => {
        let allFileIdentifiers = this.state.fileIdentifiers.concat(fileIdentifiers);
        this.props.onChangeFiles(allFileIdentifiers);
        this.setState({maxFilesError: false, fileIdentifiers: allFileIdentifiers});
      }).catch(() => {
        filesToUpload.map(f => this.removeFile(f.preview));
        this.setState({filesWithFailedUploadErrors: filesToUpload});
      });
    } else {
      this.setState({maxFilesError: true});
    }

    if (filesWithErrors) {
      this.setState({filesWithErrors});
    }
  };

  preventDefaultExcept = (predicates) => {
    return function (e) {
      var passEvery = predicates.every(function (predicate) { return predicate(e); });
      if (!passEvery) {
        e.preventDefault();
      }
    };
  }

  isDropzone = (e) => { return e.target.id === 'dropzone'; }

  render () {
    const hasActiveContracts = this.state.activeContractsOnDate.length > 0;
    const options = [{value: 0, text: this.props.intl.formatMessage(messages.chooseContract)}]
      .concat(this.state.activeContractsOnDate.map((c) => ({
        value: c.contractNr,
        text: `${c.contractNr}: ${c.productName}`
      })));

    const context = {
      ...this,
      DateInput,
      FormattedMessage,
      messages,
      SelectionInput,
      options,
      hasActiveContracts,
      Dropzone,
      config,
      BooleanInput,
      LoadingButton
    };

    return super.render(context);
  }
}

FNOL.propTypes = {
  form: PropTypes.any,
  contracts: PropTypes.array,
  onChangeContract: PropTypes.func.isRequired,
  handleSubmit: PropTypes.func,
  onChangeFiles: PropTypes.func,
  send: PropTypes.func,
  intl: PropTypes.any.isRequired,
  uploadFiles: PropTypes.func.isRequired
};

const messages = defineMessages({
  date: {
    id: 'FNOL.dateLabel',
    description: 'Label for date input',
    defaultMessage: 'Incident date'
  },
  chooseContract: {
    id: 'FNOL.chooseContractLabel',
    description: 'Label for contract choice input',
    defaultMessage: 'Choose contract'
  },
  confirm: {
    id: 'dynamicForm.confirmationCheckboxLabel',
    description: 'Label for confirmation checkbox',
    defaultMessage: 'I confirm these changes'
  },
  maxFileSizeAmount: {
    id: 'FNOL.overMaxFileSize',
    description: 'message for going over file upload size limit',
    defaultMessage: '{filename} is larger than the {sizeMb}MB limit'
  },
  maxFileAmount: {
    id: 'FNOL.overMaxFileAmount',
    description: 'message for going over file upload size limit',
    defaultMessage: 'You can only upload {maxFiles} files'
  },
  fnolDropFile: {
    id: 'FNOL.dropFileLabel',
    description: 'label inside dropfile box',
    defaultMessage: 'Try dropping some files here, or click to select files to upload.'
  },
  submitButtonLabel: {
    id: 'dynamicForm.submitButtonLabel',
    description: 'Label shown on submit button',
    defaultMessage: 'Submit'
  },
  fileUploadError: {
    id: 'FNOL.fileUploadError',
    description: 'unexpected file upload error',
    defaultMessage: 'File upload failed'
  }
});

const mapStateToProps = (state) => ({
  isSavingFnol: state.fnol.isSavingFnol
});

const mapDispatchToProps = (dispatch, ownProps) => ({
  submit: () => dispatch(submit(ownProps.formName))
});

export default connect(mapStateToProps, mapDispatchToProps)(DynamicOptionsFormEnhancer(injectIntl(FNOL)));



