
		module.exports = function(context) {
			return (function() {
				

				if (!React) {
						var React = require("react");
					}

				return (
					<div>
  {!this.props.user &&
  <div>
    <h3>Register User</h3>
    {this.props.registerError && <span>{this.props.registerError}</span>}
    <form onSubmit={this.props.handleSubmit(input => this.handleSubmit(input))}>
      <div className={this.c('form-group', this.props.fields.email.error && 'has-error')}>
        <this.Field id='email-register' name='email' component='input' type='email' className='form-control'
                    placeholder={this.props.intl.formatMessage(this.messages.emailAddress)}/>
        {this.props.fields.email.error && <p className='help-block'>{this.props.fields.email.error}</p>}
      </div>
      <div className={this.c('form-group', this.props.fields.password.error && 'has-error')}>
        <this.Field id='password-register' name='password' component='input' type='password' className='form-control'
                    placeholder={this.props.intl.formatMessage(this.messages.password)}/>
        {this.props.fields.password.error && <p className='help-block'>{this.props.fields.password.error}</p>}
      </div>
      <div className={this.c('form-group', this.props.fields.passwordConfirm.error && 'has-error')}>
        <this.Field id='password-confirm-register' name='passwordConfirm' component='input' type='password'
                    className='form-control'
                    placeholder={this.props.intl.formatMessage(this.messages.passwordConfirm)}/>
        {this.props.fields.passwordConfirm.error &&
        <p className='help-block'>{this.props.fields.passwordConfirm.error}</p>}
      </div>
      <this.LoadingButton className='btn btn-primary' message={this.messages.submit}
                          isLoading={this.props.isOfferLoading} disabled={this.props.invalid}>
        <i className='glyphicon glyphicon-send'/>
      </this.LoadingButton>
    </form>
  </div>
  }
</div>

				);
			}).apply(context);
		};
	


