import React, {PropTypes} from 'react';
import DossierCustomerDetailsView from 'views/DossierCustomerDetailsView/DossierCustomerDetailsView';
import RootContainer from 'containers/RootContainer';

export default class DossierCustomerDetailsContainer extends React.Component {
  static propTypes = {
    store: PropTypes.object.isRequired,
    locale: PropTypes.string.isRequired,
    messages: PropTypes.object.isRequired
  };

  render () {
    return (
      <RootContainer {...this.props}>
        <DossierCustomerDetailsView />
      </RootContainer>
    );
  }
}



