import {PropTypes} from 'react';
import CfeComponent from 'components/CfeComponent';
import ContractDetails from 'components/Contract/details';
import Modal from 'components/Modal';

class ContractDetailsModal extends CfeComponent {
  constructor (props) {
    super(props, 'components/Contract/detailsModal');
  }

  render () {
    return super.render({...this, Modal, ContractDetails});
  }
}

ContractDetailsModal.proptypes = {
  title: PropTypes.string.isRequired,
  isOpen: PropTypes.bool.isRequired,
  closeCallback: PropTypes.func.isRequired
};

export default ContractDetailsModal;



