import React, {PropTypes} from 'react';
import {connect} from 'react-redux';
import {actions} from 'redux/modules/product';
import ProductList from 'components/ProductList';
import RootContainer from 'containers/RootContainer';

export class ProductListContainer extends React.Component {
  componentWillMount () {
    this.props.requestProductList();
  }

  render () {
    return (
      <RootContainer {...this.props}>
        <ProductList products={this.props.products}/>
      </RootContainer>
    );
  }
}

ProductListContainer.propTypes = {
  products: PropTypes.array,
  store: PropTypes.object,
  locale: PropTypes.string,
  messages: PropTypes.object,
  requestProductList: PropTypes.func
};

const mapStateToProps = state => ({products: state.product.list});

const mapDispatchToProps = (dispatch) => ({
  requestProductList: () => dispatch(actions.requestProductList())
});

export default connect(mapStateToProps, mapDispatchToProps)(ProductListContainer);



