import React, {PropTypes} from 'react';
import RootContainer from 'containers/RootContainer';
import OfferRegistrationView from 'views/OfferRegistrationView/OfferRegistrationView';

export default class OfferRegistrationViewContainer extends React.Component {
  static propTypes = {
    store: PropTypes.object.isRequired,
    locale: PropTypes.string.isRequired,
    messages: PropTypes.object.isRequired
  };

  render () {
    return (
      <RootContainer {...this.props}>
        <OfferRegistrationView />
      </RootContainer>
    );
  }
}



