import {fork} from 'redux-saga/effects';
import {watchCalculateRequest} from './calculateSaga';
import {watchCalculationSaveRequest} from './calculationSaga';
import {contractEvents} from './contractSaga';
import {customerEvents} from './customerSaga';
import {watchSavePaymentMethodRequest} from './savePaymentMethodSaga';
import {lookupServiceEvents} from './lookupDataSaga';
import {watchPaymentRequest} from './requestPaymentSaga';
import {flowEvents} from './flowSaga';
import {watchCustomerDossierRequest} from './dossierSaga';
import {watchSteps} from './stepperSaga';
import {authEvents} from './authSaga';
import {feedbackEvents} from './feedbackSaga';
import {emailEvents} from './emailSaga';
import {incidentEvents} from './incidentSaga';
import {dynamicFormEvents} from './dynamicFormSaga';
import {productEvents} from './productSaga';
import {fnolEvents} from './fnolSaga';
import {localizationEvents} from './localizationSaga';
import {offerEvents} from './offerSaga';

// start the daemons
export default function * root () {
  yield [
    fork(fnolEvents),
    fork(productEvents),
    fork(authEvents),
    fork(contractEvents),
    fork(emailEvents),
    fork(incidentEvents),
    fork(feedbackEvents),
    fork(watchCustomerDossierRequest),
    fork(flowEvents),
    fork(watchCalculateRequest),
    fork(lookupServiceEvents),
    fork(watchPaymentRequest),
    fork(customerEvents),
    fork(watchSavePaymentMethodRequest),
    fork(watchSteps),
    fork(watchCalculationSaveRequest),
    fork(dynamicFormEvents),
    fork(localizationEvents),
    fork(offerEvents)
  ];
}



