import util from 'lib/util';
import {PropTypes} from 'react';
import {defineMessages, FormattedMessage} from 'react-intl';
import CfeComponent from 'components/CfeComponent';
import constants from 'lib/constants';

export default class ContractVersionsList extends CfeComponent {
  static displayName = 'ContractVersionsList';

  static propTypes = {
    contractVersions: PropTypes.array.isRequired
  };

  constructor (propTypes) {
    super(propTypes, 'components/Contract/versionsList');
  }

  render () {
    const context = {
      ...this,
      FormattedMessage,
      messages,
      util,
      constants
    };

    return super.render(context);
  }
}

const messages = defineMessages({
  contractVersionNumber: {
    id: 'contract.versionNumber',
    description: 'Table title',
    defaultMessage: 'Version number'
  },
  contractActivationDate: {
    id: 'contract.activationDate',
    description: 'Table title',
    defaultMessage: 'Activation date'
  },
  contractDateInactive: {
    id: 'contract.DateInactive',
    description: 'Table title',
    defaultMessage: 'Date inactive'
  }
});



