
		module.exports = function(context) {
			return (function() {
				

				if (!React) {
						var React = require("react");
					}

				return (
					<div>
  {this.props.offer.error && !this.props.offer.isLoading
    ? <p>Offer not valid: {this.props.offer.error}</p>
    : <div>
      {this.props.user && !this.props.hasActiveCustomer
        ? <this.Customer {...this.props.customer}
                         formName={this.dynamicFormTypes.customerEdit}
                         handleSubmit={this.handleSubmitCustomer}/>
        : <div className='row'>
          <div className='col-sm-6'>
            <this.LoginForm />
          </div>
          <div className='col-sm-6'>
            <this.RegisterForm />
          </div>
        </div>
      }
    </div>
  }
</div>

				);
			}).apply(context);
		};
	


