import CfeComponent from 'components/CfeComponent';
import {PropTypes} from 'react';
import {connect} from 'react-redux';
import {FormattedNumber, defineMessages, FormattedMessage} from 'react-intl';

export class CalculationResult extends CfeComponent {
  static displayName = 'CalculationResult';

  static propTypes = {
    paymentIntervalPremium: PropTypes.object
  };

  constructor (props) {
    super(props, 'components/CalculationResult/index');
  }

  render () {
    const context = {
      ...this,
      FormattedMessage,
      FormattedNumber,
      messages
    };

    return super.render(context);
  }
}

const messages = defineMessages({
  yourPremium: {
    id: 'yourPremium',
    description: 'Message before premium is calculated',
    defaultMessage: 'Your premium: Fill in the form'
  },
  amountBeforeTax: {
    id: 'amountBeforeTax',
    description: 'Message after premium is calculated',
    defaultMessage: 'Amount before tax'
  },
  tax: {
    id: 'tax',
    description: 'Message after premium is calculated',
    defaultMessage: 'Tax'
  },
  amountAfterTax: {
    id: 'amountAfterTax',
    description: 'Message after premium is calculated',
    defaultMessage: 'Amount after tax'
  }
});

const mapStateToProps = (state, ownProps) => ({ calculation: ownProps.calculation ? ownProps.calculation : state.calculation });

export default connect(mapStateToProps)(CalculationResult);




