import {types as paymentMethodTypes, actions as paymentMethodActions} from 'redux/modules/paymentMethod';
import {takeLatest} from 'redux-saga';
import {put} from 'redux-saga/effects';
import {api} from 'lib/api';
import Logger from 'lib/logger';

function * transactionCostsRequest (action = {}) {
  try {
    if (action.paymentMethod) {
      const res = yield api.paymentMethodTransactionCosts.get({paymentMethodType: action.paymentMethod.type.id});
      yield put(paymentMethodActions.receivePaymentTransactionCosts(res.transactionCosts));
    }
  } catch (error) {
    Logger.error(yield error);
  }
}

export function * watchPaymentRequest () {
  while (true) {
    yield [
      takeLatest(paymentMethodTypes.SELECT_PAYMENT_METHOD, transactionCostsRequest)
    ];
  }
}



