import React, {PropTypes} from 'react';
import {actions as lookupDataActions} from 'redux/modules/lookupData';
import {connect} from 'react-redux';
import {mapValues} from 'lodash';
import {identifierObjectToIdentifierString} from 'lib/optionIdentifier';
import Validator from 'lib/validator';
import LookupService from 'lib/lookupService';

export default (ComposedComponent) => {
  class LookupDataEnhancer extends React.Component {
    static propTypes = {
      lookupDataGroups: PropTypes.array.isRequired,
      answers: PropTypes.object,
      formName: PropTypes.string,
      options: PropTypes.array,
      questions: PropTypes.array,
      requestLookupData: PropTypes.func.isRequired,
      resetLookupData: PropTypes.func.isRequired
    };

    constructor (props) {
      super(props);
      this.service = new LookupService({formName: this.props.formName});
    }

    componentWillReceiveProps (nextProps) {
      // this code checks for lookup data related answer changes
      (nextProps.lookupDataGroups || [])
        .forEach(ldg => {
          const answeredGroupStepIndex = this.service.findAnsweredGroupStepIndex(ldg, this.props.answers, nextProps.answers);

          if (answeredGroupStepIndex > -1 && this.areAnswersValid(answeredGroupStepIndex, ldg, nextProps.answers)) {
            this.props.requestLookupData(this.service, answeredGroupStepIndex, ldg, nextProps.answers);
          }

          if (answeredGroupStepIndex > -1 && answeredGroupStepIndex < ldg.steps.length - 1) {
            this.props.resetLookupData(this.service, answeredGroupStepIndex, ldg);
          }
        });
    }

    areAnswersValid = (answeredGroupStepIndex, lookupDataGroup, answers) => {
      const options = lookupDataGroup.steps[answeredGroupStepIndex].inputIds.reduce((acc, inputId) =>
        [...acc, ...this.props.options.filter(option => identifierObjectToIdentifierString(inputId) === option.id)], []);

      const rules = Validator.buildOptionRules(options);
      const validationResult = mapValues(Validator.check(rules, answers), errors => errors[0]);
      return options.length && Object.keys(validationResult).length === 0;
    };

    render () {
      // get all additional options from lookupdata groups
      return <ComposedComponent {...this.props}
                                additionalOptions={this.service.getAllAdditionalOptions(this.props.lookupDataGroups)}/>;
    }
  }

  const mapStateToProps = (state) => ({
    lookupDataGroups: state.lookupData.lookupDataGroups
  });

  const mapDispatchToProps = (dispatch) => ({
    requestLookupData: (service, answeredGroupStepIndex, lookupDataGroup, answers) => dispatch(lookupDataActions.requestLookupData(service, answeredGroupStepIndex, lookupDataGroup, answers)),
    resetLookupData: (service, answeredGroupStepIndex, lookupDataGroup) => dispatch(lookupDataActions.resetLookupData(service, answeredGroupStepIndex, lookupDataGroup))
  });

  return connect(mapStateToProps, mapDispatchToProps)(LookupDataEnhancer);
};



