import {indexOf, extend} from 'lodash';
import Util from 'lib/util';
import Option from 'lib/option';

export default class Calculateable {
  questionSetTypes = ['calculation', 'saveproduct'];
  processedQuestions = [];

  constructor (calculateableData = {}) {
    if (!Object.keys(calculateableData).length) return;
    if (!['productId', 'options'].every(k => k in calculateableData)) return console.error('Cannot start flow. An calculateable object needs an id and options property');

    calculateableData.options = Option.initializeResourceOptions(calculateableData.options);
    extend(this, calculateableData);
  }

  getStepQuestions (step) {
    if (this.options && step.questions) {
      return step.questions.map(q => this.options.filter(o => o.id === q.id)[0]);
    }

    return [];
  }

  getQuestionSet (questionSetName) {
    if (indexOf(this.questionSetTypes, questionSetName) === -1 || !this.options) {
      return;
    }

    return this.options.filter(option => {
      if (indexOf(Util.toLowerCaseArray(option.optionUses || []), questionSetName) !== -1 && indexOf(this.processedQuestions, option.id) === -1) {
        this.processedQuestions.push(option.id);
        return true;
      }
    });
  }
}



