import {actions as customerActions, types} from 'redux/modules/customer';
import {actions as paymentMethodActions} from 'redux/modules/paymentMethod';
import {actions as validationActions} from 'redux/modules/validation';
import {takeLatest} from 'redux-saga';
import {select, put} from 'redux-saga/effects';
import {api} from 'lib/api';
import {getKeyValuePairs} from 'lib/option';
import Logger from 'lib/logger';
import locale from 'lib/locale';
import {defineMessages} from 'react-intl';
import {toastr} from 'react-redux-toastr';
import dynamicFormTypes from 'lib/dynamicFormTypes';

export function * savePaymentMethodRequest ({paymentMethod, newPaymentMethodData}) {
  try {
    const activeCustomer = yield select(state => state.customer.activeCustomer);
    newPaymentMethodData = getKeyValuePairs(newPaymentMethodData, paymentMethod.options);

    // TODO temp fix, api should be adjusted to return a paymentMethod when a paymentMethod is created
    let newPaymentMethod = paymentMethod.id
      ? yield api.paymentMethods.put({customerId: activeCustomer.id, paymentMethodId: paymentMethod.id}, newPaymentMethodData)
      : yield api.paymentMethods.post({customerId: activeCustomer.id}, {
        paymentMethodType: paymentMethod.type.id,
        answers: newPaymentMethodData
      });

    // TODO, add new paymentmethod to state, instead of making a new call
    const paymentMethods = yield api.paymentMethods.get({customerId: activeCustomer.id});
    yield put(customerActions.receiveCustomerPaymentMethods(paymentMethods));

    if (newPaymentMethod.messages && newPaymentMethod.messages[0].insertedKey) {
      const selectedPaymentMethod = paymentMethods.find(p => p.id === newPaymentMethod.messages[0].insertedKey);
      yield put(paymentMethodActions.selectPaymentMethod(selectedPaymentMethod));
    }

    yield put(customerActions.receiveSaveCustomerPaymentMethod(paymentMethods[0]));
    toastr.success(locale.getTranslatedText(messages.paymentMethodSavedSuccess));
  } catch (error) {
    const e = yield error;
    // TODO, find a way to detect in which form the request was fired from (also in other sagas)
    yield put(validationActions.receiveValidation(dynamicFormTypes.paymentMethod, e));
    yield put(validationActions.receiveValidation(dynamicFormTypes.paymentMethodEdit, e));
    yield put(customerActions.requestSaveCustomerPaymentMethodFailure(e));
    toastr.error(locale.getTranslatedText(messages.paymentMethodSavedError));
    Logger.error(e);
  }
}

export function * watchSavePaymentMethodRequest () {
  while (true) {
    yield takeLatest(types.CUSTOMER_PAYMENT_METHOD_SAVE_REQUEST, savePaymentMethodRequest);
  }
}

const messages = defineMessages({
  paymentMethodSavedSuccess: {
    id: 'paymentMethod.paymentMethodSavedSuccess',
    description: 'Message when payment method saved successfully',
    defaultMessage: 'Payment method saved successfully'
  },
  paymentMethodSavedError: {
    id: 'paymentMethod.paymentMethodSavedError',
    description: 'Message when payment method could not be saved',
    defaultMessage: 'Payment method could not be saved'
  }
});



