import {PropTypes} from 'react';
import {defineMessages, FormattedMessage} from 'react-intl';
import util from 'lib/util';
import moment from 'moment';
import CfeComponent from 'components/CfeComponent';

export default class DocumentList extends CfeComponent {
  static propTypes = {
    documents: PropTypes.array.isRequired,
    handleOpenDocument: PropTypes.func.isRequired
  };

  constructor (props) {
    super(props, 'components/Document/list');
  }

  render () {
    const sortedDocuments = this.props.documents.sort((d1, d2) => moment.utc(d2.dateCreated).diff(moment.utc(d1.dateCreated)));

    const context = {
      ...this,
      sortedDocuments,
      FormattedMessage,
      messages,
      util
    };

    return super.render(context);
  }
}

const messages = defineMessages({
  documentName: {
    id: 'document.documentName',
    description: 'Table header for document name',
    defaultMessage: 'Document name'
  },
  dateCreated: {
    id: 'document.dateCreated',
    description: 'Table header for dateCreated',
    defaultMessage: 'Date created'
  },
  documentType: {
    id: 'document.documentType',
    description: 'Table header for documentType',
    defaultMessage: 'Document Type'
  }
});



