import Config from 'lib/config';

class Http {
  constructor (options) {
    this.headers = options.headers || {};
  }

  configure (options) {
    return {
      headers: options.headers ? options.headers : this.headers
    };
  }

  get (url, options = {}) {
    return fetch(Config.divWebApiUrl + url, this.configure(options));
  }

  post (url, data = {}, options = {}) {
    return fetch(Config.divWebApiUrl + url, {...this.configure(options), body: JSON.stringify(data), method: 'post'});
  }

  put (url, data = {}, options = {}) {
    return fetch(Config.divWebApiUrl + url, {...this.configure(options), body: JSON.stringify(data), method: 'put'});
  }

  delete (url, options = {}) {
    return fetch(Config.divWebApiUrl + url, {...this.configure(options), method: 'delete'});
  }
}

export default Http;



