import React from 'react';
import ReactDOM from 'react-dom';
import {setAcceptLanguageHeader, setApiKeyHeader} from 'lib/api';
import {addLocaleData} from 'react-intl';
import nl from 'react-intl/locale-data/nl';
import de from 'react-intl/locale-data/de';
import fr from 'react-intl/locale-data/fr';
import it from 'react-intl/locale-data/it';
import Locale from 'lib/locale';
import Validator from 'lib/validator';
import configureStore from 'redux/configureStore';
import ProductConfigured from 'containers/Product/ProductConfiguredContainer';
import AccountContainer from 'containers/Account/AccountContainer';
import ProductDocumentsContainer from 'containers/Product/ProductDocumentsContainer';
import ProductListContainer from 'containers/Product/ProductListContainer';
import QuoteContainer from 'containers/QuoteContainer';
import DossierContractsContainer from 'containers/Dossier/ContractsContainer';
import DossierCustomerDetailsContainer from 'containers/Dossier/CustomerDetailsContainer';
import DossierDocumentsContainer from 'containers/Dossier/DocumentsContainer';
import DossierIncidentsContainer from 'containers/Dossier/IncidentsContainer';
import DossierPaymentMethodsContainer from 'containers/Dossier/PaymentMethodsContainer';
import CustomerFeedbackFormContainer from 'containers/CustomerFeedbackFormContainer';
import DossierCalculationsContainer from 'containers/Dossier/CalculationsContainer';
import DossierCalculationContainer from 'containers/Dossier/CalculationContainer';
import ProductInformativeValuesListContainer from 'containers/Product/ProductInformativeValuesListContainer';
import DossierEmailsContainer from 'containers/Dossier/EmailsContainer';
import DossierContractDetailsContainer from 'containers/Dossier/ContractDetailsContainer';
import DossierContractEditContainer from 'containers/Dossier/ContractEditContainer';
import SaveCalculationContainer from 'containers/SaveCalculationContainer';
import LocalizationContainer from 'containers/LocalizationContainer';
import RegisterFormContainer from 'containers/Account/RegisterFormContainer';
import LoginFormContainer from 'containers/Account/LoginFormContainer';
import OfferRegistrationContainer from 'containers/Offer/RegistrationContainer';
import config from 'lib/config';

const globalExport = window.cfe || {};
window.cfe = globalExport;

globalExport.initApp = (configData) => {
  config.initConfig(configData);

  const locale = new Locale({locale: config.locale});
  const language = locale.getCurrentLanguage();

  addLocaleData([...nl, ...de, ...fr, ...it]);
  setAcceptLanguageHeader(locale.getCurrentLocale());
  setApiKeyHeader(config.apiKey);

  const store = configureStore({
    config: {
      productConfig: configData.productConfig || {},
      calculationConfig: configData.calculationConfig || {},
      customerConfig: configData.customerConfig || {},
      dossierConfig: configData.dossierConfig || {}
    }
  });

  globalExport.store = store;

  window.onpopstate = (event) => store.dispatch({type: 'WINDOW_ON_POP_STATE', event});

  const renderApp = (translations) => {
    Validator.setMessages(translations.validation);

    const containers = [
      {elementId: 'cfe-account', container: AccountContainer},
      {elementId: 'cfe-quote', container: QuoteContainer},
      {elementId: 'cfe-productlist', container: ProductListContainer},
      {elementId: 'cfe-product-documents', container: ProductDocumentsContainer},
      {elementId: 'cfe-dossier-contracts', container: DossierContractsContainer},
      {elementId: 'cfe-dossier-contract-details', container: DossierContractDetailsContainer},
      {elementId: 'cfe-dossier-contract-edit', container: DossierContractEditContainer},
      {elementId: 'cfe-dossier-calculations', container: DossierCalculationsContainer},
      {elementId: 'cfe-dossier-calculation-resume', container: DossierCalculationContainer},
      {elementId: 'cfe-dossier-customer-details', container: DossierCustomerDetailsContainer},
      {elementId: 'cfe-dossier-emails', container: DossierEmailsContainer},
      {elementId: 'cfe-dossier-documents', container: DossierDocumentsContainer},
      {elementId: 'cfe-dossier-incidents', container: DossierIncidentsContainer},
      {elementId: 'cfe-dossier-payment-methods', container: DossierPaymentMethodsContainer},
      {elementId: 'cfe-dossier-feedback-form', container: CustomerFeedbackFormContainer},
      {elementId: 'cfe-calculation-save-button', container: SaveCalculationContainer},
      {elementId: 'cfe-product-informative-values-list', container: ProductInformativeValuesListContainer},
      {elementId: 'cfe-localization', container: LocalizationContainer},
      {elementId: 'cfe-register-form', container: RegisterFormContainer},
      {elementId: 'cfe-login-form', container: LoginFormContainer},
      {elementId: 'cfe-offer-registration', container: OfferRegistrationContainer},
      {
        elementId: 'cfe-product-configured',
        container: ProductConfigured,
        props: {productConfig: configData.productConfig}
      }
    ];

    const defaultProps = {store: store, locale: language, messages: translations.messages};

    containers.forEach(function (container) {
      const domElement = document.getElementById(container.elementId);
      if (domElement) {
        const props = {...defaultProps, ...container.props};
        const reactElement = React.createElement(container.container, props);
        ReactDOM.render(reactElement, domElement);
      }
    });
  };

  return locale.set().then(renderApp).catch((err) => setTimeout(() => {
    throw err;
  }));
};




