import React from 'react';
import DossierIncidentsView from 'views/DossierIncidentsView/DossierIncidentsView';
import RootContainer from 'containers/RootContainer';

export default class DossierIncidentsContainer extends React.PureComponent {
  render () {
    return (
      <RootContainer {...this.props}>
        <DossierIncidentsView />
      </RootContainer>
    );
  }
}



