import {handleActions, createAction} from 'redux-actions';
import {REHYDRATE} from 'redux-persist/constants';
import clientTypes from 'lib/clientTypes';
import config from 'lib/config';

// TYPES
const SET_ACTIVE_CUSTOMER = 'ITS/CUSTOMER/SET_ACTIVE_CUSTOMER';
const CUSTOMER_RECEIVE = 'ITS/CUSTOMER/CUSTOMER_RECEIVE';
const CUSTOMER_FAILURE = 'CUSTOMER_FAILURE';
const CUSTOMER_SAVE_REQUEST = 'ITS/CUSTOMER/CUSTOMER_SAVE_REQUEST';
const CUSTOMER_SAVE_RECEIVE = 'ITS/CUSTOMER/CUSTOMER_SAVE_RECEIVE';
const CUSTOMER_SAVE_RECEIVE_FAILURE = 'ITS/CUSTOMER/CUSTOMER_SAVE_RECEIVE_FAILURE';
const CUSTOMER_PAYMENT_METHOD_SAVE_REQUEST = 'ITS/CUSTOMER/CUSTOMER_PAYMENT_METHOD_SAVE_REQUEST';
const CUSTOMER_PAYMENT_METHOD_SAVE_RECEIVE = 'ITS/CUSTOMER/CUSTOMER_PAYMENT_METHOD_SAVE_RECEIVE';
const CUSTOMER_PAYMENT_METHOD_SAVE_FAILURE = 'ITS/CUSTOMER/CUSTOMER_PAYMENT_METHOD_SAVE_FAILURE';
const CUSTOMER_TEMPLATE_RECEIVE = 'ITS/CUSTOMER/CUSTOMER_TEMPLATE_RECEIVE';
const CUSTOMER_PAYMENT_METHODS_REQUEST = 'ITS/CUSTOMER/CUSTOMER_PAYMENT_METHODS_REQUEST';
const CUSTOMER_PAYMENT_METHODS_RECEIVE = 'CUSTOMER_PAYMENT_METHODS_RECEIVE';
const CUSTOMER_DOSSIER_REQUEST = 'ITS/CUSTOMER/CUSTOMER_DOSSIER_REQUEST';
const CUSTOMER_DOCUMENTS_REQUEST = 'CUSTOMER_DOCUMENTS_REQUEST';
const CUSTOMER_DOCUMENTS_RECEIVE = 'CUSTOMER_DOCUMENTS_RECEIVE';
const CUSTOMER_DOCUMENTS_RECEIVE_FAILURE = 'CUSTOMER_DOCUMENTS_RECEIVE_FAILURE';

// ACTIONS
const receiveCustomer = (customer) => ({type: CUSTOMER_RECEIVE, customer});
const receiveCustomerFailure = (error) => ({type: CUSTOMER_FAILURE, payload: error}); // uses payload for old action handler in validation reducer
const requestSaveCustomer = (customer, newCustomerData) => ({type: CUSTOMER_SAVE_REQUEST, customer, newCustomerData});
const requestSaveCustomerPaymentMethod = (paymentMethod, newPaymentMethodData) => ({
  type: CUSTOMER_PAYMENT_METHOD_SAVE_REQUEST,
  paymentMethod,
  newPaymentMethodData
});

const requestSaveCustomerPaymentMethodFailure = (error) => ({type: CUSTOMER_PAYMENT_METHOD_SAVE_FAILURE, error});
const receiveSaveCustomerPaymentMethod = (paymentMethod) => ({
  type: CUSTOMER_PAYMENT_METHOD_SAVE_RECEIVE,
  paymentMethod
});
const receiveCustomerTemplate = (customerTemplate) => ({type: CUSTOMER_TEMPLATE_RECEIVE, customerTemplate});
const requestCustomerPaymentMethods = () => ({type: CUSTOMER_PAYMENT_METHODS_REQUEST});
const receiveCustomerPaymentMethods = (paymentMethods) => ({type: CUSTOMER_PAYMENT_METHODS_RECEIVE, paymentMethods});
const requestCustomerDocuments = () => ({type: CUSTOMER_DOCUMENTS_REQUEST});
const receiveCustomerDocuments = (documents) => ({type: CUSTOMER_DOCUMENTS_RECEIVE, documents});
const receiveCustomerDocumentsFailure = (error) => ({type: CUSTOMER_DOCUMENTS_RECEIVE_FAILURE, error});
const requestCustomerDossier = () => ({type: CUSTOMER_DOSSIER_REQUEST});
const requestSaveCustomerFailure = () => (customer) => ({isSavingCustomer: false});

// REDUCER
const handleReceiveCustomer = (state, action) => {
  saveCustomerToStorageAfterLogin(state, action);
  return {...state, activeCustomer: action.customer, isSavingCustomer: false};
};
const handleReceiveCustomerTemplate = (state, action) => ({...state, customerTemplate: action.customerTemplate});
const handleReceiveCustomerPaymentMethods = (state, action) => ({
  ...state,
  paymentMethods: action.paymentMethods,
  isLoadingPaymentMethod: false
});

const handleRequestCustomerDocuments = (state, action) => ({...state, isDocumentListLoading: true});
const handleReceiveCustomerDocuments = (state, action) => ({...state, customerDocuments: action.documents, isDocumentListLoading: false});
const handleReceiveCustomerDocumentsFailure = (state, action) => ({...state, error: action.error, isDocumentListLoading: false});
const handleRequestCustomerDossier = (state, action) => ({...state, isDossierFetched: true});

export default handleActions({
  [CUSTOMER_SAVE_REQUEST]: (state, action) => ({...state, isSavingCustomer: true}),
  [SET_ACTIVE_CUSTOMER]: (state, {payload}) => ({...state, customerToLoad: payload}),
  [CUSTOMER_TEMPLATE_RECEIVE]: handleReceiveCustomerTemplate,
  [CUSTOMER_RECEIVE]: handleReceiveCustomer,
  [CUSTOMER_PAYMENT_METHOD_SAVE_REQUEST]: (state) => ({
    ...state,
    isLoadingPaymentMethod: true,
    isSavingPaymentMethod: true
  }),
  [CUSTOMER_PAYMENT_METHOD_SAVE_RECEIVE]: (state) => ({...state, isSavingPaymentMethod: false}),
  [CUSTOMER_PAYMENT_METHOD_SAVE_FAILURE]: (state) => ({...state, isSavingPaymentMethod: false}),
  [CUSTOMER_PAYMENT_METHODS_RECEIVE]: handleReceiveCustomerPaymentMethods,
  [CUSTOMER_DOCUMENTS_REQUEST]: handleRequestCustomerDocuments,
  [CUSTOMER_DOCUMENTS_RECEIVE]: handleReceiveCustomerDocuments,
  [CUSTOMER_DOCUMENTS_RECEIVE_FAILURE]: handleReceiveCustomerDocumentsFailure,
  [CUSTOMER_DOSSIER_REQUEST]: handleRequestCustomerDossier,
  [REHYDRATE]: (state, action) => ({...state, ...action.payload.customer, isDossierFetched: false})
}, {paymentMethods: [], isLoadingPaymentMethod: false, isSavingCustomer: false});

export const types = {
  SET_ACTIVE_CUSTOMER,
  CUSTOMER_RECEIVE,
  CUSTOMER_DOSSIER_REQUEST,
  CUSTOMER_FAILURE,
  CUSTOMER_SAVE_REQUEST,
  CUSTOMER_SAVE_RECEIVE,
  CUSTOMER_SAVE_RECEIVE_FAILURE,
  CUSTOMER_PAYMENT_METHOD_SAVE_REQUEST,
  CUSTOMER_PAYMENT_METHOD_SAVE_RECEIVE,
  CUSTOMER_PAYMENT_METHOD_SAVE_FAILURE,
  CUSTOMER_PAYMENT_METHODS_REQUEST,
  CUSTOMER_PAYMENT_METHODS_RECEIVE,
  CUSTOMER_TEMPLATE_RECEIVE,
  CUSTOMER_DOCUMENTS_REQUEST,
  CUSTOMER_DOCUMENTS_RECEIVE,
  CUSTOMER_DOCUMENTS_RECEIVE_FAILURE
};

export const actions = {
  receiveCustomer,
  receiveCustomerFailure,
  requestSaveCustomer,
  requestSaveCustomerPaymentMethod,
  receiveSaveCustomerPaymentMethod,
  requestSaveCustomerPaymentMethodFailure,
  requestSaveCustomerFailure,
  receiveCustomerTemplate,
  requestCustomerPaymentMethods,
  receiveCustomerPaymentMethods,
  requestCustomerDocuments,
  receiveCustomerDocuments,
  receiveCustomerDocumentsFailure,
  requestCustomerDossier
};

export const setActiveCustomer = createAction(SET_ACTIVE_CUSTOMER);

// needed as a hack to persist activeCustomer after login
const saveCustomerToStorageAfterLogin = (state, action) => {
  if (config.clientType !== clientTypes.serviceCenter) {
    const storedCustomer = localStorage.getItem('reduxPersist:customer');
    if (!storedCustomer) {
      localStorage.setItem('reduxPersist:customer', JSON.stringify({
        ...state.customer,
        activeCustomer: action.customer
      }));
    }
  }
};



