import React, {PropTypes} from 'react';
import {connect} from 'react-redux';
import Loader from 'components/Loader';

export class ProductDocuments extends React.PureComponent {
  static propTypes = {
    documents: PropTypes.array
  };

  render () {
    if (!this.props.product.insuranceDocuments) return <Loader />;

    return (
      <div>
        <ul>
          {this.props.product.insuranceDocuments.map((d, i) => <li key={i}><a href={d.url} target='_blank'>{d.caption}</a></li>)}
        </ul>
      </div>
    );
  }
}
const mapStateToProps = (state) => ({product: state.product.selected});

export default connect(mapStateToProps)(ProductDocuments);



