import {takeLatest} from 'redux-saga';
import {put, select} from 'redux-saga/effects';
import {api} from 'lib/api';
import {types as offerTypes, actions as offerActions} from 'redux/modules/offer';
import {actions as calculationActions} from 'redux/modules/calculation';
import {types as customerTypes} from 'redux/modules/customer';
import Logger from 'lib/logger';
import url from 'url';

function * requestRegisterOffer () {
  try {
    const [customer, offerHash, calculationsConfig] = yield [
      select(state => state.customer.activeCustomer),
      select(state => state.offer.hash),
      select(state => state.config.dossierConfig.calculationsOverview)
    ];

    if (offerHash && customer && customer.id) {
      const {calculationId} = yield api.contracts.registerOffer.post({customerId: customer.id, offerHash});
      yield put(calculationActions.setCalculationToLoadById(calculationId));

      yield put(offerActions.clearOfferHash());

      window.location.href = calculationsConfig.urls.calculationResume;
    }
  } catch (error) {
    const e = yield error;
    Logger.error(e);
    yield put(offerActions.receiveRegisterOfferFailure(e.messages && e.messages.length && e.messages[0].message));
  }
}

function * checkOfferHash () {
  const customer = yield select(state => state.customer.activeCustomer);
  const {query: {offerHash}} = url.parse(window.location.href, true);
  if (offerHash) yield put(offerActions.setOfferHash(offerHash));
  if (customer) yield requestRegisterOffer();
}

export function * offerEvents () {
  yield [
    takeLatest(offerTypes.CHECK_FOR_OFFER_HASH, checkOfferHash),
    takeLatest(customerTypes.CUSTOMER_RECEIVE, requestRegisterOffer)
  ];
}



