import React from 'react';
import RehydrateManager from 'lib/RehydrateManager';

export default (ComposedComponent) => {
  class RehydrateEnhancer extends React.PureComponent {
    constructor (props) {
      super(props);
      this.RehydrateManager = RehydrateManager.getInstance(props.store);
      this.state = {
        isRehydrated: false
      };
    }

    componentWillMount () {
      this.RehydrateManager.rehydrate().then(() => {
        this.setState({isRehydrated: true});
      });
    }

    render () {
      return <ComposedComponent {...this.props} isRehydrated={this.state.isRehydrated}/>;
    }
  }

  return RehydrateEnhancer;
};



