import {PropTypes} from 'react';
import {injectIntl} from 'react-intl';
import {connect} from 'react-redux';
import PaymentMethodClass from 'lib/paymentMethod';
import PaymentMethodCreator from 'components/PaymentMethod/PaymentMethodCreator';
import PaymentMethodPicker from 'components/PaymentMethod/PaymentMethodPicker';
import {actions as contractActions} from 'redux/modules/contract';
import {actions as stepperActions} from 'redux/modules/stepper';
import {actions as customerActions} from 'redux/modules/customer';
import {actions as paymentMethodActions} from 'redux/modules/paymentMethod';
import CustomerClass from 'lib/customer';
import CfeComponent from 'components/CfeComponent';
import messages from './messages';
import Calculateable from 'lib/calculateable';

class PaymentStep extends CfeComponent {
  static displayName = 'PaymentStep';

  static propTypes = {
    dispatch: PropTypes.func,
    customer: PropTypes.object,
    selectedPaymentMethodId: PropTypes.number,
    customerPaymentMethods: PropTypes.array,
    paymentMethodTemplates: PropTypes.array,
    intl: PropTypes.object,
    productId: PropTypes.number,
    contractId: PropTypes.number,
    payment: PropTypes.object,
    isLoadingPaymentMethod: PropTypes.bool,
    isSavingAndSigningContract: PropTypes.bool,
    calculateable: PropTypes.instanceOf(Calculateable)
  };

  constructor (props) {
    super(props, 'views/ProductView/PaymentStep');
  }

  handlePayment = () => this.props.requestContractSaveAndSign(this.props.selectedPaymentMethod);

  createPaymentMethod = (newPaymentMethodData, paymentMethod) => {
    this.props.requestSaveCustomerPaymentMethod(paymentMethod, newPaymentMethodData);
  };

  handleSelectPaymentMethod = (paymentMethod) => {
    this.props.selectPaymentMethod(paymentMethod);
  };

  render () {
    const {
      props: {
        customerPaymentMethods,
        paymentMethodTemplates,
        selectedPaymentMethod,
        customer,
        isLoadingPaymentMethod,
        isSavingAndSigningContract,
        intl: {formatMessage}
      }
    } = this;

    const commonPaymentProps = {
      paymentMethodTemplates
    };

    const paymentMethodCreatorProps = {
      ...commonPaymentProps,
      handleSubmit: this.createPaymentMethod
    };

    const paymentMethodPickerProps = {
      ...commonPaymentProps,
      customerPaymentMethods,
      isLoadingPaymentMethod,
      selectedPaymentMethodId: selectedPaymentMethod.id,
      handleCreatePaymentMethod: this.createPaymentMethod,
      handleSubmit: this.handlePayment,
      handleSelectPaymentMethod: this.handleSelectPaymentMethod,
      isSavingAndSigningContract: isSavingAndSigningContract
    };

    return super.render({
      ...this,
      PaymentMethodCreator,
      PaymentMethodPicker,
      customerPaymentMethods,
      paymentMethodCreatorProps,
      paymentMethodPickerProps,
      messages,
      formatMessage,
      customer
    });
  }
}

const mapStateToProps = (state) => ({
  contractId: state.contract.id,
  payment: state.payment,
  paymentMethodTemplates: (state.paymentMethod.list || []).map(p => new PaymentMethodClass(p)),
  customerPaymentMethods: (state.customer.paymentMethods || []).map(p => new PaymentMethodClass(p)),
  selectedPaymentMethod: state.paymentMethod.selected,
  validation: state.validation,
  customer: new CustomerClass(state.customer.activeCustomer),
  isLoadingPaymentMethod: state.customer.isLoadingPaymentMethod,
  isSavingAndSigningContract: state.contract.isSavingAndSigningContract
});

const mapDispatchToProps = (dispatch) => ({
  setActiveStep: (stepIndex) => dispatch(stepperActions.setActiveStep(stepIndex)),
  requestSaveCustomerPaymentMethod: (paymentMethod, newPaymentMethodData) => dispatch(customerActions.requestSaveCustomerPaymentMethod(paymentMethod, newPaymentMethodData)),
  requestContractSaveAndSign: (selectedPaymentMethod) => dispatch(contractActions.requestContractSaveAndSign(selectedPaymentMethod)),
  selectPaymentMethod: (paymentMethod) => dispatch(paymentMethodActions.selectPaymentMethod(paymentMethod)),
  dispatch
});

export default connect(mapStateToProps, mapDispatchToProps)(injectIntl(PaymentStep));



