import {getKeyValuePairs} from 'lib/option';
import {types, actions as calculationActions} from 'redux/modules/calculation';
import {takeLatest} from 'redux-saga';
import {call, put, select} from 'redux-saga/effects';
import {api} from 'lib/api';
import {getFormValues} from 'redux-form';
import Logger from 'lib/logger';
import locale from 'lib/locale';
import {defineMessages} from 'react-intl';
import {toastr} from 'react-redux-toastr';
import dynamicFormTypes from 'lib/dynamicFormTypes';

const delay = (ms) => new Promise(resolve => setTimeout(resolve, ms));

function * requestCalculations () {
  try {
    const customer = yield select(state => state.customer.activeCustomer);
    let calculations = yield api.calculations.get({customerId: customer.id});

    yield put(calculationActions.receiveCalculations(calculations));
  } catch (error) {
    const e = yield error;
    yield put(calculationActions.receiveCalculationsFailure(e));
    Logger.error(e);
  }
}

function * calculationDeleteRequest ({calculation}) {
  try {
    const [customer, calculations] = yield [
      select(state => state.customer.activeCustomer),
      select(state => state.calculations.list)
    ];

    yield api.calculations.delete({customerId: customer.id, calculationId: calculation.calculationId});

    const filteredCalculations = calculations.filter(c => c.calculationId !== calculation.calculationId);

    yield put(calculationActions.receiveCalculations(filteredCalculations));
    toastr.success(locale.getTranslatedText(messages.calculationDeletedSuccess));
  } catch (error) {
    const e = yield error;
    yield put(calculationActions.receiveCalculationsFailure(e));
    toastr.error(locale.getTranslatedText(messages.calculationDeletedError));
    Logger.error(e);
  }
}

export function * calculationSaveRequest () {
  yield call(delay, 200);

  try {
    const [productCalculationAnswers, productSaveAnswers, calculateable] = yield [
      select(state => getFormValues(dynamicFormTypes.calculation)(state)),
      select(state => getFormValues(dynamicFormTypes.extraQuestions)(state)),
      select(state => state.flow.calculateable)
    ];

    const calculationInput = getKeyValuePairs({...productCalculationAnswers, ...productSaveAnswers}, calculateable.options);

    const [customer, calculations] = yield [
      select(state => state.customer.activeCustomer),
      select(state => state.calculations)];

    // TODO figure out how to map the calculateable options from redux instead of always putting those in flow.calculateable so it's more clear what's being calculated
    const calculationId = (calculations && calculations.id) ? calculations.id : calculateable.calculationId;

    let calculation;
    if (calculationId) {
      calculation = yield api.calculations.put({customerId: customer.id, calculationId: calculationId}, {
        answers: calculationInput,
        productId: calculateable.productId
      });
    } else {
      calculation = yield api.calculations.post({customerId: customer.id}, {
        answers: calculationInput,
        productId: calculateable.productId
      });
    }

    yield put(calculationActions.receiveSaveCalculation(calculation));
    toastr.success(locale.getTranslatedText(messages.calculationSavedSuccess));
  } catch (error) {
    const e = yield error;
    yield put(calculationActions.receiveSaveCalculationFailure(e));
    toastr.error(locale.getTranslatedText(messages.calculationSavedError));
    Logger.error(e);
  }
}

export function * watchCalculationSaveRequest () {
  yield [
    takeLatest(types.CALCULATIONS_REQUEST, requestCalculations),
    takeLatest(types.CALCULATION_SAVE_REQUEST, calculationSaveRequest),
    takeLatest(types.CALCULATION_DELETE_REQUEST, calculationDeleteRequest)
  ];
}

const messages = defineMessages({
  calculationDeletedSuccess: {
    id: 'calculation.calculationDeletedSuccess',
    description: 'Message when calculation deleted successfully',
    defaultMessage: 'Calculation deleted successfully'
  },
  calculationDeletedError: {
    id: 'calculation.calculationDeletedError',
    description: 'Message when calculation could not be deleted',
    defaultMessage: 'Calculation could not be deleted'
  },
  calculationSavedSuccess: {
    id: 'calculation.calculationSavedSuccess',
    description: 'Message when calculation saved successfully',
    defaultMessage: 'Calculation saved successfully'
  },
  calculationSavedError: {
    id: 'calculation.calculationSavedError',
    description: 'Message when calculation could not be saved',
    defaultMessage: 'Calculation could not be saved'
  }
});



