import {PropTypes} from 'react';
import CustomerClass from 'lib/customer';
import dossierFetcher from 'enhancers/dossierFetcher';
import {injectIntl, defineMessages} from 'react-intl';
import {actions as customerActions} from 'redux/modules/customer';
import {connect} from 'react-redux';
import {reset} from 'redux-form';
import Customer from 'components/Customer';
import CfeComponent from 'components/CfeComponent';
import dynamicFormTypes from 'lib/dynamicFormTypes';

export class DossierCustomerDetailsView extends CfeComponent {
  static displayName = 'DossierCustomerDetailsView';

  static propTypes = {
    customerConfig: PropTypes.object,
    customer: PropTypes.object,
    dispatch: PropTypes.func,
    intl: PropTypes.object
  };

  constructor (props) {
    super(props, 'views/DossierCustomerDetailsView/DossierCustomerDetailsView');

    this.state = {
      readOnly: true
    };
  }

  handleClickEditCustomer = () => {
    let readOnly = !this.state.readOnly;

    this.setState({
      readOnly
    });

    if (readOnly) {
      this.props.dispatch(reset(dynamicFormTypes.customerEdit));
    }
  };

  handleSubmitCustomer = (newCustomerData) => {
    this.props.requestSaveCustomer(this.props.customer, newCustomerData);
    this.setState({readOnly: true});
  };

  componentWillReceiveProps (nextProps) {
    if (nextProps.validationMessages && nextProps.validationMessages.length) {
      this.setState({
        readOnly: false
      });
    }
  }

  getStepQuestions () {
    if (this.props.customer.options && this.props.customerConfig && this.props.customerConfig.questions) {
      return this.props.customerConfig.questions.map(q => this.props.customer.options.filter(o => o.id === q.id)[0]);
    }

    return this.props.customer.options;
  }

  render () {
    const {customer} = this.props;
    customer.options = this.getStepQuestions();

    const context = {
      ...this,
      Customer,
      messages,
      formatMessage: this.props.intl.formatMessage,
      dynamicFormTypes
    };

    return super.render(context);
  }
}

const messages = defineMessages({
  edit: {
    id: 'customerDetails.edit',
    description: 'Label on customer edit button',
    defaultMessage: 'Edit details'
  },
  cancelEdit: {
    id: 'customerDetails.cancelEdit',
    description: 'Label on customer cancel editing button',
    defaultMessage: 'Cancel editing'
  }
});

const mapStateToProps = (state) => ({
  customer: new CustomerClass(state.customer.activeCustomer || state.customer.customerTemplate),
  validationMessages: state.validation.customer,
  customerPaymentMethods: state.customer.paymentMethods,
  customerConfig: state.config.customerConfig
});

const mapDispatchToProps = (dispatch) => ({
  requestSaveCustomer: (customer, newCustomerData) => dispatch(customerActions.requestSaveCustomer(customer, newCustomerData)),
  dispatch
});

export default connect(mapStateToProps, mapDispatchToProps)(dossierFetcher(injectIntl(DossierCustomerDetailsView)));



