import validate from 'validate.js';
import moment from 'moment';
import {each} from 'lodash';
import DateUtil from './date';

const flagsMapper = new Map([
  ['IgnoreCase', 'i'],
  ['Multiline', 'm']
]);

export default class Validator {
  validationMessages = [];

  static setMessages (validationMessages) {
    this.validationMessages = validationMessages;

    validate.options = {fullMessages: false};
    validate.validators.presence.options = {message: validationMessages.presence};
    validate.validators.email.options = {message: validationMessages.email};
    validate.validators.length.options = {
      tooLong: validationMessages.tooLong,
      tooShort: validationMessages.tooShort
    };

    validate.validators.datetime.options = {
      tooEarly: validationMessages.tooEarly,
      tooLate: validationMessages.tooLate
    };

    validate.extend(validate.validators.datetime, {
      parse: (value) => +moment.utc(value, DateUtil.formats),
      format: (value) => moment.utc(value).format('DD-MM-YYYY')
    });

    validate.extend(validate.validators.numericality, {
      parse: (value) => value.toString()
    });

    validate.validators.decimal = function (value, options) {
      if (validate.isNumber(options)) {
        options = {precision: options};
      }
      options = validate.extend({}, this.options, options);

      const message = options.message || this.message || 'is invalid';
      const precision = options.precision;

      const strValue = String(value);
      const pattern = new RegExp(`^[0-9]+((\.|[eE]*)[0-9]{${precision},})?$`);
      const match = pattern.exec(strValue);
      if (!match || match[0].length !== strValue.length) {
        return message;
      }
    };

    validate.validators.integer = function (value, options) {
      options = validate.extend({}, this.options, options);

      const message = options.message || this.message || 'is invalid';

      const strValue = String(value);
      const pattern = new RegExp(`^[0-9]+$`);
      const match = pattern.exec(strValue);
      if (!match || match[0].length !== strValue.length) {
        return message;
      }
    };
  };

  static formatMessage (message = '', values = {}) {
    each(values, (value, key) => {
      message = message.replace('{' + key + '}', value);
    });

    return message;
  }

  static buildOptionRules (options = {}) {
    let rules = {};
    options.forEach((option) => {
      rules[option.id] = this.getRulesForOption(option);
    });

    return rules;
  }

  static getRulesForOption (option) {
    if (!option.isRelevant) return {};

    let rule = {
      presence: option.isMandatory,
      exclusion: {
        within: ['null'],
        message: this.validationMessages.makeChoice
      }
    };

    if (option.isReadonly) return rule;

    switch (option.type.toLowerCase()) {
      case 'string':
        if (option.regex) {
          rule.format = {
            pattern: option.regex.pattern,
            flags: this.getFlags(option.regex.options),
            message: option.regex.message
          };
        }

        rule.length = {
          minimum: option.minLength,
          maximum: option.maxLength
        };
        break;
      case 'datetime':
        rule.datetime = {
          dateOnly: true,
          earliest: option.minValue,
          latest: option.maxValue,
          message: this.validationMessages.invalidDatetime
        };
        break;
      case 'currency':
      case 'integer':
      case 'decimal':
        rule.numericality = {
          greaterThanOrEqualTo: option.minValue,
          lessThanOrEqualTo: option.maxValue
        };

        if (option.precision) {
          rule.decimal = {
            precision: option.precision,
            message: this.formatMessage(this.validationMessages.invalidPrecision, {precision: option.precision})
          };
        } else {
          rule.integer = {
            message: this.validationMessages.invalidInteger
          };
        }
        break;
    }

    return rule;
  }

  static getFlags (regexOptions) {
    return regexOptions
      .split(', ')
      .map(e => flagsMapper.get(e))
      .reduce((prev, curr) => prev + curr);
  }

  static check (rules, input) {
    return validate(input, rules) || {};
  }
}



