import {takeLatest} from 'redux-saga';
import {put, call} from 'redux-saga/effects';
import {actionTypes as formTypes} from 'redux-form';
import {actions as validationActions} from 'redux/modules/validation';

const delay = ms => new Promise(resolve => setTimeout(resolve, ms));

function * clearValidationMessage ({meta}) {
  yield call(delay, 200);

  try {
    yield put(validationActions.clearServerValidationMessage(meta.form, meta.field));
  } catch (error) {
    console.log(yield error);
  }
}

export function * dynamicFormEvents () {
  yield [
    takeLatest(formTypes.CHANGE, clearValidationMessage)
  ];
}



