import {PropTypes} from 'react';
import CfeComponent from 'components/CfeComponent';
import {FormattedMessage} from 'react-intl';

export class LoadingButton extends CfeComponent {
  static propTypes = {
    disabled: PropTypes.bool,
    isLoading: PropTypes.bool,
    onClick: PropTypes.func,
    visibleWhenLoggedOn: PropTypes.bool,
    message: PropTypes.object.isRequired,
    className: PropTypes.string
  };

  constructor (props) {
    super(props, 'components/buttons/LoadingButton');
  }

  render () {
    return super.render({...this, FormattedMessage});
  }
}

export default LoadingButton;



