import React, {PropTypes} from 'react';
import {connect} from 'react-redux';
import ProductView from 'views/ProductView/ProductView';
import {actions as flowActions} from 'redux/modules/flow';
import RootContainer from 'containers/RootContainer';

export class ProductConfiguredContainer extends React.PureComponent {
  componentWillMount () {
    if (this.props.productConfig) {
      this.props.requestStartProductFlow(this.props.productConfig.id);
    }
  }

  render () {
    return (
      <RootContainer {...this.props}>
        <ProductView />
      </RootContainer>
    );
  }
}

ProductConfiguredContainer.propTypes = {
  productConfig: PropTypes.object,
  requestStartProductFlow: PropTypes.func,
  store: PropTypes.object.isRequired,
  locale: PropTypes.string.isRequired,
  messages: PropTypes.object.isRequired
};

const mapDispatchToProps = (dispatch) => ({
  requestStartProductFlow: (productId) => dispatch(flowActions.requestStartProductFlow(productId))
});

export default connect(null, mapDispatchToProps)(ProductConfiguredContainer);



