import {PropTypes} from 'react';
import {injectIntl} from 'react-intl';
import CfeComponent from 'components/CfeComponent';
import {connect} from 'react-redux';
import EmailsList from 'components/Email/list';
import Modal from 'components/Modal';
import {actions as emailActions} from 'redux/modules/email';
import {getAndOpenEmailAttachmentPdfInNewTab} from 'lib/pdf';

export class DossierEmailsView extends CfeComponent {
  static displayName = 'DossierEmailsView';

  static propTypes = {
    emails: PropTypes.array,
    customer: PropTypes.object,
    isEmailsListLoading: PropTypes.bool
  };

  constructor (props) {
    super(props, 'views/DossierEmailsView/DossierEmailsView');

    this.state = {
      activeEmail: {},
      isConfirmationModalOpen: false
    };
  }

  componentWillMount () {
    this.props.requestEmails();
  }

  handleClickOpenEmail = (email) => {
    this.props.requestEmailBody(email);

    this.setState({
      activeEmail: email,
      isConfirmationModalOpen: true
    });
  }

  handleClickOpenAttachment = (attachmentFile) => {
    getAndOpenEmailAttachmentPdfInNewTab(this.props.customer.id, attachmentFile.channelEmailId, attachmentFile.channelEmailAttachmentId);
  }

  modalClosedCallback = () => {
    this.setState({
      activeEmail: {},
      isConfirmationModalOpen: false}
    );
  }

  render () {
    const context = {
      ...this,
      EmailsList,
      Modal
    };

    return super.render(context);
  }
}

const mapStateToProps = (state) => ({
  emails: state.emails.list,
  emailBodyAttachmentFiles: state.emails.emailBodyAttachmentFiles,
  customer: state.customer.activeCustomer
});

const mapDispatchToProps = (dispatch) => ({
  requestEmails: () => dispatch(emailActions.requestEmails()),
  requestEmailBody: (email) => dispatch(emailActions.requestEmailBody(email))
});

export default connect(mapStateToProps, mapDispatchToProps)(injectIntl(DossierEmailsView));



