import {takeLatest} from 'redux-saga';
import {call, put, select} from 'redux-saga/effects';
import {types, actions as lookupDataActions} from 'redux/modules/lookupData';
import {actions as validationActions} from 'redux/modules/validation';
import {change} from 'redux-form';
import {identifierObjectToIdentifierString} from 'lib/optionIdentifier';
import Logger from 'lib/logger';
import dynamicFormTypes from 'lib/dynamicFormTypes';

const delay = (ms) => new Promise(resolve => setTimeout(resolve, ms));

const getAnswersFromLookupData = options => options.map(o => ({
  inputId: identifierObjectToIdentifierString(o.id),
  answer: o.value
}));

function * lookupDataRequest ({service, answeredGroupStepIndex, lookupDataGroup, answers}) {
  yield call(delay, 150);
  let lookupDataGroups = yield select(state => state.lookupData.lookupDataGroups);

  try {
    let existingLookupGroups = lookupDataGroups.filter(lookupDataGroup => service.isExisitingLookupDataGroup(lookupDataGroup));
    let lookupGroups = lookupDataGroups.filter(lookupDataGroup => !service.isExisitingLookupDataGroup(lookupDataGroup));

    if (existingLookupGroups.length > 0) {
      existingLookupGroups = yield service.getExistingLookupDataGroups(existingLookupGroups, answers);
      lookupDataGroups = [...existingLookupGroups, ...lookupGroups];
    }

    if (lookupGroups.length === 0 || service.isExisitingLookupDataGroup(lookupDataGroup)) {
      return yield put(lookupDataActions.receiveLookupData(lookupDataGroups));
    }

    const group = lookupDataGroup.steps[answeredGroupStepIndex];
    const groupsAnswers = service.getGroupAnswers(group, answers);

    // only do a call if some answers are given
    if (!allAnswersEmpty(groupsAnswers) && (group.service && group.service.url)) {
      const lookupData = yield service.fetchGroupLookupData(group, groupsAnswers);
      lookupDataGroups = service.mergeLookupDataGroups(lookupDataGroups, group, lookupData);
      yield put(lookupDataActions.receiveLookupData(lookupDataGroups));
      // set form values from lookupdata
      if (lookupData.additionalOptions) {
        const answers = getAnswersFromLookupData(lookupData.additionalOptions);
        yield answers.map(a => put(change(service.formName, a.inputId, a.answer)));
      }
    } else {
      yield put(lookupDataActions.receiveLookupData(lookupDataGroups));
    }
  } catch (error) {
    const e = yield error;
    yield put(validationActions.receiveValidation(dynamicFormTypes.calculation, e));
    Logger.error(e);
  }
}

function allAnswersEmpty (groupsAnswers) {
  return groupsAnswers.filter(a => a.value !== null && a.value !== undefined && a.value !== '').length === 0;
}

function * resetLookupData ({service, answeredGroupStepIndex, lookupDataGroup}) {
  const lookupDataGroups = yield select(state => state.lookupData.lookupDataGroups);

  // clear form values
  const inputsToReset = service.getInputIdsToReset(answeredGroupStepIndex, lookupDataGroup);
  yield inputsToReset.map(inputId => put(change(service.formName, inputId, '')));
  const resetLookupDataGroups = service.resetLookupDataGroupSteps(answeredGroupStepIndex, lookupDataGroups, lookupDataGroup);
  yield put(lookupDataActions.receiveLookupData(resetLookupDataGroups));
}

export function * lookupServiceEvents () {
  yield [
    takeLatest(types.REQUEST_LOOKUP_DATA, lookupDataRequest),
    takeLatest(types.RESET_LOOKUP_DATA, resetLookupData)
  ];
}



