
		module.exports = function(context) {
			return (function() {
				

				if (!React) {
						var React = require("react");
					}

				return (
					<div>
  { this.props.isLoadingContractList
    ? <this.Loader/>
    : <div>
        <this.ContractList
          contracts={this.props.contracts}
          handleClickView={this.handleClickView}
          handleClickRemove={this.openTerminateModal}
          shouldUseModals={this.props.shouldUseModals}
        />
        <this.ContractDetailsModal
          title={`${this.state.activeContract.productName} ${this.state.activeContract.contractNr}`}
          isOpen={this.state.isDetailsModalOpen}
          closeCallback={this.modalClosedCallback} />
        <this.Modal
          title={this.props.intl.formatMessage(this.messages.terminateContract)}
          open={this.state.isConfirmationModalOpen}
          closeCallback={this.modalClosedCallback}
          id='confirmation-modal'>
          <div className='modal-body'>
            <this.TerminateReasons
              formName={this.dynamicFormTypes.contractTerminate}
              answers={this.props.terminateReasonsAnswers}
              handleSubmit={this.terminateContract}
              {...this.props.terminateReasons}/>
          </div>
        </this.Modal>
      </div>
  }
</div>

				);
			}).apply(context);
		};
	


