import React, {PropTypes} from 'react';
import {injectIntl} from 'react-intl';
import {connect} from 'react-redux';
import DocumentList from 'components/Document/list';
import Loader from 'components/Loader';
import {getAndOpenPdfDocumentInNewTab} from 'lib/pdf';
import {actions as customerActions} from 'redux/modules/customer';

export class DossierDocumentsView extends React.Component {
  static displayName = 'DossierDocumentsView';

  static propTypes = {
    dispatch: PropTypes.func,
    customer: PropTypes.object,
    documents: PropTypes.array,
    requestCustomerDocuments: PropTypes.func
  };

  componentWillMount () {
    this.props.requestCustomerDocuments();
  }

  handleOpenDocument = (document) => {
    getAndOpenPdfDocumentInNewTab(this.props.customer.id, document.id);
  };

  render () {
    const {props: {documents}} = this;

    return (
      documents
        ? <DocumentList
            documents={documents}
            handleOpenDocument={this.handleOpenDocument}/>
        : <Loader/>
    );
  }
}

const mapStateToProps = (state) => ({
  documents: state.customer.customerDocuments,
  customer: state.customer.activeCustomer
});

const mapDispatchToProps = (dispatch) => ({
  requestCustomerDocuments: () => dispatch(customerActions.requestCustomerDocuments())
});

export default connect(mapStateToProps, mapDispatchToProps)(injectIntl(DossierDocumentsView));



