import {PropTypes} from 'react';
import Product from 'components/Product';
import CfeComponent from 'components/CfeComponent';
import CustomerClass from 'lib/customer';
import {injectIntl} from 'react-intl';
import {connect} from 'react-redux';
import {getFormValues} from 'redux-form';
import {actions as stepperActions} from 'redux/modules/stepper';
import Calculateable from 'lib/calculateable';
import dynamicFormTypes from 'lib/dynamicFormTypes';
import formComponentTypes from 'lib/formComponentTypes';

class ExtraQuestionsStep extends CfeComponent {
  static displayName = 'ExtraQuestionsStep';

  constructor (props) {
    super(props, 'views/ProductView/ExtraQuestionsStep');
  }

  nextStep = () => this.props.setActiveStep(this.props.step.stepIndex + 1);

  getStepQuestions = () => this.props.calculateable.getStepQuestions(this.props.step);

  render () {
    let options = this.getStepQuestions();

    const context = {
      ...this,
      Product,
      options,
      nextStep: this.nextStep,
      formatMessage: this.props.intl.formatMessage,
      isLoading: this.props.isSavingContract,
      stepKey: this.props.step.stepKey || 'default',
      formComponent: this.props.step.formComponent || formComponentTypes.dynamicOptionsForm,
      dynamicFormTypes
    };

    return super.render(context);
  }
}

ExtraQuestionsStep.propTypes = {
  customer: PropTypes.object,
  calculateable: PropTypes.instanceOf(Calculateable),
  setActiveStep: PropTypes.func,
  saveContractAnswers: PropTypes.object
};

const mapStateToProps = (state) => ({
  customer: new CustomerClass(state.customer.activeCustomer),
  saveContractAnswers: getFormValues(dynamicFormTypes.extraQuestions)(state)
});

const mapDispatchToProps = (dispatch) => ({
  setActiveStep: (stepIndex) => dispatch(stepperActions.setActiveStep(stepIndex))
});

export default connect(mapStateToProps, mapDispatchToProps)(injectIntl(ExtraQuestionsStep));



