
		module.exports = function(context) {
			return (function() {
				

				if (!React) {
						var React = require("react");
					}

				return (
					<div>
  {{
    default: (
      <div>
        <this.Product
          {...this.props.product}
          hideSubmitButton
          shouldSubmitWhenValid={this.props.step.shouldSubmitWhenValid}
          formName={this.dynamicFormTypes.calculation}
          formComponent={this.formComponent}
          stepKey={this.stepKey}
          options={this.options}
          answers={this.props.calculationAnswers}
          handleSubmit={this.handleCalculation}/>
        {this.props.step.isLastStepOfType
          ? <div>
            <this.LoadingButton
              className="btn btn-primary"
              onClick={this.handleGoToCustomerDataStep}
              message={this.localMessages.submitButtonLabel}
              isLoading={this.props.isLoading}
              disabled={!this.canContinueAfterCalculationStep}>
              <i className='glyphicon glyphicon-send'/>
            </this.LoadingButton>
          </div>
          :
          <button
            className='btn btn-primary'
            type='submit'
            disabled={!this.props.step.isValid} onClick={this.nextStep}>
            Next
          </button>}
      </div>
    )
  }[this.stepKey]}
</div>




				);
			}).apply(context);
		};
	


