
		module.exports = function(context) {
			return (function() {
				

				if (!React) {
						var React = require("react");
					}

				return (
					<table className='table table-hover'>
  <thead>
  <tr>
    <th><this.FormattedMessage {...this.messages.documentName}/></th>
    <th><this.FormattedMessage {...this.messages.documentType}/></th>
    <th><this.FormattedMessage {...this.messages.dateCreated}/></th>
  </tr>
  </thead>
  <tbody>
  {this.sortedDocuments.map(document => {
    return (
      <tr key={document.id} onClick={() => this.props.handleOpenDocument(document)}>
        <td>{document.name}</td>
        <td>{document.type}</td>
        <td>{this.util.formatDate(document.dateCreated)}</td>
      </tr>);
  })}
  </tbody>
</table>

				);
			}).apply(context);
		};
	


