import {PropTypes} from 'react';
import {defineMessages, FormattedMessage, FormattedHTMLMessage} from 'react-intl';
import CfeComponent from 'components/CfeComponent';
import LoadingButton from 'components/buttons/LoadingButton';
import localMessages from 'views/ProductView/messages';

export default class Summary extends CfeComponent {
  static propTypes = {
    summaryLists: PropTypes.array,
    documents: PropTypes.array,
    handleSubmit: PropTypes.func.isRequired
  };

  constructor (props) {
    super(props, 'components/Summary/index');
    this.state = {
      disabled: true
    };
  }

  handleChange = (e) => {
    this.setState({
      disabled: !e.target.checked
    });
  };

  render () {
    const {summaryLists, documents} = this.props;
    let confirmationStatementUrl = '';
    if (documents) {
      confirmationStatementUrl = documents.filter(doc => doc.type === 'confirmationStatement').pop().url;
    }

    const context = {
      ...this,
      summaryLists,
      documents,
      confirmationStatementUrl,
      FormattedHTMLMessage,
      FormattedMessage,
      LoadingButton,
      messages,
      localMessages
    };

    return super.render(context);
  }
}

const messages = defineMessages({
  agree: {
    id: 'summary.agree',
    description: 'I confirm',
    defaultMessage: 'Yes, I agree'
  },
  confirmationStatement: {
    id: 'summary.confirmationStatement',
    description: 'Confirm if customer has read the agreement',
    defaultMessage: 'Yes, I have read the <a href={link} target=\'_blank\'>agreement<\/a>and understood the content.'
  }
});



