import c from 'classnames';
import {PropTypes} from 'react';
import CfeComponent from 'components/CfeComponent';

export default class Section extends CfeComponent {
  static displayName = 'Section';

  static propTypes = {
    title: PropTypes.string,
    index: PropTypes.number,
    onHeaderClick: PropTypes.func,
    children: PropTypes.node,
    isOpen: PropTypes.bool,
    isValid: PropTypes.bool,
    touched: PropTypes.bool,
    errors: PropTypes.array
  };

  constructor (props) {
    super(props, 'components/Stepper/Section');
  }

  componentWillReceiveProps (nextProps) {
    if (this.isTemplateOverriden) {
      super.componentWillReceiveProps(nextProps);
    }
  }

  render () {
    let sectionClass = '';
    const {children, title, onHeaderClick, isOpen, isValid, isVisited, errors, index} = this.props;
    const hasErrors = errors && errors.length > 0;

    if (isVisited) {
      sectionClass = isValid ? 'section-valid' : '';
    }

    if (hasErrors) {
      sectionClass = isVisited ? 'section-invalid' : '';
    }

    const context = {errors, sectionClass, children, title, onHeaderClick, isOpen, index, hasErrors, c, isValid, isVisited};

    return super.render(context);
  }
}



