import {PropTypes} from 'react';
import util from 'lib/util';
import {defineMessages, FormattedMessage} from 'react-intl';
import Loader from 'components/Loader';
import moment from 'moment';
import CfeComponent from 'components/CfeComponent';

export default class Incident extends CfeComponent {
  static propTypes = {
    incidents: PropTypes.array,
    isLoading: PropTypes.bool,
    handleClickRow: PropTypes.func
  };

  constructor (props) {
    super(props, 'components/Incident/list');
  }

  componentWillMount () {
    if (this.isTemplateOverriden) {
      super.componentWillMount();
    }
  }

  componentDidMount () {
    if (this.isTemplateOverriden) {
      super.componentDidMount();
    }
  }

  componentWillReceiveProps (nextProps) {
    if (this.isTemplateOverriden) {
      super.componentWillReceiveProps(nextProps);
    }
  }

  componentDidUpdate () {
    if (this.isTemplateOverriden) {
      super.componentDidUpdate();
    }
  }

  render () {
    const sortedIncidents = (this.props.incidents || []).sort((i1, i2) => moment.utc(i2.dateIncident).diff(moment.utc(i1.dateIncident)));

    const context = {
      ...this,
      Loader,
      FormattedMessage,
      sortedIncidents,
      messages,
      util
    };

    return super.render(context);
  }
}

const messages = defineMessages({
  contractNumber: {
    id: 'contract.number',
    description: 'Table title',
    defaultMessage: 'Contract number'
  },
  contractDescription: {
    id: 'incident.contractDescription',
    description: 'Table title',
    defaultMessage: 'Contract'
  },
  incidentDate: {
    id: 'incident.date',
    description: 'Table title',
    defaultMessage: 'Date of the incident'
  },
  incidentReportDate: {
    id: 'incident.reportDate',
    description: 'Table title',
    defaultMessage: 'Incident report date'
  },
  incidentState: {
    id: 'incident.state',
    description: 'Table title',
    defaultMessage: 'Status'
  }
});



