
		module.exports = function(context) {
			return (function() {
				

				if (!React) {
						var React = require("react");
					}

				return (
					<this.Stepper
  steps={this.props.steps}
  onHeaderClick={this.handleClickStepperHeader}>
  {this.props.steps.map((s, i) => {
    return {
      calculation: (
        <this.Section isOpen={s.isOpened} title={this.formatMessage(this.messages.calculate)} errors={this.getValidationMessages(this.dynamicFormTypes.calculation)} key={i}>
          <this.CalculationStep calculateable={this.props.calculateable} step={s} />
        </this.Section>
      ),
      customer: (
        <this.Section isOpen={s.isOpened} title={this.formatMessage(this.messages.customerData)} errors={this.getValidationMessages(this.dynamicFormTypes.customer)} key={i}>
          <this.CustomerStep step={s} />
        </this.Section>
      ),
      saveContract: (
        <this.Section isOpen={s.isOpened} title={this.formatMessage(this.messages.additionalQuestions)} errors={this.getValidationMessages(this.dynamicFormTypes.extraQuestions)} key={i}>
          <this.ExtraQuestionsStep calculateable={this.props.calculateable} step={s} />
        </this.Section>
      ),
      summary: (
        <this.Section isOpen={s.isOpened} title={this.formatMessage(this.messages.summary)} errors={this.getValidationMessages('summary')} key={i}>
          <this.SummaryStep step={s} />
        </this.Section>
      ),
      payment: (
        <this.Section isOpen={s.isOpened} title={this.formatMessage(this.messages.paymentSectionTitle)} errors={this.getValidationMessages(this.dynamicFormTypes.paymentMethod)} key={i}>
          {this.customer.id && <this.PaymentStep calculateable={this.props.calculateable} step={s} /> }
        </this.Section>
      ),
      closing: (
        <this.Section isOpen={s.isOpened} title='' key={i}>
          <this.SignedContract
            step={s}
            signedContractHeader={this.formatMessage(this.messages.signedContractHeader)}
            signedContractBody={this.formatMessage(this.messages.signedContractBody, {insuranceName: "this.props.product.selected.name"})}/>
        </this.Section>
      )
    }[s.type]
  })}

</this.Stepper>

				);
			}).apply(context);
		};
	


