import CfeComponent from 'components/CfeComponent';
import {PropTypes} from 'react';

export default class SignedContract extends CfeComponent {
  static propTypes = {
    signedContractHeader: PropTypes.string.isRequired,
    signedContractBody: PropTypes.string.isRequired
  };

  constructor (props) {
    super(props, 'components/SignedContract/index');
  }

  render () {
    return super.render(this);
  }
}



