import config from 'lib/config';
import clientTypes from 'lib/clientTypes';
import {put, take, fork, select} from 'redux-saga/effects';
import {api} from 'lib/api';
import {types, actions as customerActions} from 'redux/modules/customer';
import {actions as contractActions} from 'redux/modules/contract';
import {actions as paymentMethodActions} from 'redux/modules/paymentMethod';
import Logger from 'lib/logger';

export function * dossierRequest (action) {
  try {
    let customer;
    const stateCustomer = yield select(state => state.customer);

    // Check if there already is an active customer
    if (!stateCustomer || !stateCustomer.activeCustomer) {
      if (config.clientType === clientTypes.cms) {
        // for cms we currently support only 1 customer per user, fetch it and set it as active
        const customers = yield api.customers.get();
        customer = customers[0];
      } else if (config.clientType === clientTypes.serviceCenter && action.payload.customerId) {
        // for service center, wait for the client to set the customer to load via SET_ACTIVE_CUSTOMER
        customer = yield api.customers.get({customerId: action.payload.customerId});
      }

      yield put(customerActions.receiveCustomer(customer));
    }

    customer = stateCustomer.activeCustomer || customer;

    const [contracts, paymentMethods, paymentMethodTemplates] = yield [
      api.contracts.get({customerId: customer.id}),
      api.paymentMethods.get({customerId: customer.id}),
      api.paymentMethodTemplates.get()
    ];

    yield [
      put(contractActions.receiveContracts(contracts)),
      put(customerActions.receiveCustomerPaymentMethods(paymentMethods)),
      put(paymentMethodActions.receivePaymentMethodTemplates(paymentMethodTemplates))
    ];
  } catch (error) {
    const e = yield error;
    yield put(contractActions.receiveContractsFailure(e));
    Logger.error(e);
  }
}

export function * watchCustomerDossierRequest () {
  while (true) {
    const action = yield take([types.CUSTOMER_DOSSIER_REQUEST, types.SET_ACTIVE_CUSTOMER]);
    yield fork(dossierRequest, action);
  }
}




