import React, {PropTypes} from 'react';
import {connect} from 'react-redux';
import {actions as authActions} from 'redux/modules/auth';
import Navbar from 'components/Navbar';
import CustomerClass from 'lib/customer';
import RootContainer from 'containers/RootContainer';

export class AccountContainer extends React.Component {
  render () {
    return (
      <RootContainer {...this.props}>
        <Navbar customer={this.props.customer} logout={this.props.logout} user={this.props.auth.user}/>
      </RootContainer>
    );
  }
}

AccountContainer.propTypes = {
  logout: PropTypes.func,
  auth: PropTypes.object,
  customer: PropTypes.object,
  store: PropTypes.object,
  locale: PropTypes.string,
  messages: PropTypes.object
};

const mapStateToProps = state => ({
  auth: state.auth,
  customer: new CustomerClass(state.customer.activeCustomer)
});

const mapDispatchToProps = dispatch => ({
  logout: (accessToken) => dispatch(authActions.logout(accessToken))
});

export default connect(mapStateToProps, mapDispatchToProps)(AccountContainer);



