import {types as authTypes, actions as authActions} from 'redux/modules/auth';
import {takeLatest} from 'redux-saga';
import {put} from 'redux-saga/effects';
import {actions as customerActions} from 'redux/modules/customer';
import Auth from 'lib/auth';
import {api} from 'lib/api';
import Logger from 'lib/logger';

function * requestExternalLogin ({identityProvider}) {
  try {
    const user = yield new Auth().login(identityProvider);
    yield put(authActions.setUser(user));
  } catch (error) {
    Logger.error(yield error);
  }
}

function * requestLogin ({credentials}) {
  try {
    const user = yield new Auth().loginUsingTokenApi(credentials);
    yield put(authActions.setUser(user));
    yield put(authActions.receiveLogin());
  } catch (error) {
    const e = yield error;
    yield put(authActions.receiveLoginFailure(e.error));
    Logger.error(e);
  }
}

function * getCustomers () {
  const [customers, customerTemplate] = yield [
    api.customers.get(),
    api.customerTemplates.get({type: 'NaturalPerson'})
  ];

  yield [
    put(customerActions.receiveCustomer(customers[0])),
    put(customerActions.receiveCustomerTemplate(customerTemplate))
  ];
}

function * logout ({accessToken}) {
  new Auth().logout(accessToken);
  window.location = '/login';
}

function * requestRegister ({credentials}) {
  try {
    yield new Auth().register(credentials);
    yield requestLogin({credentials});
    yield put(authActions.receiveRegister());
  } catch (error) {
    const e = yield error;
    const errorMessage = e.Errors && e.Errors.length ? e.Errors[0] : 'Unexpected error';
    yield put(authActions.receiveRegisterFailure(errorMessage));
  }
}

export function * authEvents () {
  yield [
    takeLatest(authTypes.EXTERNAL_LOGIN_REQUEST, requestExternalLogin),
    takeLatest(authTypes.LOGIN_REQUEST, requestLogin),
    takeLatest(authTypes.SET_USER, getCustomers),
    takeLatest(authTypes.LOGOUT, logout),
    takeLatest(authTypes.REGISTER_REQUEST, requestRegister)
  ];
}



