import url from 'url';

/* Language class that watches the URL  */
export default class Locale {
  constructor (config) {
    this.config = config;
  }

  // Sets a translation
  // Storing the translations file in localstorage
  set () {
    localStorage.removeItem('translations');
    return new Promise((resolve, reject) => {
      const {query: {lang}} = url.parse(window.location.href, true);

      // Checks if locale is selected in URL
      try {
        if (lang) {
          const translations = this.storeTranslationsForLocale({locale: lang});
          return resolve(translations);

          // checks if translations file is already stored in localstorage
        } else if (localStorage.getItem('translations')) {
          return resolve(JSON.parse(localStorage.getItem('translations')));

          // else loads default translation, if all fails, load english translation
        } else {
          const locale = this.config.locale || 'en_US';
          const translations = this.storeTranslationsForLocale({locale});
          return resolve(translations);
        }
      } catch (e) {
        return reject(e);
      }
    });
  }

  getCurrentLocale () {
    const {query: {lang}} = url.parse(window.location.href, true);
    const storedTranslation = localStorage.getItem('translations');
    return (lang || (storedTranslation && storedTranslation.locale) || this.config.locale);
  }

  storeTranslationsForLocale ({locale}) {
    const translations = this.getTranslationsForLocale({locale});
    translations.locale = locale;
    localStorage.setItem('translations', JSON.stringify(translations));
    return translations;
  }

  getTranslationsForLocale ({locale}) {
    return require.context('translations', true, /.json/)(`./${locale.split('_')[0]}.json`);
  }

  getCurrentLanguage () {
    const storedTranslation = JSON.parse(localStorage.getItem('translations'));
    // get stored language, defaults to english
    return (storedTranslation && storedTranslation.locale.split('_')[0]) || 'en';
  }

  getCurrent () {
    if (typeof localStorage['translations'] !== 'undefined') {
      return JSON.parse(localStorage['translations']).locale.replace('_', '-');
    }
  }

  static getTranslatedText (messageKey) {
    if (typeof localStorage['translations'] !== 'undefined') {
      return JSON.parse(localStorage['translations']).messages[messageKey.id];
    }

    return messageKey.defaultMessage;
  }
}



