import {handleActions} from 'redux-actions';
import { REHYDRATE } from 'redux-persist/constants';

// TYPES
const REQUEST_LOOKUP_DATA = 'REQUEST_LOOKUP_DATA';
const RECEIVE_LOOKUP_DATA = 'RECEIVE_LOOKUP_DATA';
const RESET_LOOKUP_DATA = 'RESET_LOOKUP_DATA';
const REMOVE_LOOKUP_DATA = 'REMOVE_LOOKUP_DATA';

// ACTIONS
const resetLookupData = (service, answeredGroupStepIndex, lookupDataGroup) => ({type: RESET_LOOKUP_DATA, service, answeredGroupStepIndex, lookupDataGroup});
const requestLookupData = (service, answeredGroupStepIndex, lookupDataGroup, answers) => ({type: REQUEST_LOOKUP_DATA, service, answeredGroupStepIndex, lookupDataGroup, answers});
const receiveLookupData = (lookupDataGroups) => ({type: RECEIVE_LOOKUP_DATA, lookupDataGroups});
const removeLookupData = () => ({type: REMOVE_LOOKUP_DATA});

// REDUCER
const INITIAL_STATE = {
  lookupDataGroups: [],
  isLoading: false
};

export default handleActions({
  [REQUEST_LOOKUP_DATA]: (state, action) => ({...state, isLoading: true}),
  [RECEIVE_LOOKUP_DATA]: (state, action) => ({...state, lookupDataGroups: action.lookupDataGroups, isLoading: false}),
  [REMOVE_LOOKUP_DATA]: (state, action) => ({...state, lookupDataGroups: []}),
  [REHYDRATE]: (state, action) => ({...state, ...action.payload.lookupData})
}, INITIAL_STATE);

export const types = {REQUEST_LOOKUP_DATA, RECEIVE_LOOKUP_DATA, RESET_LOOKUP_DATA, REMOVE_LOOKUP_DATA};
export const actions = {requestLookupData, receiveLookupData, resetLookupData, removeLookupData};



