import {handleActions} from 'redux-actions';

// TYPES
const FNOL_TEMPLATE_REQUEST = 'FNOL_TEMPLATE_REQUEST';
const FNOL_TEMPLATE_RECEIVE = 'FNOL_TEMPLATE_RECEIVE';
const FNOL_TEMPLATE_SAVE_REQUEST = 'FNOL_TEMPLATE_SAVE_REQUEST';
const FNOL_TEMPLATE_SAVE_RECEIVE = 'FNOL_TEMPLATE_SAVE_RECEIVE';

// ACTIONS
const requestFnolTemplate = (contractNr, date) => ({type: FNOL_TEMPLATE_REQUEST, contractNr, date});
const receiveFnolTemplate = (fnolTemplate) => ({type: FNOL_TEMPLATE_RECEIVE, fnolTemplate});
const requestSaveFnol = (fileGuids) => ({type: FNOL_TEMPLATE_SAVE_REQUEST, fileGuids});
const receiveSaveFnol = () => ({type: FNOL_TEMPLATE_SAVE_RECEIVE});

// REDUCER
const handleReceiveFnolTemplate = (state, action) => ({...state, options: action.fnolTemplate});
const handleRequestSaveFnol = (state, action) => ({...state, isSavingFnol: true});
const handleReceiveSaveFnol = (state, action) => ({...state, isSavingFnol: false});
const handleRequestFnolTemplate = (state, action) => ({
  ...state,
  selectedContractNr: action.contractNr,
  selectedDate: action.date
});

export default handleActions({
  [FNOL_TEMPLATE_REQUEST]: handleRequestFnolTemplate,
  [FNOL_TEMPLATE_RECEIVE]: handleReceiveFnolTemplate,
  [FNOL_TEMPLATE_SAVE_REQUEST]: handleRequestSaveFnol,
  [FNOL_TEMPLATE_SAVE_RECEIVE]: handleReceiveSaveFnol
}, {isSavingFnol: false});

export const types = {
  FNOL_TEMPLATE_REQUEST,
  FNOL_TEMPLATE_RECEIVE,
  FNOL_TEMPLATE_SAVE_REQUEST,
  FNOL_TEMPLATE_SAVE_RECEIVE
};

export const actions = {requestFnolTemplate, receiveFnolTemplate, requestSaveFnol, receiveSaveFnol};



