import React, {PropTypes} from 'react';
import {Field} from 'redux-form';
import BooleanInput from 'components/InputTypes/Boolean';
import CurrencyInput from 'components/InputTypes/Currency';
import DateInput from 'components/InputTypes/Date';
import IntegerInput from 'components/InputTypes/Integer';
import SelectionInput from 'components/InputTypes/Selection';
import StringInput from 'components/InputTypes/String';

const inputTypes = {
  boolean: BooleanInput,
  currency: CurrencyInput,
  datetime: DateInput,
  integer: IntegerInput,
  decimal: IntegerInput,
  selection: SelectionInput,
  string: StringInput
};

export default class Input extends React.PureComponent {
  static propTypes = {
    question: PropTypes.object.isRequired
  };

  constructor (props) {
    super(props);
    this.state = {
      isHelpBlockOpened: false
    };
  }

  toggleHelpBlock = () => {
    this.setState({
      isHelpBlockOpened: !this.state.isHelpBlockOpened
    });
  };

  getGuideTextHtml = () => {
    const {question: {guideText}} = this.props;
    return guideText ? {__html: guideText} : null;
  };

  render () {
    const {state: {isHelpBlockOpened}, props: {question}} = this;
    const inputType = question.type.toLowerCase();

    return (
      <Field {...this.props}
        component={inputTypes[inputType]}
        helpText={this.getGuideTextHtml()}
        isHelpBlockOpened={isHelpBlockOpened}
        toggleHelpBlock={this.toggleHelpBlock}/>
    );
  }
}



