import {refreshAuthorizationHeader} from 'lib/api';

const userStorageKey = 'authUser';
const userStorage = {
  isLoggedIn () {
    return localStorage.getItem(userStorageKey) !== null && localStorage.getItem(userStorageKey) !== '';
  },

  unsetStoredUser () {
    localStorage.removeItem(userStorageKey);
    refreshAuthorizationHeader();
  },

  setStoredUser (user) {
    localStorage[userStorageKey] = JSON.stringify(user);
    refreshAuthorizationHeader();
  },

  getStoredUser () {
    const jsonUser = localStorage[userStorageKey];
    if (!jsonUser) {
      return null;
    }

    const authUser = JSON.parse(jsonUser);
    if (!authUser) {
      return null;
    }

    const expDate = new Date(authUser.jsonData.exp * 1000);
    if (expDate < new Date()) {
      userStorage.unsetStoredUser();
      return null;
    }

    return authUser;
  }
};

export default userStorage;



