
		module.exports = function(context) {
			return (function() {
				

				if (!React) {
						var React = require("react");
					}

				return (
					<div>
  <button className='btn btn-primary' onClick={this.handleOpenFnolModal}>
    <this.FormattedMessage {...this.messages.addIncident} />
  </button>
  <this.IncidentList
    incidents={this.props.incidents}
    isLoading={this.props.isIncidentListLoading}
    handleClickRow={this.handleClickRow}
  />
  <this.Modal
    title={this.state.activeIncident.insuranceDescription}
    open={this.state.isDetailsModalOpen}
    id='incident-detail-modal'
    closeCallback={this.handleCloseDetailsModal}>
    <div className='modal-body'>
      <this.Incident {...this.state.activeIncident} readOnly formName='incident'/>
      <this.DocumentList documents={this.props.documents} handleOpenDocument={this.handleOpenDocument}/>
    </div>
  </this.Modal>
  <this.Modal
    title={this.props.intl.formatMessage(this.messages.reportDamage)}
    id='fnol-modal'
    open={this.state.isFnolModalOpen}
    closeCallback={this.handleCloseFnolModal}>
    <div className='modal-body'>
      {this.props.fnol &&
      <this.FNOL {...this.props.fnol}
                 uploadFiles={this.File.upload.bind(this, this.dispatch)}
                 shouldReset={this.state.shouldResetFnolForm}
                 contracts={this.props.contracts}
                 handleSubmit={this.handleSubmitFnol}
                 onChangeContract={this.fetchFnolForContract}
                 onChangeFiles={this.storeFnolFileGuids}
                 formName={this.fnolFormName}
                 answers={this.props.fnolAnswers}
                 enableReinitialize
                 hideSubmitButton/>}
    </div>
  </this.Modal>
</div>

				);
			}).apply(context);
		};
	


