import {PropTypes} from 'react';
import CfeComponent from 'components/CfeComponent';

export default class BooleanInput extends CfeComponent {
  static displayName = 'BooleanInput';

  static propTypes = {
    callback: PropTypes.func,
    name: PropTypes.string,
    label: PropTypes.string,
    checked: PropTypes.bool
  };

  constructor (props) {
    super(props, 'components/InputTypes/Boolean/input');
    this.state = {
      isChecked: false
    };
  }

  handleChange = () => {
    const {props: {callback}} = this;
    const isChecked = !this.state.isChecked;

    this.setState({isChecked});

    // prop onChange isn't passed through correctly, prop callback is used for onChange now
    if (callback) {
      callback(isChecked);
    }
  };

  render () {
    return super.render(this);
  }
}



