
		module.exports = function(context) {
			return (function() {
				

				if (!React) {
						var React = require("react");
					}

				return (
					<div>
{this.props.isLoading
  ? <this.Loader />
  : <table className='table table-hover'>
      <thead>
      <tr>
        <th><this.FormattedMessage {...this.messages.contractDescription}/></th>
        <th><this.FormattedMessage {...this.messages.contractNumber}/></th>
        <th><this.FormattedMessage {...this.messages.incidentDate}/></th>
        <th><this.FormattedMessage {...this.messages.incidentReportDate}/></th>
        <th><this.FormattedMessage {...this.messages.incidentState}/></th>
      </tr>
      </thead>
      <tbody>
      {this.sortedIncidents.length > 0 && this.sortedIncidents.map((incident, i) =>
        <tr key={i} onClick={() => this.props.handleClickRow(incident)}>
          <td>{incident.insuranceDescription}</td>
          <td>{incident.contractId}</td>
          <td>{this.util.formatDate(incident.dateIncident)}</td>
          <td>{this.util.formatDate(incident.dateNotification)}</td>
          <td>{incident.state}</td>
        </tr>)}
      </tbody>
    </table>}
</div>

				);
			}).apply(context);
		};
	


