import {actions, types} from 'redux/modules/feedback';
import {takeLatest} from 'redux-saga';
import {put, select} from 'redux-saga/effects';
import config from 'lib/config';
import Logger from 'lib/logger';

function * checkFeedbackRequest ({customerId, caseId, token}) {
  try {
    const feedbackData = yield fetch(`${config.serviceCenterApiUrl}/CustomerFeedback/${customerId}/${caseId}/${token}`).then(res => res.json());

    if (!feedbackData.ValidationResult.Errors.length) {
      yield put(actions.receiveFeedbackCheck({...feedbackData, customerId, caseId, token}));
    } else {
      yield put(actions.receiveFeedbackCheckFailure(feedbackData.ValidationResult.Errors[0]));
    }
  } catch (error) {
    Logger.error(yield error);
  }
}

function * sendFeedbackRequest ({feedback}) {
  try {
    const {customerId, caseId, token} = yield select(state => state.feedback);
    const {ValidationResult} = yield fetch(`${config.serviceCenterApiUrl}/CustomerFeedback`, {
      headers: {
        'Accept': 'application/json',
        'Content-Type': 'application/json'
      },
      method: 'post',
      body: JSON.stringify({
        CustomerIdentifier: customerId,
        CaseId: Number(caseId),
        Token: token,
        Rating: Number(feedback.rating),
        Comment: feedback.comment || ''
      })
    }).then(res => res.json());

    if (!ValidationResult.Errors.length) {
      yield put(actions.receiveSendFeedback());
    } else {
      yield put(actions.receiveSendFeedbackFailure(ValidationResult.Errors[0]));
    }
  } catch (error) {
    Logger.error(yield error);
  }
}

export function * feedbackEvents () {
  while (true) {
    yield [
      takeLatest(types.FEEDBACK_CHECK_REQUEST, checkFeedbackRequest),
      takeLatest(types.FEEDBACK_SEND_REQUEST, sendFeedbackRequest)
    ];
  }
}




