import {handleActions} from 'redux-actions';

const PRODUCT_DETAILS_RECEIVE = 'PRODUCT_DETAILS_RECEIVE';
const PRODUCT_LIST_REQUEST = 'PRODUCT_LIST_REQUEST';
const PRODUCT_LIST_RECEIVE = 'PRODUCT_LIST_RECEIVE';

// ACTIONS
const receiveProductDetails = (productDetails) => ({type: PRODUCT_DETAILS_RECEIVE, productDetails});
const requestProductList = () => ({type: PRODUCT_LIST_REQUEST});
const receiveProductList = (productList) => ({type: PRODUCT_LIST_RECEIVE, productList});

// REDUCER
const handleReceiveProductDetails = (state, action) => ({...state, selected: action.productDetails});
const handleReceiveProductList = (state, action) => ({...state, list: action.productList});

export default handleActions({
  [PRODUCT_LIST_RECEIVE]: handleReceiveProductList,
  [PRODUCT_DETAILS_RECEIVE]: handleReceiveProductDetails
}, {selected: {}});

export const types = {PRODUCT_DETAILS_RECEIVE, PRODUCT_LIST_REQUEST, PRODUCT_LIST_RECEIVE};
export const actions = {receiveProductDetails, receiveProductList, requestProductList};



