import Option from './option';

export default class Fnol {
  constructor (fnolData = {}, customer = null) {
    if (fnolData.options) fnolData.options = Option.initializeResourceOptions(fnolData.options);
    Object.assign(this, fnolData);
    this.customer = customer;
  }
}



