import {handleActions} from 'redux-actions';

// TYPES
const FEEDBACK_CHECK_REQUEST = 'FEEDBACK_CHECK_REQUEST';
const FEEDBACK_CHECK_RECEIVE = 'FEEDBACK_CHECK_RECEIVE';
const FEEDBACK_CHECK_FAILURE = 'FEEDBACK_CHECK_FAILURE';
const FEEDBACK_SEND_REQUEST = 'FEEDBACK_SEND_REQUEST';
const FEEDBACK_SEND_RECEIVE = 'FEEDBACK_SEND_RECEIVE';
const FEEDBACK_SEND_FAILURE = 'FEEDBACK_SEND_FAILURE';

// ACTIONS
const requestFeedbackCheck = (customerId, caseId, token) => ({type: FEEDBACK_CHECK_REQUEST, customerId, caseId, token});
const receiveFeedbackCheck = (feedbackCheckData) => ({type: FEEDBACK_CHECK_RECEIVE, feedbackCheckData});
const receiveFeedbackCheckFailure = (error) => ({type: FEEDBACK_CHECK_FAILURE, error});
const requestSendFeedback = (feedback) => ({type: FEEDBACK_SEND_REQUEST, feedback});
const receiveSendFeedback = () => ({type: FEEDBACK_SEND_RECEIVE});
const receiveSendFeedbackFailure = (error) => ({type: FEEDBACK_SEND_FAILURE, error});

// REDUCER
const handleReceiveFeedbackCheck = (state, action) => ({...state, isChecking: false, ...action.feedbackCheckData});
const handleReceiveFeedbackCheckFailure = (state, action) => ({...state, error: action.error});
const handleReceiveFeedbackFailure = (state, action) => ({...state, error: action.error});
const handleReceiveSendFeedback = (state, action) => ({...state, isFeedbackSend: true});

export default handleActions({
  [FEEDBACK_CHECK_RECEIVE]: handleReceiveFeedbackCheck,
  [FEEDBACK_CHECK_FAILURE]: handleReceiveFeedbackCheckFailure,
  [FEEDBACK_SEND_FAILURE]: handleReceiveFeedbackFailure,
  [FEEDBACK_SEND_RECEIVE]: handleReceiveSendFeedback
}, {isChecking: true, isFeedbackSend: false});

export const types = {
  FEEDBACK_CHECK_REQUEST,
  FEEDBACK_CHECK_RECEIVE,
  FEEDBACK_CHECK_FAILURE,
  FEEDBACK_SEND_REQUEST,
  FEEDBACK_SEND_FAILURE
};

export const actions = {
  requestFeedbackCheck,
  receiveFeedbackCheck,
  receiveFeedbackCheckFailure,
  requestSendFeedback,
  receiveSendFeedback,
  receiveSendFeedbackFailure
};



