import {handleActions} from 'redux-actions';

// TYPES
const REGISTER_OFFER_RECEIVE_FAILURE = 'REGISTER_OFFER_RECEIVE_FAILURE';
const CHECK_FOR_OFFER_HASH = 'CHECK_FOR_OFFER_HASH';
const SET_OFFER_HASH = 'SET_OFFER_HASH';
const CLEAR_OFFER_HASH = 'RECEIVE_OFFER_REQUEST';

// ACTIONS
const receiveRegisterOfferFailure = (error) => ({type: REGISTER_OFFER_RECEIVE_FAILURE, error});
const checkForOfferHash = () => ({type: CHECK_FOR_OFFER_HASH});
const setOfferHash = (hash) => ({type: SET_OFFER_HASH, hash});
const clearOfferHash = () => ({type: CLEAR_OFFER_HASH});

// REDUCER
const handleCheckOfferHash = (state, action) => ({...state, isLoading: true});
const handleReceiveRegisterOfferFailure = (state, action) => ({...state, error: action.error, hash: null});
const handleSetOfferHash = (state, action) => ({...state, hash: action.hash, isLoading: false});
const handleClearOfferHash = (state, action) => ({...state, hash: null, isLoading: false});

const INITIAL_STATE = {
  isLoading: false,
  hash: null
};

export default handleActions({
  [CHECK_FOR_OFFER_HASH]: handleCheckOfferHash,
  [CLEAR_OFFER_HASH]: handleClearOfferHash,
  [REGISTER_OFFER_RECEIVE_FAILURE]: handleReceiveRegisterOfferFailure,
  [SET_OFFER_HASH]: handleSetOfferHash
}, INITIAL_STATE);

export const types = {
  REGISTER_OFFER_RECEIVE_FAILURE,
  CHECK_FOR_OFFER_HASH,
  SET_OFFER_HASH
};
export const actions = {clearOfferHash, receiveRegisterOfferFailure, checkForOfferHash, setOfferHash};



