
		module.exports = function(context) {
			return (function() {
				

				if (!React) {
						var React = require("react");
					}

				return (
					<div>
  {this.paymentMethodTemplates
    ? <div>
        <div className='form-group'>
          <label><this.FormattedMessage {... this.messages.paymentMethodTypes}/>:</label>
          <select className='form-control' onChange={this.handleChange}>
            {this.paymentMethodTemplates.map((m, i) => {
              return <option key={i} value={m.type.id}>{m.type.name}</option>;
            })}
          </select>
        </div>
        <this.PaymentMethod {...this.selectedPaymentMethodTemplate}
          handleSubmit={(inputKeyValue) => this.props.handleSubmit(inputKeyValue, this.selectedPaymentMethodTemplate)}
                            isLoading={this.props.isSavingPaymentMethod}
          formName={this.dynamicFormTypes.paymentMethod} />
      </div>
    : <Loader/>}
</div>

				);
			}).apply(context);
		};
	


