import moment from 'moment';

export default class DateUtil {
  static formats = [
    moment.ISO_8601,
    'YYYY-MM-DD',
    'DD-MM-YYYY'
  ];

  static format (date, format) {
    return date instanceof Date
      ? moment(date).format(format)
      : moment(date, DateUtil.formats).format(format);
  }
}



