import React, {PropTypes} from 'react';
import {connect} from 'react-redux';
import {actions as localizationActions} from 'redux/modules/localization';
import LanguageSwitcher from 'components/Localization/LanguageSwitcher';
import messages from 'components/Localization/messages';
import Loader from 'components/Loader';
import {FormattedMessage} from 'react-intl';
import RootContainer from 'containers/RootContainer';

export class LocalizationContainer extends React.PureComponent {
  componentWillMount () {
    this.props.requestLocalizations();
  }

  render () {
    return (
      <RootContainer {...this.props}>
        {this.props.isLoading
          ? <Loader />
          : <ul className='nav navbar-nav navbar-right'>
            <li className='dropdown'>
              <a href='#' className='dropdown-toggle' data-toggle='dropdown' role='button' aria-haspopup='true'
                 aria-expanded='false'>
                <FormattedMessage {...messages.culture} />
                <span className='caret'></span>
              </a>
              <LanguageSwitcher localizations={this.props.localizations}/>
            </li>
          </ul>
        }
      </RootContainer>
    );
  }
}

LocalizationContainer.propTypes = {
  requestLocalizations: PropTypes.func,
  isLoading: PropTypes.bool,
  store: PropTypes.object,
  locale: PropTypes.string,
  messages: PropTypes.object
};

const mapStateToProps = state => ({
  isLoading: state.localization.isLoading,
  localizations: state.localization.list
});

const mapDispatchToProps = dispatch => ({
  requestLocalizations: () => dispatch(localizationActions.requestLocalizations())
});

export default connect(mapStateToProps, mapDispatchToProps)(LocalizationContainer);



