import React from 'react';

export default class CfeComponent extends React.PureComponent {
  constructor (props, componentPath) {
    super(props);

    this.componentPath = componentPath;

    if (window.cfe) {
      const pathSplitBySlash = this.componentPath.split('/');
      const templateOverride = pathSplitBySlash.reduce((o, i) => o && o[i] ? o[i] : undefined, window.cfe);
      if (templateOverride) {
        this.templateOverride = templateOverride;
      }
    }
  }

  render (context) {
    if (this.templateOverride) {
      if (!context.props) {
        context.props = true; // needed as a hack for the dynamic template overrides
      }
      return this.templateOverride.call(context, {React});
    } else {
      const defaultTemplate = require.context('../', true, /.jsx$/)(`./${this.componentPath}.jsx`);
      return defaultTemplate(context);
    }
  }

  componentWillMount () {
    invokeCallback(this.templateOverride, 'componentWillMount', this);
  }

  componentDidMount () {
    invokeCallback(this.templateOverride, 'componentDidMount', this);
  }

  componentWillReceiveProps (nextProps) {
    invokeCallback(this.templateOverride, 'componentWillReceiveProps', this, [nextProps]);
  }

  componentDidUpdate (prevProps, prevState) {
    invokeCallback(this.templateOverride, 'componentDidUpdate', this, [prevProps, prevState]);
  }

  get isTemplateOverriden () {
    return !!this.templateOverride;
  }
}

const invokeCallback = (templateOverride, callbackName, context, args) => {
  if (templateOverride && templateOverride.callbacks && templateOverride.callbacks[callbackName]) {
    templateOverride.callbacks[callbackName].apply(context, args);
  }
};



