import React, {PropTypes} from 'react';
import ProductInformativeValuesList from 'components/Product/informativeValuesList';
import RootContainer from 'containers/RootContainer';

export default class ProductConfigured extends React.Component {
  static propTypes = {
    store: PropTypes.object.isRequired,
    locale: PropTypes.string.isRequired,
    messages: PropTypes.object.isRequired
  };

  render () {
    return (
      <RootContainer {...this.props}>
        <ProductInformativeValuesList />
      </RootContainer>
    );
  }
}



