import {handleActions} from 'redux-actions';

// TYPES
const EMAILS_REQUEST = 'EMAILS_REQUEST';
const EMAILS_RECEIVE = 'EMAILS_RECEIVE';
const EMAILS_FAILURE = 'EMAILS_FAILURE';
const EMAIL_BODY_REQUEST = 'EMAIL_BODY_REQUEST';
const EMAIL_BODY_RECEIVE = 'EMAIL_BODY_RECEIVE';

export const types = {
  EMAILS_REQUEST,
  EMAILS_RECEIVE,
  EMAILS_FAILURE,
  EMAIL_BODY_REQUEST,
  EMAIL_BODY_RECEIVE
};

// ACTIONS
const requestEmails = () => ({type: EMAILS_REQUEST});
const receiveEmails = (emails) => ({type: EMAILS_RECEIVE, emails});
const receiveEmailsFailure = (error) => ({type: EMAILS_FAILURE, error});
const requestEmailBody = (email) => ({type: EMAIL_BODY_REQUEST, email});
const receiveEmailBody = (emailBodyAttachmentFiles) => ({type: EMAIL_BODY_RECEIVE, emailBodyAttachmentFiles});

export const actions = {
  requestEmails,
  receiveEmails,
  receiveEmailsFailure,
  requestEmailBody,
  receiveEmailBody
};

// REDUCER
const handleRequestEmails = (state, action) => ({...state, isListLoading: true});
const handleReceiveEmails = (state, action) => ({...state, list: action.emails, isListLoading: false});
const handleReceiveEmailsFailure = (state, action) => ({...state, error: action.error, isListLoading: false});
const handleRequestEmailBody = (state, action) => ({...state, isEmailBodyLoading: true});
const handleReceiveEmailBody = (state, action) => ({...state, emailBodyAttachmentFiles: action.emailBodyAttachmentFiles, isEmailBodyLoading: false});

const INITIAL_STATE = {
  list: [],
  emailBodyAttachmentFiles: {}
};

export default handleActions({
  [EMAILS_REQUEST]: handleRequestEmails,
  [EMAILS_RECEIVE]: handleReceiveEmails,
  [EMAILS_FAILURE]: handleReceiveEmailsFailure,
  [EMAIL_BODY_REQUEST]: handleRequestEmailBody,
  [EMAIL_BODY_RECEIVE]: handleReceiveEmailBody
}, INITIAL_STATE);



