import React, {PropTypes} from 'react';
import ProductView from 'views/ProductView/ProductView';
import {connect} from 'react-redux';
import {actions as flowActions} from 'redux/modules/flow';

export class DossierContractEditView extends React.Component {
  componentWillMount () {
    this.props.requestStartContractEditFlow();
  }

  render () {
    return (
      <ProductView />
    );
  }
}

DossierContractEditView.propTypes = {
  requestStartContractEditFlow: PropTypes.func
};

const mapDispatchToProps = (dispatch) => ({
  requestStartContractEditFlow: () => dispatch(flowActions.requestStartContractEditFlow())
});

export default connect(null, mapDispatchToProps)(DossierContractEditView);




