
		module.exports = function(context) {
			return (function() {
				

				if (!React) {
						var React = require("react");
					}

				return (
					<div>
  {{
    accept: (
      <this.SignedContract
          signedContractHeader={this.formatMessage(this.messages.signedContractHeader)}
          signedContractBody={this.formatMessage(this.messages.signedContractBody, {insuranceName: this.props.product.name})}/>
    ),
    decline: (
      <div>Payment declined</div>
    ),
    exception: (
      <div>Payment exception</div>
    ),
    cancel: (
      <div>Payment cancelled</div>
    )
  }[this.props.routeParams.paymentStatus]}
</div>


				);
			}).apply(context);
		};
	


