import React, {PropTypes} from 'react';
import DynamicOptionsFormEnhancer from 'enhancers/DynamicOptionsFormEnhancer';

class Customer extends React.PureComponent {
  static propTypes = {
    form: PropTypes.any
  };

  render () {
    return this.props.form;
  }
}

export default DynamicOptionsFormEnhancer(Customer);



