import {PropTypes} from 'react';
import {reset} from 'redux-form';
import {defineMessages, FormattedMessage} from 'react-intl';
import {injectIntl} from 'react-intl';
import {connect} from 'react-redux';
import Modal from 'components/Modal';
import PaymentMethodCreator from 'components/PaymentMethod/PaymentMethodCreator';
import CfeComponent from 'components/CfeComponent';
import LoadingButton from 'components/buttons/LoadingButton';
import localMessages from 'views/ProductView/messages';
import dynamicFormTypes from 'lib/dynamicFormTypes';

class PaymentMethodPicker extends CfeComponent {
  static displayName = 'PaymentMethodPicker';

  static propTypes = {
    handleCreatePaymentMethod: PropTypes.func.isRequired,
    paymentMethodTemplates: PropTypes.array.isRequired,
    intl: PropTypes.object.isRequired,
    handleSubmit: PropTypes.func.isRequired,
    customerPaymentMethods: PropTypes.array.isRequired,
    selectedPaymentMethodId: PropTypes.string,
    handleSelectPaymentMethod: PropTypes.func.isRequired,
    dispatch: PropTypes.func.isRequired,
    isSavingAndSigningContract: PropTypes.bool
  };

  constructor (props) {
    super(props, 'components/PaymentMethod/PaymentMethodPicker');
    this.state = {
      isAddPaymentMethodModalOpen: false
    };
  }

  componentWillReceiveProps (nextProps) {
    if (this.props.isLoadingPaymentMethod && !nextProps.isLoadingPaymentMethod) {
      this.closeModals();
    }
  }

  handleAddPaymentMethod = () => {
    this.setState({
      isAddPaymentMethodModalOpen: true
    });
  };

  handleCreatePaymentMethodWrapper = (input, paymentMethod) => {
    this.props.handleCreatePaymentMethod(input, paymentMethod).then(() => {
      this.closeModals();
    });
  };

  render () {
    const context = {
      ...this,
      Modal,
      messages,
      FormattedMessage,
      PaymentMethodCreator,
      LoadingButton,
      localMessages,
      formatMessage: this.props.intl.formatMessage
    };

    return super.render(context);
  }

  selectedPaymentMethodChanged = (event) => {
    const selectedPaymentMethod = this.props.customerPaymentMethods.find(p => p.id === event.target.value);
    this.props.handleSelectPaymentMethod(selectedPaymentMethod);
  };

  closeModals = () => {
    this.setState({isAddPaymentMethodModalOpen: false});
    this.props.dispatch(reset(dynamicFormTypes.paymentMethod));
  };
}

export default connect()(injectIntl(PaymentMethodPicker));

const messages = defineMessages({
  addPaymentMethodModalTitle: {
    id: 'paymentMethodPicker.addPaymentMethodModalTitle',
    description: 'Modal title label',
    defaultMessage: 'Add payment method'
  },
  createNewPaymentMethodMessage: {
    id: 'paymentMethodPicker.createPaymentMethod',
    description: 'Modal title label',
    defaultMessage: 'or create a new one'
  },
  selectPaymentMethodLabel: {
    id: 'paymentMethodPicker.selectPaymentMethodLabel',
    description: 'Label on dropdown',
    defaultMessage: 'select one'
  }
});



