import {PropTypes} from 'react';
import {injectIntl, defineMessages} from 'react-intl';
import CfeComponent from 'components/CfeComponent';
import {connect} from 'react-redux';
import CalculationsList from 'components/Calculation/list';
import Modal from 'components/Modal';
import {actions as calculationActions} from 'redux/modules/calculation';
import {actions as flowActions} from 'redux/modules/flow';
import ProductView from 'views/ProductView/ProductView';
import CalculationResult from 'components/CalculationResult';
import ProductInformativeValuesList from 'components/Product/informativeValuesList';
import ProductDocuments from 'components/Product/documents';

export class DossierCalculationsView extends CfeComponent {
  static displayName = 'DossierCalculationsView';

  static propTypes = {
    calculations: PropTypes.array,
    isCalculationsListLoading: PropTypes.bool
  };

  constructor (props) {
    super(props, 'views/DossierCalculationsView/DossierCalculationsView');

    this.state = {
      activeCalculation: {},
      isConfirmationModalOpen: false
    };
  }

  componentWillMount () {
    this.props.requestCalculations();
  }

  handleClickRemove = (calculation) => {
    this.setState({
      activeCalculation: calculation,
      isConfirmationModalOpen: true
    });
  };

  modalClosedCallback = () => {
    this.setState({isConfirmationModalOpen: false});
  };

  resumeCalculation = (calculation) => {
    this.props.requestStartCalculationFlow(calculation.calculationId);
  };

  handleDeleteCalculation = () => {
    const {state: {activeCalculation}} = this;

    this.props.requestDeleteCalculation(activeCalculation);

    this.setState({
      activeCalculation: {},
      isConfirmationModalOpen: false
    });
  };

  render () {
    const context = {
      ...this,
      CalculationsList,
      messages,
      Modal,
      ProductView,
      CalculationResult,
      ProductInformativeValuesList,
      ProductDocuments
    };

    return super.render(context);
  }
}

const messages = defineMessages({
  deleteCalculation: {
    id: 'calculationsView.deleteCalculation',
    description: 'Dialog title for delete calculation',
    defaultMessage: 'Delete calculation'
  },
  deleteCalculationConfirmation: {
    id: 'calculationsView.deleteCalculationConfirmation',
    description: 'Dialog title for delete calculation',
    defaultMessage: 'Are you sure you want to delete this calculation?'
  },
  deleteText: {
    id: 'delete',
    description: 'Button text for delete calculation',
    defaultMessage: 'Delete'
  }
});

const mapStateToProps = (state) => ({
  calculations: (state.calculations.list || []),
  calculateable: state.flow.calculateable
});

const mapDispatchToProps = (dispatch) => ({
  requestCalculations: () => dispatch(calculationActions.requestCalculations()),
  requestStartCalculationFlow: (calculationId) => dispatch(flowActions.requestStartCalculationFlow(calculationId)),
  requestDeleteCalculation: (calculation) => dispatch(calculationActions.requestDeleteCalculation(calculation))
});

export default connect(mapStateToProps, mapDispatchToProps)(injectIntl(DossierCalculationsView));



