import {PropTypes} from 'react';
import {connect} from 'react-redux';
import {actions as stepperActions} from 'redux/modules/stepper';
import Stepper from 'components/Stepper';
import Section from 'components/Stepper/Section';
import {injectIntl} from 'react-intl';
import PaymentStep from './PaymentStep';
import CalculationStep from './CalculationStep';
import ExtraQuestionsStep from './ExtraQuestionsStep';
import CustomerStep from './CustomerStep';
import SummaryStep from './SummaryStep';
import messages from './messages';
import CfeComponent from 'components/CfeComponent';
import SignedContract from 'components/SignedContract';
import Calculateable from 'lib/calculateable';
import CustomerClass from 'lib/customer';
import dynamicFormTypes from 'lib/dynamicFormTypes';

class ProductView extends CfeComponent {
  static displayName = 'ProductView';
  static propTypes = {
    setActiveStep: PropTypes.func,
    productId: PropTypes.oneOfType([
      PropTypes.string,
      PropTypes.number
    ]),
    product: PropTypes.object,
    intl: PropTypes.object
  };

  constructor (props) {
    super(props, 'views/ProductView/ProductView');
  }

  getValidationMessages = (object) => this.props.validation[object] || [];

  handleClickStepperHeader = (stepIndex, isVisited) => {
    if (isVisited) {
      this.props.setActiveStep(stepIndex);
    }
  };

  render () {
    const context = {
      ...this,
      messages,
      formatMessage: this.props.intl.formatMessage,
      Stepper,
      Section,
      CustomerStep,
      SummaryStep,
      PaymentStep,
      SignedContract,
      CalculationStep,
      ExtraQuestionsStep,
      dynamicFormTypes,
      customer: this.props.customer
    };

    return super.render(context);
  }
}

const mapStateToProps = (state) => ({
  calculateable: new Calculateable(state.flow.calculateable),
  customer: new CustomerClass(state.customer.activeCustomer),
  validation: state.validation,
  activeStepIndex: state.stepper.activeStepIndex,
  steps: state.stepper.steps
});

const mapDispatchToProps = (dispatch) => ({
  setActiveStep: (stepIndex) => dispatch(stepperActions.setActiveStep(stepIndex))
});

export default connect(mapStateToProps, mapDispatchToProps)(injectIntl(ProductView));



