import React from 'react';
import {connect} from 'react-redux';
import {getFormValues} from 'redux-form';
import Calculateable from 'lib/calculateable';
import dynamicFormTypes from 'lib/dynamicFormTypes';

class ProductInformativeList extends React.PureComponent {
  isQuestionRelevant = (question) => question.isQuestionRelevant(this.props.answers, this.props.calculateable.options);

  getRelevantInformativeOptions = () =>
    this.props.calculateable.options
      ? this.props.calculateable.options.filter(q => this.isQuestionRelevant(q) && q.isReadonly)
      : [];

  render () {
    const options = this.getRelevantInformativeOptions();

    return (options.length
      ? <div>
          <p>Informative values:</p>
          <ul>
            {this.getRelevantInformativeOptions().map((o, i) => <li key={i}>{o.questionText}: {o.getValue()}</li>)}
          </ul>
        </div>
      : null);
  }
}

const mapStateToProps = (state) => ({
  calculateable: new Calculateable(state.flow.calculateable),
  answers: {...getFormValues(dynamicFormTypes.calculation)(state), ...getFormValues(dynamicFormTypes.extraQuestions)(state)}
});

export default connect(mapStateToProps)(ProductInformativeList);



