import {extend, trim} from 'lodash';
import {defineMessages} from 'react-intl';
import Option from 'lib/option';

export default class Product {
  processedQuestions = [];

  constructor (productData = {}) {
    if (productData.options) productData.options = Option.initializeResourceOptions(productData.options);
    if (productData.recommendations) productData.recommendations.options = Option.initializeResourceOptions(productData.recommendations.options);
    extend(this, productData);
  }

  getSummary (answers, formatMessageFn) {
    let summary = [];

    if (this.processedQuestions.length) {
      summary = this.options.reduce((memo, option) => {
        if (option.id === 'Contract_1_PaymentInterval') {
          return memo;
        }

        if (trim(answers[option.id]).length) {
          memo.push({
            type: option.type,
            key: trim(option.questionText),
            value: trim(answers[option.id])
          });
        }
        return memo;
      }, []);
    }

    if (this.pool) {
      summary.push({
        type: 'String',
        key: formatMessageFn(messages.summaryInsurancePoolNameLabel),
        value: this.pool.poolPartners[0] ? this.pool.poolPartners[0].name : ''
      });

      summary.push({
        type: 'String',
        key: formatMessageFn(messages.summaryInsurancePoolConditionsLabel),
        value: this.insuranceTermsDescr
      });
    }

    return summary;
  }

  getContractSummary (answers, formatMessageFn) {
    return [{
      key: formatMessageFn(messages.summaryInsuranceTypeLine),
      value: this.name
    }, {
      key: formatMessageFn(messages.summaryStartDateLine),
      value: this.getStartDate(answers)
    }, {
      key: formatMessageFn(messages.summaryDebitFrequencyLine),
      value: this.getPaymentInterval(answers)
    }];
  }

  getPaymentInterval (answers) {
    if (this.options) {
      return this.options.reduce((memo, option) => {
        if (option.id === 'Contract_1_PaymentInterval') {
          const paymentIntervalOption = option.items.filter(i => i.key === answers['Contract_1_PaymentInterval'])[0];

          if (paymentIntervalOption) {
            return paymentIntervalOption.value;
          }
        }
      });
    }
  }

  getStartDate (answers) {
    return trim(answers['Contract_1_DateStart']);
  }
}

const messages = defineMessages({
  summaryInsuranceTypeLine: {
    id: 'summary.insuranceTypeLine',
    description: 'Summary insurance type line',
    defaultMessage: 'Insurance type'
  },
  summaryStartDateLine: {
    id: 'summary.startDateLine',
    description: 'Summary start date line',
    defaultMessage: 'Start date'
  },
  summaryDebitFrequencyLine: {
    id: 'summary.debitFrequencyLine',
    description: 'Summary debit frequency line',
    defaultMessage: 'Debit frequency'
  },
  summaryInsurancePoolNameLabel: {
    id: 'summary.insurancePool.nameLabel',
    description: 'Insurer name label',
    defaultMessage: 'Insurer'
  },
  summaryInsurancePoolConditionsLabel: {
    id: 'summary.insurancePool.conditionsLabel',
    description: 'Insurer conditions label',
    defaultMessage: 'Conditions'
  }
});



