import {handleActions} from 'redux-actions';

// TYPES
const CALCULATE_REQUEST = 'CALCULATE_REQUEST';
const CALCULATE_RECEIVE = 'CALCULATE_RECEIVE';
const CALCULATE_FAILURE = 'CALCULATE_FAILURE';

// ACTIONS
const requestCalculate = (calculateable, calculationContext) => ({type: CALCULATE_REQUEST, calculateable, calculationContext});
const receiveCalculate = (calculation) => ({type: CALCULATE_RECEIVE, calculation});
const receiveCalculateFailure = (error) => ({type: CALCULATE_FAILURE, payload: error}); // uses payload for old action handler in validation reducer

// REDUCER
const INITIAL_STATE = {
  fetching: null
};

const request = (state, action) => ({...state, fetching: true});
const receive = (state, action) => ({...state, fetching: false, ...action.calculation});
const failure = (state, action) => ({...state, fetching: false});

export default handleActions({
  [CALCULATE_RECEIVE]: receive,
  [CALCULATE_REQUEST]: request,
  [CALCULATE_FAILURE]: failure
}, INITIAL_STATE);

export const types = {CALCULATE_REQUEST, CALCULATE_RECEIVE, CALCULATE_FAILURE};
export const actions = {requestCalculate, receiveCalculate, receiveCalculateFailure};



