import {handleActions} from 'redux-actions';

// TYPES
const PAYMENT_METHOD_TEMPLATES_RECEIVE = 'PAYMENT_METHOD_TEMPLATES_RECEIVE';
const SELECT_PAYMENT_METHOD = 'SELECT_PAYMENT_METHOD';
const TRANSACTION_COSTS_RECEIVE = 'TRANSACTION_COSTS_RECEIVE';

// ACTIONS
const selectPaymentMethod = (paymentMethod) => ({type: SELECT_PAYMENT_METHOD, paymentMethod});
const receivePaymentTransactionCosts = (transactionCosts) => ({type: TRANSACTION_COSTS_RECEIVE, transactionCosts});
const receivePaymentMethodTemplates = (paymentMethodTemplates) => ({
  type: PAYMENT_METHOD_TEMPLATES_RECEIVE,
  paymentMethodTemplates
});

// REDUCER
const handleReceivePaymentMethodTemplates = (state, action) => ({...state, list: action.paymentMethodTemplates});
const handleSelectPaymentMethod = (state, action) => ({...state, selected: action.paymentMethod});
const handleReceivePaymentTransactionCosts = (state, action) => ({...state, transactionCosts: action.transactionCosts});

export default handleActions({
  [PAYMENT_METHOD_TEMPLATES_RECEIVE]: handleReceivePaymentMethodTemplates,
  [SELECT_PAYMENT_METHOD]: handleSelectPaymentMethod,
  [TRANSACTION_COSTS_RECEIVE]: handleReceivePaymentTransactionCosts
}, {selected: {}});

export const types = {PAYMENT_METHOD_TEMPLATES_RECEIVE, SELECT_PAYMENT_METHOD, TRANSACTION_COSTS_RECEIVE};
export const actions = {receivePaymentMethodTemplates, selectPaymentMethod, receivePaymentTransactionCosts};



