import {PropTypes} from 'react';
import {defineMessages, injectIntl, FormattedMessage} from 'react-intl';
import FnolClass from 'lib/fnol';
import IncidentClass from 'lib/incident';
import CustomerClass from 'lib/customer';
import ContractClass from 'lib/contract';
import File from 'lib/file';
import CfeComponent from 'components/CfeComponent';
import {actions as fnolActions} from 'redux/modules/fnol';
import {actions as incidentActions} from 'redux/modules/incident';
import dynamicFormTypes from 'lib/dynamicFormTypes';
import {connect} from 'react-redux';
import Modal from 'components/Modal';
import Incident from 'components/Incident';
import IncidentList from 'components/Incident/list';
import FNOL from 'components/FNOL';
import DocumentList from 'components/Document/list';
import dossierFetcher from 'enhancers/dossierFetcher';

export class DossierIncidentsView extends CfeComponent {
  static displayName = 'DossierIncidentsView';

  constructor (props) {
    super(props, 'views/DossierIncidentsView/DossierIncidentsView');

    this.fnolFormName = dynamicFormTypes.fnol;

    this.state = {
      activeIncident: {},
      contractNrForFNOL: null,
      isDetailsModalOpen: false,
      isFnolModalOpen: false,
      shouldResetFnolForm: false,
      fnolFileGuids: []
    };
  }

  componentWillMount () {
    this.props.requestIncidents();
  }

  handleClickRow = (incident) => {
    this.props.requestIncidentDocuments(incident.documentIds);

    this.setState({
      activeIncident: incident,
      isDetailsModalOpen: true
    });
  };

  handleOpenDocument = (document) => {
    File.openDocument(document);
  };

  handleCloseDetailsModal = () => {
    this.setState({
      activeIncident: {},
      isDetailsModalOpen: false
    });
  };

  handleOpenFnolModal = () => {
    this.setState({isFnolModalOpen: true, shouldResetFnolForm: false, fnolFileGuids: []});
  };

  handleCloseFnolModal = () => {
    this.setState({isFnolModalOpen: false, shouldResetFnolForm: true, fnolFileGuids: []});
  };

  storeFnolFileGuids = (fileIndentifiers) => {
    const guids = fileIndentifiers.map(i => i.guid);
    this.setState({fnolFileGuids: [...guids]});
  };

  fetchFnolForContract = (contractNr, date) => this.props.requestFnolTemplate(contractNr, date);

  handleSubmitFnol = () => {
    this.props.requestFnolSave(this.state.fnolFileGuids);
    this.setState({isFnolModalOpen: false});
  };

  render () {
    const context = {
      ...this,
      FormattedMessage,
      IncidentList,
      DocumentList,
      Modal,
      dispatch: this.props.dispatch,
      Incident,
      File,
      FNOL,
      messages
    };

    return super.render(context);
  }
}

DossierIncidentsView.propTypes = {
  incidents: PropTypes.array,
  contracts: PropTypes.array,
  customer: PropTypes.object,
  fnol: PropTypes.object,
  intl: PropTypes.any,
  isIncidentListLoading: PropTypes.bool,
  fnolAnswers: PropTypes.object
};

const mapStateToProps = (state) => {
  const customer = new CustomerClass(state.customer.activeCustomer);
  const fnol = new FnolClass(state.fnol, customer);

  return {
    customer,
    incidents: (state.incident.list || []).map(i => new IncidentClass(i)),
    isIncidentListLoading: state.incident.isListLoading,
    validationMessagesUpload: state.validation.fileList,
    fnolAnswers: state.form.fnol ? state.form.fnol.values || {} : {},
    fnol,
    contracts: (state.contract.list || []).map(c => new ContractClass(c, customer)),
    documents: state.incident.documents
  };
};

const mapDispatchToProps = (dispatch) => ({
  requestIncidents: () => dispatch(incidentActions.requestIncidents()),
  requestIncidentDocuments: (documentIds) => dispatch(incidentActions.requestIncidentDocuments(documentIds)),
  requestFnolTemplate: (contractNr, date) => dispatch(fnolActions.requestFnolTemplate(contractNr, date)),
  requestFnolSave: (fileGuids) => dispatch(fnolActions.requestSaveFnol(fileGuids))
});

const messages = defineMessages({
  reportDamage: {
    id: 'incident.reportDamage',
    description: 'Modal title',
    defaultMessage: 'Report damage'
  },
  addIncident: {
    id: 'incident.addIncidentButton',
    description: 'Text for add incident button',
    defaultMessage: 'Report damage'
  }
});

export default connect(mapStateToProps, mapDispatchToProps)(dossierFetcher(injectIntl(DossierIncidentsView)));



