import CfeComponent from 'components/CfeComponent';
import {defineMessages, FormattedMessage} from 'react-intl';

export default class Loader extends CfeComponent {
  constructor (props) {
    super(props, 'components/Loader/index');
  }

  render () {
    return super.render({FormattedMessage, messages});
  }
}

const messages = defineMessages({
  loading: {
    id: 'loading',
    description: 'Loading message',
    defaultMessage: 'Loading'
  }
});



