import React, {PropTypes} from 'react';
import DynamicOptionsFormEnhancer from 'enhancers/DynamicOptionsFormEnhancer';

class PaymentMethod extends React.PureComponent {
  static displayName = 'PaymentMethod';

  static propTypes = {
    form: PropTypes.any
  };

  render () {
    return this.props.form;
  }
}

export default DynamicOptionsFormEnhancer(PaymentMethod);



