import {isEqual, findIndex, flatten, uniq, without} from 'lodash';
import {identifierObjectToIdentifierString, identifierStringToIdentifierObject} from 'lib/optionIdentifier';
import {headers} from 'lib/api';
import Option from 'lib/option';

export default class LookupService {
  constructor (config) {
    this.formName = config.formName;
  }

  static serviceDefinitionsToLookupDataGroups (serviceDefinitions = []) {
    return serviceDefinitions.map(g => ({
      groupName: g.service.name,
      steps: [{...g}]
    }));
  }

  isExisitingLookupDataGroup (lookupdataGroup) {
    return lookupdataGroup.steps.some(s => s.service ? s.service.step === 'Existing' : false);
  }

  getExistingLookupDataGroups (lookupDataGroups, answers) {
    const requests = flatten(lookupDataGroups.map(ldg => {
      return ldg.steps.map(group => {
        const groupAnswers = this.getGroupAnswers(group, answers);
        if (group.service && group.service.url) {
          return this.fetchGroupLookupData(group, groupAnswers);
        }
      });
    }));

    return Promise.all(requests).then(existingLookupDataGroups => {
      return lookupDataGroups.map((ldg, i) => ({
        groupName: ldg.groupName,
        steps: existingLookupDataGroups[i].reduce((acc, ld) => [...acc, {inputIds: ld.additionalOptions ? ld.additionalOptions.map(o => o.id) : [], ...ld}], [])
      }));
    });
  }

  findAnsweredGroupStepIndex = (lookupDataGroup, previousAnswers = {}, answers = {}) => {
    if (answers) {
      return findIndex(lookupDataGroup.steps, group => {
        const groupsAnswers = this.getGroupAnswers(group, answers);
        const previousGroupAnswers = this.getGroupAnswers(group, previousAnswers);
        return !isEqual(groupsAnswers, previousGroupAnswers) && this.isEverythingAnswered(groupsAnswers);
      });
    }

    return -1;
  };

  resetLookupDataGroupSteps (answeredGroupStepIndex, lookupDataGroups, lookupDataGroup) {
    return lookupDataGroups.map(g => g.groupName === lookupDataGroup.groupName ? {
      ...lookupDataGroup,
      steps: lookupDataGroup.steps.slice(0, answeredGroupStepIndex + 1)
    } : g);
  }

  getGroupAnswers (group, allAnswers) {
    const idStrings = group.inputIds.map(entityId => identifierObjectToIdentifierString(entityId));
    return idStrings.map(id => {
      return {
        key: identifierStringToIdentifierObject(id),
        value: allAnswers[id]
      };
    });
  }

  isEverythingAnswered (answers) {
    return answers.filter(a => a.value === undefined || a.value === 'null').length === 0;
  }

  fetchGroupLookupData (group, groupAnswers) {
    return fetch(group.service.url, {
      headers,
      method: 'post',
      body: JSON.stringify(groupAnswers)
    }).then(res => {
      const payload = res.json();

      if (res.ok) {
        return payload;
      }

      throw payload;
    })
      .catch(err => {
        throw err;
      });
  }

  getInputIdsToReset (answeredGroupStepIndex, lookupDataGroup) {
    const previouslyModifiedInputs = this.getUniqueInputIdsFromSteps(lookupDataGroup.steps.slice(0, answeredGroupStepIndex + 1));
    const inputsModifiedAfterResetStep = this.getUniqueInputIdsFromSteps(lookupDataGroup.steps.slice(answeredGroupStepIndex + 1, lookupDataGroup.steps.length));
    return without(inputsModifiedAfterResetStep, ...previouslyModifiedInputs);
  }

  getUniqueInputIdsFromSteps (steps) {
    return uniq(flatten(steps.map(group => group.inputIds)).map(inputId => identifierObjectToIdentifierString(inputId)));
  }

  getAllAdditionalOptions (lookupDataGroups = []) {
    const allAdditionalOptions = lookupDataGroups.reduce((acc, ldg) => {
      ldg.steps.forEach(s => s.additionalOptions ? acc.push(...s.additionalOptions) : s);
      return acc;
    }, []);

    return Option.initializeResourceOptions(allAdditionalOptions);
  }

  mergeLookupDataGroups (lookupDataGroups, group, lookupData) {
    return lookupDataGroups.map(g => {
      if (g.groupName === group.service.name) {
        return {
          groupName: g.groupName,
          steps: [...g.steps, {
            ...lookupData,
            inputIds: this.mergeGroupInputIdsWithNewLookupData(group, lookupData)
          }]
        };
      }
      return g;
    });
  }

  mergeGroupInputIdsWithNewLookupData (group, lookupData) {
    const newIds = [...group.inputIds];
    if (lookupData.additionalOptions) {
      lookupData.additionalOptions.forEach(ao => {
        const isInputAlreadyMerged = newIds.filter(id => identifierObjectToIdentifierString(id) === identifierObjectToIdentifierString(ao.id)).length > 0;
        if (!isInputAlreadyMerged) {
          newIds.push(ao.id);
        }
      });
    }
    return newIds;
  }
}



