
		module.exports = function(context) {
			return (function() {
				

				if (!React) {
						var React = require("react");
					}

				return (
					<div>{
  this.props.calculateable.options
    ? <div>
        <this.CalculationResult />
        <this.ProductInformativeValuesList />
        <this.ProductView />
        <this.ProductDocuments />
      </div>
    : <div>
      <this.CalculationsList
        calculations={this.props.calculations}
        handleResumeCalculation={this.resumeCalculation}
        handleClickRemove={this.handleClickRemove}
      />
      <this.Modal
        title={this.props.intl.formatMessage(this.messages.deleteCalculation)}
        open={this.state.isConfirmationModalOpen}
        closeCallback={this.modalClosedCallback}
        id='delete-confirmation-modal'>
        <div className='modal-body'>
          <p>{this.props.intl.formatMessage(this.messages.deleteCalculationConfirmation)}</p>
          <button className='btn btn-primary' onClick={this.handleDeleteCalculation}>
            {this.props.intl.formatMessage(this.messages.deleteText)}
          </button>
        </div>
      </this.Modal>
    </div>
}</div>

				);
			}).apply(context);
		};
	


