
		module.exports = function(context) {
			return (function() {
				

				if (!React) {
						var React = require("react");
					}

				return (
					<button className={this.props.className} type='submit' onClick={this.props.onClick}
        disabled={this.props.disabled || this.props.isLoading}>
  <this.FormattedMessage {...this.props.message}/>
  {this.props.children}
  {this.props.isLoading &&
  <i className="glyphicon glyphicon-refresh glyphicon-spin" aria-hidden="true"/>
  }
</button>


				);
			}).apply(context);
		};
	


