import clientTypes from 'lib/clientTypes';

class Config {

  // todo: this should be done in the constructor but due to the current app init implementation it is not possible
  initConfig (config) {
    this.config = config;
  }

  get divWebApiUrl () {
    return this.config.divUrl + '/api';
  }

  get apiKey () {
    return this.config.apiKey;
  }

  get clientId () {
    return this.config.clientId;
  }

  get clientSecret () {
    return this.config.clientSecret;
  }

  get documentStoreApiUrl () {
    return this.config.documentStoreUrl + '/api';
  }

  get serviceCenterApiUrl () {
    return this.config.serviceCenterUrl + '/api';
  }

  get stsWebApiUrl () {
    return this.config.stsUrl + '/api';
  }

  get stsAuthorizeUrl () {
    return this.config.stsUrl + '/connect/authorize';
  }

  get stsTokenUrl () {
    return this.config.stsUrl + '/connect/token';
  }

  get stsEndSessionUrl () {
    return this.config.stsUrl + '/connect/endsession';
  }

  get stsSettingsUrl () {
    return this.config.stsUrl + '/settings';
  }

  get stsRegisterUrl () {
    return this.config.stsUrl + '/Account/Register';
  }

  get tenant () {
    return this.config.tenant;
  }

  get locale () {
    return this.config.locale;
  }

  get maxFnolFiles () {
    return this.config.maxFnolFiles;
  }

  get maxFileSize () {
    return this.config.maxFileSize;
  }

  get customRoutesMapping () {
    return this.config.customRoutesMapping;
  }

  get clientType () {
    return this.config.clientType || clientTypes.cms;
  }

}

export default new Config();



