import {takeLatest} from 'redux-saga';
import {put, select} from 'redux-saga/effects';
import {api} from 'lib/api';
import {types, actions as fnolActions} from 'redux/modules/fnol';
import {actions as incidentActions} from 'redux/modules/incident';
import Logger from 'lib/logger';
import moment from 'moment';
import {getKeyValuePairs} from 'lib/option';
import {getFormValues} from 'redux-form';
import dynamicFormTypes from 'lib/dynamicFormTypes';
import {toastr} from 'react-redux-toastr';
import locale from 'lib/locale';
import {defineMessages} from 'react-intl';
import {actions as validationActions} from 'redux/modules/validation';

function * requestFnolTemplate ({contractNr, date}) {
  try {
    const dateFnol = moment(date, 'DD-MM-YYYY').format();
    const activeCustomer = yield select(state => state.customer.activeCustomer);
    const fnolTemplate = yield api.fnolTemplates.get({customerId: activeCustomer.id, contractNr, dateFnol});
    yield put(fnolActions.receiveFnolTemplate(fnolTemplate));
  } catch (error) {
    Logger.error(yield error);
  }
}

function * requestFnolSave ({fileGuids}) {
  try {
    const [activeCustomer, fnol, fnolData] = yield [
      select(state => state.customer.activeCustomer),
      select(state => state.fnol),
      select(state => getFormValues(dynamicFormTypes.fnol)(state))
    ];

    yield api.contract.incident.post({
      customerId: activeCustomer.id,
      contractNr: fnol.selectedContractNr,
      dateIncident: moment(fnol.selectedDate, 'DD-MM-YYYY').format()
    }, {answers: getKeyValuePairs(fnolData, fnol.options), fileGuids});

    const incidents = yield api.incidents.get({customerId: activeCustomer.id});
    yield put(incidentActions.receiveIncidents(incidents));
    toastr.success(locale.getTranslatedText(messages.incidentSavedSuccess));
  } catch (error) {
    const e = yield error;
    yield put(validationActions.receiveValidation(dynamicFormTypes.fnol, e));
    toastr.error(locale.getTranslatedText(messages.incidentSavedError));
    Logger.error(e);
  }
}

export function * fnolEvents () {
  while (true) {
    yield [
      takeLatest(types.FNOL_TEMPLATE_REQUEST, requestFnolTemplate),
      takeLatest(types.FNOL_TEMPLATE_SAVE_REQUEST, requestFnolSave)
    ];
  }
}

const messages = defineMessages({
  incidentSavedSuccess: {
    id: 'incident.savedSuccess',
    description: 'Message when incident saved successfully',
    defaultMessage: 'Calculation saved successfully'
  },
  incidentSavedError: {
    id: 'incident.savedError',
    description: 'Message when calculation could not be saved',
    defaultMessage: 'Calculation could not be saved'
  }
});



