import {PropTypes} from 'react';
import CfeComponent from 'components/CfeComponent';
import Modal from 'components/Modal';
import Contract from 'components/Contract';
import Loader from 'components/Loader';
import PaymentMethod from 'components/PaymentMethod';
import CalculationResult from 'components/CalculationResult';
import {injectIntl, defineMessages} from 'react-intl';
import {isValid, getFormValues} from 'redux-form';
import {connect} from 'react-redux';
import dynamicFormTypes from 'lib/dynamicFormTypes';
import {actions as contractActions} from 'redux/modules/contract';
import ContractDetailsClass from 'lib/contractDetails';
import PaymentMethodClass from 'lib/paymentMethod';
import CalculationClass from 'lib/calculation';

class ContractDetails extends CfeComponent {
  static displayName = 'ContractDetails';

  constructor (props) {
    super(props, 'components/Contract/details');

    this.state = {
      readOnly: true,
      isEditingContract: false
    };
  }

  componentWillMount () {
    if (this.props.contractToLoad) {
      this.props.requestContractVersion(this.props.contractToLoad);
    }
    if (this.isTemplateOverriden) {
      super.componentWillMount();
    }
  }

  closeCallback = () => {
    this.props.closeCallback();
    this.state = {
      isEditingContract: false
    };
  };

  handleClickEdit = () => window.location = this.props.config.urls.contractEdit;

  handleSubmit = () => this.props.requestContractEditSave(this.state.isEditingContract ? this.props.contractEditDetails : this.props.contractDetails);

  render () {
    const {contractDetails, contractEditDetails} = this.props;

    const editedContract = this.state.isEditingContract ? contractEditDetails : contractDetails;
    const formParams = {
      ...editedContract,
      readOnly: !this.state.isEditingContract,
      questions: editedContract.options,
      answers: this.props.contractAnswers,
      handleSubmit: this.handleSubmit,
      handleCalculation: this.props.requestContractRecalculate,
      formName: dynamicFormTypes.contract
    };

    const context = {
      ...this,
      contractDetails,
      editedContract,
      Modal,
      formParams,
      Contract,
      messages,
      formatMessage: this.props.intl.formatMessage,
      Loader,
      PaymentMethod,
      CalculationResult,
      dynamicFormTypes
    };

    return super.render(context);
  }
}

const messages = defineMessages({
  edit: {
    id: 'customerDetails.edit',
    description: 'Label on customer edit button',
    defaultMessage: 'Edit details'
  },
  cancelEdit: {
    id: 'customerDetails.cancelEdit',
    description: 'Label on customer cancel editing button',
    defaultMessage: 'Cancel editing'
  },
  editNotAllowed: {
    id: 'customerDetails.editNotAllowed',
    description: 'Mutation of the current contract is not allowed',
    defaultMessage: 'Unfortunately it is not possible to adjust this contract. Please contact our service center.'
  }
});

ContractDetails.propTypes = {
  isLoadingCalculation: PropTypes.bool.isRequired,
  isLoadingContractDetails: PropTypes.bool.isRequired,
  isLoadingContractEditDetails: PropTypes.bool.isRequired,
  isEditFailed: PropTypes.bool.isRequired,
  contractAnswers: PropTypes.object,
  paymentMethodDetails: PropTypes.object,
  validation: PropTypes.array,
  contract: PropTypes.object,
  contractDetails: PropTypes.object,
  contractEditDetails: PropTypes.object,
  calculation: PropTypes.object,
  intl: PropTypes.object,
  dispatch: PropTypes.func
};

const mapStateToProps = (state) => ({
  isEditFailed: state.contract.isEditFailed,
  contractAnswers: getFormValues(dynamicFormTypes.contract)(state),
  isContractEditFormValid: isValid(dynamicFormTypes.contract)(state),
  isLoadingCalculation: state.contract.isLoadingCalculation,
  contractDetails: new ContractDetailsClass(state.contract.details || {}),
  contractEditDetails: new ContractDetailsClass(state.contract.contractEditDetails || {}),
  paymentMethodDetails: new PaymentMethodClass(state.contract.paymentMethodDetails || {}),
  calculation: new CalculationClass(state.contract.calculation),
  validation: state.validation.contractVersion,
  isLoadingContractDetails: state.contract.isLoadingContractDetails,
  isLoadingContractEditDetails: state.contract.isLoadingContractEditDetails,
  config: state.config.dossierConfig.contractsOverview
});

const mapDispatchToProps = (dispatch) => ({
  requestContractVersion: (contract) => dispatch(contractActions.requestContractVersion(contract)),
  requestContractEdit: (contract) => dispatch(contractActions.requestContractEdit(contract)),
  requestContractRecalculate: () => dispatch(contractActions.requestContractRecalculate()),
  requestContractEditSave: (newContract) => dispatch(contractActions.requestContractEditSave(newContract)),
  dispatch
});

export default connect(mapStateToProps, mapDispatchToProps)(injectIntl(ContractDetails));



