export default {
  products: {
    url: '/Products',
    handlers: {
      details: '/:productId/details',
      recommendations: '/:productId/recommendations'
    }
  },

  calculate: {
    url: '/Calculate/:productId'
  },

  calculations: {
    url: '/Customers/:customerId/Calculations/:calculationId'
  },

  emails: {
    url: '/Customers/:customerId/Emails/:emailId'
  },

  customers: {
    url: '/Customers/:customerId'
  },

  contracts: {
    url: '/Customers/:customerId/Contracts',
    handlers: {
      sign: '/:contractId/Sign',
      terminate: '/:contractId/Terminate',
      pay: '/:contractId/Pay',
      registerOffer: '/:offerHash/RegisterOffer'
    }
  },

  contract: {
    url: '/Customers/:customerId/Contracts/:contractNr',
    handlers: {
      version: '/Versions/:contractId',
      recalculate: '/Versions/:contractId/Calculate',
      sign: '/Versions/:contractId/Sign',
      incident: '/FNOL',
      edit: '/Versions/:contractId/Edit'
    }
  },

  incidents: {
    url: '/Customers/:customerId/Incidents/:incidentId'
  },

  paymentMethods: {
    url: '/Customers/:customerId/PaymentMethods/:paymentMethodId'
  },

  documents: {
    url: '/Customers/:customerId/Documents'
  },

  terminateReasons: {
    url: '/TerminateReasons'
  },

  fnolTemplates: {
    url: '/Customers/:customerId/Contracts/:contractNr/FNOL/Templates'
  },

  customerTemplates: {
    url: '/Customers/Types/:type/Templates'
  },

  paymentMethodTemplates: {
    url: '/Customers/PaymentMethods/Types/Templates'
  },

  paymentMethodTransactionCosts: {
    url: '/Customers/PaymentMethods/Types/:paymentMethodType/TransactionCosts'
  },

  customerFeedback: {
    url: '/CustomerFeedback/:customerId/:caseId/:token'
  },

  localizations: {
    url: '/Localizations'
  }
};



