
		module.exports = function(context) {
			return (function() {
				

				if (!React) {
						var React = require("react");
					}

				return (
					<button {...this.props} className='btn btn-primary' type='button'>
  <this.FormattedMessage {...this.messages.submitButtonLabel}/> <i className='glyphicon glyphicon-send'/>
</button>

				);
			}).apply(context);
		};
	


