import React from 'react';
import CfeOptionComponent from 'components/CfeOptionComponent';

export default class BooleanInput extends CfeOptionComponent {
  static displayName = 'BooleanInput';

  constructor (props) {
    super(props, 'components/InputTypes/Boolean/index');
  }

  getErrorMessage () {
    const {props: {validationMessage, touched, error}} = this;
    let errorMessage = null;

    if (validationMessage) {
      // server side error
      errorMessage = validationMessage;
    } else if (error) {
      // client side error
      errorMessage = touched && error;
    }

    return errorMessage;
  }

  render () {
    const {props: {input, question, helpText, toggleHelpBlock, isHelpBlockOpened, disabled}} = this;
    const inputHtml = <input id={question.id} type='checkbox' checked={this.props.input.value} {...this.props.input} disabled={disabled}/>;

    const context = {
      ...this,
      error: this.getErrorMessage(),
      question,
      helpText,
      toggleHelpBlock,
      isHelpBlockOpened,
      input,
      disabled,
      field: inputHtml
    };

    return super.render(context);
  };
}




