import React, {PropTypes} from 'react';
import {injectIntl} from 'react-intl';
import {connect} from 'react-redux';
import {actions as stepperActions} from 'redux/modules/stepper';
import CustomerClass from 'lib/customer';
import ProductClass from 'lib/product';
import Summary from 'components/Summary';
import messages from './messages';
import CalculationClass from 'lib/calculation';
import PaymentMethodClass from 'lib/paymentMethod';
import {getFormValues} from 'redux-form';
import dynamicFormTypes from 'lib/dynamicFormTypes';

class SummaryStep extends React.Component {
  static displayName = 'SummaryStep';

  getSummaryLists = () => {
    const summaryLists = [];
    const {intl: {formatMessage}, product, customer, productAnswers, customerAnswers, calculation, paymentMethodTemplates} = this.props;
    const startDate = product.getStartDate(productAnswers);
    const paymentInterval = product.getPaymentInterval(productAnswers);

    if (customerAnswers && customer.options) {
      summaryLists.push({
        heading: formatMessage(messages.summaryCustomerDetailsHeader),
        summary: customer.getSummary(customerAnswers)
      });
    }

    if (productAnswers && product.options) {
      summaryLists.push({
        heading: formatMessage(messages.summaryInsuranceDetailsHeader),
        summary: product.getSummary(productAnswers, formatMessage)
      });
    }

    summaryLists.push({
      heading: formatMessage(messages.summaryContractDetailsHeader),
      summary: product.getContractSummary(productAnswers, formatMessage)
    });

    if (calculation.paymentIntervalPremium && paymentMethodTemplates.transactionCosts) {
      summaryLists.push({
        heading: formatMessage(messages.summaryQuoteDetailsHeader),
        summary: calculation.getSummary(startDate, paymentInterval, paymentMethodTemplates.transactionCosts, product, formatMessage)
      });
    }

    return summaryLists;
  };

  goToNextStep = () => this.props.setActiveStep(this.props.step.stepIndex + 1);

  render () {
    const {product} = this.props;

    return <Summary
      summaryLists={this.getSummaryLists()}
      documents={product.insuranceDocuments}
      handleSubmit={this.goToNextStep}/>;
  }
}

SummaryStep.propTypes = {
  customer: PropTypes.object,
  setActiveCustomer: PropTypes.func,
  intl: PropTypes.object,
  user: PropTypes.object,
  activeStepIndex: PropTypes.number,
  setActiveStep: PropTypes.func,
  product: PropTypes.object,
  step: PropTypes.object,
  productAnswers: PropTypes.object,
  calculation: PropTypes.object,
  paymentMethodTemplates: PropTypes.array,
  customerAnswers: PropTypes.object,
  selectedPaymentMethodId: PropTypes.string
};

const mapStateToProps = (state) => {
  const product = new ProductClass({...state.product.selected, options: state.flow.calculateable.options});
  const customer = new CustomerClass(state.customer.activeCustomer || state.customer.customerTemplate);
  const productSaveAnswers = getFormValues(dynamicFormTypes.extraQuestions)(state);
  const productCalculationAnswers = getFormValues(dynamicFormTypes.calculation)(state);
  const productAnswers = {...productCalculationAnswers, ...productSaveAnswers};
  const customerAnswers = getFormValues(dynamicFormTypes.customer)(state);
  const calculation = new CalculationClass(state.calculation);
  const paymentMethodTemplates = (state.paymentMethod.list || []).map(p => new PaymentMethodClass(p));

  return {
    customer,
    product,
    productAnswers,
    customerAnswers,
    calculation,
    paymentMethodTemplates,
    selectedPaymentMethodId: state.paymentMethod.selected ? state.paymentMethod.selected.id : null
  };
};

const mapDispatchToProps = (dispatch) => ({
  setActiveStep: (stepIndex) => dispatch(stepperActions.setActiveStep(stepIndex))
});

export default connect(mapStateToProps, mapDispatchToProps)(injectIntl(SummaryStep));



