
		module.exports = function(context) {
			return (function() {
				

				if (!React) {
						var React = require("react");
					}

				return (
					<div className="checkbox">
  <label htmlFor={this.question.id}>
    {this.field}
    {this.question.questionText}{this.question.isMandatory && <span>*</span>}
  </label>
</div>

				);
			}).apply(context);
		};
	


