import {PropTypes} from 'react';
import CfeComponent from 'components/CfeComponent';
import {Field} from 'redux-form';
import {defineMessages, FormattedMessage, injectIntl} from 'react-intl';
import {connect} from 'react-redux';
import c from 'classnames';
import Loader from 'components/Loader';
import dynamicFormTypes from 'lib/dynamicFormTypes';
import {actions as authActions} from 'redux/modules/auth';
import formEnhancer from 'enhancers/formEnhancer';
import LoadingButton from 'components/buttons/LoadingButton';
import CustomerClass from 'lib/customer';

const rules = {
  email: {
    presence: true,
    email: true
  },
  password: {
    presence: true
  }
};

class LoginForm extends CfeComponent {
  static displayName = 'LoginForm';

  constructor (props) {
    super(props, 'forms/LoginForm/index');
  }

  componentWillMount () {
    this.props.getStsSettings();
  }

  openIdProvider = (externalProvider) => this.props.requestExternalLogin(externalProvider.AuthenticationType);

  handleSubmit = (credentials) => this.props.requestLogin(credentials);

  render () {
    return super.render({...this, c, Loader, Field, FormattedMessage, messages, LoadingButton});
  }
}

LoginForm.propTypes = {
  getStsSettings: PropTypes.func,
  requestLogin: PropTypes.func,
  requestExternalLogin: PropTypes.func,
  stsSettings: PropTypes.object,
  customer: PropTypes.instanceOf(CustomerClass),
  user: PropTypes.object
};

const mapDispatchToProps = (dispatch) => ({
  requestLogin: (credentials) => dispatch(authActions.requestLogin(credentials)),
  requestExternalLogin: (identityProvider) => dispatch(authActions.requestExternalLogin(identityProvider)),
  getStsSettings: () => dispatch(authActions.getStsSettings())
});

const mapStateToProps = (state) => ({
  stsSettings: state.auth.stsSettings,
  isLoginLoading: state.auth.isLoginLoading,
  loginError: state.auth.loginError,
  user: state.auth.user,
  customer: new CustomerClass(state.customer.activeCustomer)
});

const messages = defineMessages({
  emailAddress: {
    id: 'loginForm.emailAddress',
    description: 'email address field description',
    defaultMessage: 'Email Address'
  },
  password: {
    id: 'loginForm.password',
    description: 'password field description',
    defaultMessage: 'Password'
  },
  submit: {
    id: 'loginForm.submitButton',
    description: 'button text',
    defaultMessage: 'Login'
  }
});

export default connect(mapStateToProps, mapDispatchToProps)(injectIntl(formEnhancer(LoginForm, {
  form: dynamicFormTypes.login,
  rules
})));



