import {PropTypes} from 'react';
import CfeComponent from 'components/CfeComponent';
import {Field, reduxForm, getFormSyncErrors} from 'redux-form';
import {defineMessages, FormattedMessage} from 'react-intl';
import {connect} from 'react-redux';
import {mapValues} from 'lodash';
import Validator from '../../lib/validator';
import {actions as feedbackActions} from 'redux/modules/feedback';
import c from 'classnames';
import url from 'url';
import Loader from 'components/Loader';

class CustomerFeedbackForm extends CfeComponent {
  static displayName = 'CustomerFeedbackForm';

  constructor (props) {
    super(props, 'forms/CustomerFeedback/CustomerFeedbackForm');
  }

  componentWillMount () {
    const {query: {customerId, emailAddress, caseId, token}} = url.parse(window.location.href, true);
    this.props.requestFeedbackCheck(customerId || emailAddress, caseId, token);
  }

  handleSubmit = (input) => this.props.requestSendFeedback(input);

  render () {
    return super.render({...this, c, Loader, Field, FormattedMessage, messages});
  }
}

CustomerFeedbackForm.propTypes = {
  customerName: PropTypes.string,
  caseName: PropTypes.string,
  pristine: PropTypes.bool,
  submitting: PropTypes.bool,
  isChecking: PropTypes.bool,
  invalid: PropTypes.bool,
  handleSubmit: PropTypes.func,
  requestFeedbackCheck: PropTypes.func,
  requestSendFeedback: PropTypes.func,
  errors: PropTypes.object,
  fields: PropTypes.object
};

const rules = {
  rating: {
    presence: true
  }
};

const validate = (values) => mapValues(Validator.check(rules, values), e => e[0]);

const mapStateToProps = (state) => ({
  isChecking: state.feedback.isChecking,
  isFeedbackSend: state.feedback.isFeedbackSend,
  fields: state.form.feedback ? state.form.feedback.fields || {} : {},
  errors: getFormSyncErrors('feedback')(state),
  customerName: state.feedback.CustomerName,
  caseName: state.feedback.CaseName,
  customerId: state.feedback.customerId,
  caseId: state.feedback.caseId,
  token: state.feedback.token,
  errorMessage: state.feedback.error
});

const mapDispatchToProps = (dispatch) => ({
  requestSendFeedback: (feedback) => dispatch(feedbackActions.requestSendFeedback(feedback)),
  requestFeedbackCheck: (customerId, caseId, token) => dispatch(feedbackActions.requestFeedbackCheck(customerId, caseId, token))
});

const messages = defineMessages({
  intro: {
    id: 'feedback.intro',
    description: 'Message shown to customer',
    defaultMessage: 'Hello {customerName}, please rate {caseName}'
  },

  remarksLabel: {
    id: 'feedback.remarksLabel',
    description: 'Label for the remarks field',
    defaultMessage: 'Please fill in your remarks'
  },

  thanks: {
    id: 'feedback.thanks',
    description: 'Message shown when feedback is send successfully',
    defaultMessage: 'Thank you for your feedback'
  }
});

export default connect(mapStateToProps, mapDispatchToProps)(reduxForm({
  form: 'feedback',
  validate
})(CustomerFeedbackForm));



