
		module.exports = function(context) {
			return (function() {
				

				if (!React) {
						var React = require("react");
					}

				return (
					<this.Modal
  title={this.props.title}
  open={this.props.isOpen}
  closeCallback={this.closeCallback}
  id='contract-detail-modal'>
  <div className='modal-body'>
    <this.ContractDetails />
  </div>
</this.Modal>

				);
			}).apply(context);
		};
	


