import React from 'react';
import CfeOptionComponent from 'components/CfeOptionComponent';
import InputWrapper from 'enhancers/InputWrapper';

export class DateInput extends CfeOptionComponent {
  static displayName = 'DateInput';

  constructor (props) {
    super(props, 'components/InputTypes/Date/index');
  }

  // reset date to previous value when edditing is cancelled
  componentWillReceiveProps (nextProps) {
    if (this.isTemplateOverriden) {
      super.componentWillReceiveProps(nextProps);
    } else {
      const {props: {readOnly, value}} = this;
      if (nextProps.value !== value && nextProps.readOnly !== readOnly) {
        this.$dateInput.datepicker('update', nextProps.value);
      }
    }
  }

  componentDidMount () {
    if (this.isTemplateOverriden) {
      super.componentDidMount();
    } else {
      const {props: {onChange, onBlur, question}} = this;
      this.$dateInput = jQuery('[name=' + question.id + ']');

      this.$dateInput.datepicker({format: 'dd-mm-yyyy', autoclose: true})
        .on('change', onChange)
        .on('blur', onBlur);
    }
  }

  render () {
    const {props: {error, input, question, helpText, toggleHelpBlock, isHelpBlockOpened, disabled}} = this;
    const inputHtml = <input className='form-control' type='text' {...this.props.input} disabled={disabled} />;

    const context = {
      ...this,
      error,
      question,
      helpText,
      toggleHelpBlock,
      isHelpBlockOpened,
      input,
      disabled,
      field: inputHtml
    };

    return super.render(context);
  };
}

export default InputWrapper(DateInput);



