import React, {PropTypes} from 'react';
import {Provider} from 'react-redux';
import {IntlProvider} from 'react-intl';
import ReduxToastr from 'react-redux-toastr';
import RehydrateEnhancer from 'enhancers/rehydrateEnhancer';

export class RootContainer extends React.Component {
  render () {
    return (this.props.isRehydrated &&
      <Provider store={this.props.store}>
        <IntlProvider locale={this.props.locale} messages={this.props.messages}>
          <div style={{height: '100%'}}>
            {this.props.children}
            <ReduxToastr
              timeOut={3000}
              newestOnTop={false}
              preventDuplicates
              position='top-right'
              transitionIn='fadeIn'
              transitionOut='fadeOut'
              progressBar/>
          </div>
        </IntlProvider>
      </Provider>
    );
  }
}

RootContainer.propTypes = {
  store: PropTypes.object.isRequired,
  locale: PropTypes.string.isRequired,
  messages: PropTypes.object.isRequired,
  children: PropTypes.any,
  isRehydrated: PropTypes.bool
};

export default RehydrateEnhancer(RootContainer);



