import {PropTypes} from 'react';
import {defineMessages, FormattedMessage} from 'react-intl';
import util from 'lib/util';
import Loader from 'components/Loader';
import CfeComponent from 'components/CfeComponent';

export default class CalculationList extends CfeComponent {
  static propTypes = {
    calculations: PropTypes.array,
    isLoading: PropTypes.bool,
    handleClickRemove: PropTypes.func,
    handleResumeCalculation: PropTypes.func
  };

  constructor (props) {
    super(props, 'components/Calculation/list');
  }

  componentWillMount () {
    if (this.isTemplateOverriden) {
      super.componentWillMount();
    }
  }

  componentDidMount () {
    if (this.isTemplateOverriden) {
      super.componentDidMount();
    }
  }

  componentWillReceiveProps (nextProps) {
    if (this.isTemplateOverriden) {
      super.componentWillReceiveProps(nextProps);
    }
  }

  componentDidUpdate () {
    if (this.isTemplateOverriden) {
      super.componentDidUpdate();
    }
  }

  render () {
    const calculations = (this.props.calculations || []);

    const context = {
      ...this,
      Loader,
      FormattedMessage,
      calculations,
      messages,
      util
    };

    return super.render(context);
  }
}

const messages = defineMessages({
  contractNumber: {
    id: 'contract.number',
    description: 'Table title',
    defaultMessage: 'Contract number'
  },
  productName: {
    id: 'insurance.name',
    description: 'Table title',
    defaultMessage: 'Product name'
  },
  dateCreated: {
    id: 'calculation.dateCreated',
    description: 'Table title',
    defaultMessage: 'Date created'
  }
});



