import React, {PropTypes} from 'react';
import RootContainer from 'containers/RootContainer';
import DossierPaymentMethodsView from 'views/DossierPaymentMethodsView/DossierPaymentMethodsView';

export default class DossierPaymentMethodsContainer extends React.Component {
  static propTypes = {
    store: PropTypes.object.isRequired,
    locale: PropTypes.string.isRequired,
    messages: PropTypes.object.isRequired
  };

  render () {
    return (
      <RootContainer {...this.props}>
        <DossierPaymentMethodsView />
      </RootContainer>
    );
  }
}



