
		module.exports = function(context) {
			return (function() {
				

				if (!React) {
						var React = require("react");
					}

				return (
					<div>
  { this.customerPaymentMethods && this.customerPaymentMethods.length > 0
    ? <this.PaymentMethodPicker {...this.paymentMethodPickerProps}/>
    : <this.PaymentMethodCreator {...this.paymentMethodCreatorProps}/> }
</div>

				);
			}).apply(context);
		};
	


