import {PropTypes} from 'react';
import {FormattedMessage} from 'react-intl';
import messages from './messages';
import LanguageSwitcher from '../Localization/LanguageSwitcher';
import CfeComponent from 'components/CfeComponent';
import CustomerClass from 'lib/customer';

export default class Navbar extends CfeComponent {
  static propTypes = {
    customer: PropTypes.instanceOf(CustomerClass),
    user: PropTypes.object,
    login: PropTypes.func,
    logout: PropTypes.func
  };

  static displayName = 'components/Navbar/index';

  constructor (props) {
    super(props, 'components/Navbar/index');
  }

  logout = () => this.props.logout(this.props.user.access_token);

  render () {
    const context = {
      ...this,
      FormattedMessage,
      LanguageSwitcher,
      messages
    };

    return super.render(context);
  }
}



