import {actions as customerActions, types} from 'redux/modules/customer';
import {actions as validationActions} from 'redux/modules/validation';
import {takeLatest} from 'redux-saga';
import {put, select} from 'redux-saga/effects';
import {api} from 'lib/api';
import {getKeyValuePairs} from 'lib/option';
import Logger from 'lib/logger';
import dynamicFormTypes from 'lib/dynamicFormTypes';

export function * saveCustomerRequest ({customer, newCustomerData}) {
  try {
    newCustomerData = getKeyValuePairs(newCustomerData, customer.options);

    let newCustomer = customer.id
      ? yield api.customers.put({customerId: customer.id}, newCustomerData)
      : yield api.customers.post({}, {answers: newCustomerData, customerType: 0});

    // TODO temp fix, api should be adjusted to return a customer when a customer is created
    if (newCustomer.messages && newCustomer.messages[0].insertedKey) {
      const customers = yield api.customers.get();
      newCustomer = customers[0];
    }

    yield put(customerActions.receiveCustomer(newCustomer));
    yield put({type: types.CUSTOMER_SAVE_RECEIVE});
  } catch (error) {
    const e = yield error;
    yield put(validationActions.receiveValidation(dynamicFormTypes.customer, e));
    yield put(validationActions.receiveValidation(dynamicFormTypes.customerEdit, e));
    yield put(customerActions.requestSaveCustomerFailure(e));
    Logger.error(e);
  }
}

export function * requestDocuments () {
  try {
    const customer = yield select(state => state.customer.activeCustomer);
    let documents = yield api.documents.get({customerId: customer.id});
    yield put(customerActions.receiveCustomerDocuments(documents));
  } catch (error) {
    const e = yield error;
    yield put(customerActions.receiveCustomerDocumentsFailure(e));
    Logger.error(e);
  }
}

export function * customerEvents () {
  while (true) {
    yield [
      takeLatest(types.CUSTOMER_SAVE_REQUEST, saveCustomerRequest),
      takeLatest(types.CUSTOMER_DOCUMENTS_REQUEST, requestDocuments)
    ];
  }
}



