import {actions as paymentMethodActions} from 'redux/modules/paymentMethod';
import {types as customerTypes, actions as customerActions} from 'redux/modules/customer';
import {types as contractTypes} from 'redux/modules/contract';
import {actions as stepperActions, types as stepperTypes} from 'redux/modules/stepper';
import {takeLatest} from 'redux-saga';
import {put, select} from 'redux-saga/effects';
import {api} from 'lib/api';
import {actionTypes as formTypes, getFormValues} from 'redux-form';
import Validator from 'lib/validator';
import {isEmpty} from 'lodash';
import config from 'lib/config';
import clientTypes from 'lib/clientTypes';
import Logger from 'lib/logger';
import dynamicFormTypes from 'lib/dynamicFormTypes';
import formComponentTypes from 'lib/formComponentTypes';

function * getStepData ({stepIndex}) {
  try {
    const [user, activeCustomer] = yield [
      select(state => state.auth.user),
      select(state => state.customer.activeCustomer)
    ];

    if (user && stepIndex === 1) {
      if (config.clientType === clientTypes.cms) {
        const [customers, customerTemplate] = yield [
          api.customers.get(),
          api.customerTemplates.get({type: 'NaturalPerson'})
        ];

        yield [
          put(customerActions.receiveCustomer(customers[0])),
          put(customerActions.receiveCustomerTemplate(customerTemplate))
        ];
      }
      if (config.clientType === clientTypes.serviceCenter) {
        const customerToLoadId = yield select(state => state.customer.customerToLoad.customerId);
        const customer = yield api.customers.get({customerId: customerToLoadId});
        yield put(customerActions.receiveCustomer(customer));
      }
    }

    if (stepIndex === 4) {
      const [paymentMethodTemplates, paymentMethods] = yield [
        api.paymentMethodTemplates.get(),
        api.paymentMethods.get({customerId: activeCustomer.id})
      ];

      yield [
        put(customerActions.receiveCustomerPaymentMethods(paymentMethods)),
        put(paymentMethodActions.receivePaymentMethodTemplates(paymentMethodTemplates))
      ];
    }
  } catch (error) {
    Logger.error(yield error);
  }
}

function * nextStep () {
  const activeStepIndex = yield select(state => state.stepper.activeStepIndex);
  yield put(stepperActions.setActiveStep(activeStepIndex + 1));
}

function * validate ({meta: {form}}) {
  if (![dynamicFormTypes.calculation, dynamicFormTypes.extraQuestions].includes(form)) return;

  const [calculateableOptions, stepper, formAnswers] = yield [
    select(state => state.flow.calculateable.options),
    select(state => state.stepper),
    select(state => getFormValues(form)(state))
  ];

  const activeStep = stepper.steps[stepper.activeStepIndex];

  if (activeStep.questions) {
    const {relevantCalculateableOptions, answers} = activeStep.questions.reduce((acc, q) => {
      let calculateableOption = calculateableOptions.find(pO => pO.id === q.id);

      if (calculateableOption && calculateableOption.isQuestionRelevant(formAnswers, calculateableOptions)) {
        acc.relevantCalculateableOptions.push(calculateableOption);
        acc.answers[q.id] = formAnswers[q.id];
      }
      return acc;
    }, {relevantCalculateableOptions: [], answers: {}});

    const rules = Validator.buildOptionRules(relevantCalculateableOptions);
    const errors = Validator.check(rules, answers);

    if (isEmpty(errors)) {
      yield put(stepperActions.markStepAsValid(stepper.activeStepIndex));
    } else {
      yield put(stepperActions.markStepAsInvalid(stepper.activeStepIndex));
    }

    if (activeStep.formComponent === formComponentTypes.stepByStepQuestions) {
      if (errors[activeStep.questions[activeStep.activeQuestionIndex].id]) {
        yield put(stepperActions.markQuestionAsInvalid(stepper.activeStepIndex, activeStep.activeQuestionIndex));
      } else {
        yield put(stepperActions.markQuestionAsValid(stepper.activeStepIndex, activeStep.activeQuestionIndex));
      }
    }
  }
}

export function * watchSteps () {
  yield [
    takeLatest(formTypes.CHANGE, validate),
    takeLatest(stepperTypes.SET_ACTIVE_STEP, getStepData),
    takeLatest(customerTypes.CUSTOMER_SAVE_RECEIVE, nextStep),
    takeLatest(contractTypes.CONTRACT_SAVE_AND_SIGN_RECEIVE, nextStep)
  ];
}



