import {trimEnd, each} from 'lodash';

export default class Resource {
  constructor (config, http) {
    this.config = config;
    this.http = http;

    if (config.handlers) {
      Object.keys(config.handlers).forEach(handlerKey => {
        this[handlerKey] = new Resource({
          url: this.config.url + config.handlers[handlerKey]
        }, http);
      });
    }
  }

  buildQueryString = (params = {}) => {
    let queryString = '';

    each(params, (v, k) => {
      queryString += k + '=' + encodeURIComponent(v) + '&';
    });

    return trimEnd(queryString, '&');
  };

  buildEndPointUrl = (params = {}) => {
    let url = this.config.url;

    if (Object.keys(params).length) {
      const urlParams = url.replace(/.*?:([^\/]+)/g, '$1 ').trim().split(' ');

      urlParams.forEach((urlParam) => {
        if (url.match(new RegExp(':' + urlParam))) {
          url = url.replace(':' + urlParam, params[urlParam] ? params[urlParam] : '');
          delete params[urlParam];
        }
      });
    }

    if (Object.keys(params).length) {
      url = url + '?' + this.buildQueryString(params);
    }

    return url.replace(/\/:.*/, '');
  };

  transform = (method, data, params = {}) => {
    if (this.config.transformers && this.config.transformers[method]) {
      return this.config.transformers[method](data, params);
    }

    if (this.config.transformer) {
      return this.config.transformer(data, params);
    }

    return data;
  };

  fetch = (method, ...args) => {
    const urlParams = args[0] || {};
    const payload = args[1] || {};
    return this.http[method](this.buildEndPointUrl(urlParams), payload)
      .then(res => {
        const payload = parseInt(res.headers.get('Content-Length'), 10) > 0 ? res.json() : {};

        if (res.ok) {
          return payload;
        }

        throw payload;
      })
      .then((payload) => this.transform(method, payload, urlParams))
      .catch(err => {
        throw err;
      });
  };

  get = (...args) => {
    return this.fetch('get', ...args);
  };

  post = (...args) => {
    return this.fetch('post', ...args);
  };

  put = (...args) => {
    return this.fetch('put', ...args);
  };

  delete = (...args) => {
    return this.fetch('delete', ...args);
  };
}



