
		module.exports = function(context) {
			return (function() {
				

				if (!React) {
						var React = require("react");
					}

				return (
					<div className={'form-group' + (this.error ? ' has-error' : '')}>
  <label htmlFor={this.question.id}>
    {this.question.questionText}
    {this.question.isMandatory && <span>*</span>}
  </label>
  {this.helpText
  ? <div>
      <div className='input-group'>
        {this.field}
        <span className='input-group-addon' onClick={this.toggleHelpBlock}>
          <span className='glyphicon glyphicon-info-sign'/>
        </span>
      </div>
      <p className={'help-block' + (!this.isHelpBlockOpened ? ' hide' : '') } dangerouslySetInnerHTML={this.helpText}/>
    </div>
  : this.field}
  {this.error && <span className='help-block'>{this.error}</span>}
</div>

				);
			}).apply(context);
		};
	


