import Config from './config';
import Locale from 'lib/locale';
import userStorage from 'lib/userStorage';
import Util from 'lib/util';

const locale = new Locale({locale: 'nl_NL'});

class Auth {
  constructor () {
    this.contentTypeHeaders = {
      'Content-Type': 'application/x-www-form-urlencoded',
      'Accept-Language': locale.getCurrentLocale().replace('_', '-')
    };
  }

  static getStsSettings () {
    return new Promise((resolve, reject) => {
      fetch(Config.stsSettingsUrl)
        .then(response => response.json())
        .then(settingsResponse => {
          settingsResponse.error ? reject(settingsResponse) : resolve(settingsResponse);
        });
    });
  }

  loginUsingTokenApi = (credentials) => {
    return new Promise((loginPromiseResolve, loginPromiseReject) => {
      const encodedSecret = btoa(`${Config.clientId}:${Config.clientSecret}`);
      const Authorization = `Basic ${encodedSecret}`;

      fetch(Config.stsTokenUrl, {
        method: 'post',
        headers: {...this.contentTypeHeaders, Authorization},
        body: jQuery.param({
          username: credentials.email.replace('@', ''),
          password: credentials.password,
          grant_type: 'password',
          scope: 'webapi divdocumentstorewebapi',
          acr_values: 'tenant:' + Config.tenant
        })
      })
        .then(response => response.json())
        .then(tokenReseponse => {
          if (!tokenReseponse.error) {
            const jsonIdToken = JSON.parse(atob(tokenReseponse.access_token.split('.')[1]));

            const authUser = {
              access_token: tokenReseponse.access_token,
              jsonData: jsonIdToken
            };
            this.setLoggedInUser(authUser);
            loginPromiseResolve(authUser);
          } else {
            loginPromiseReject(tokenReseponse);
          }
        });
    });
  };

  login (identityProvider) {
    let loginPromiseResolve, loginPromiseReject;
    const loginPromise = new Promise((resolve, reject) => {
      loginPromiseReject = reject;
      loginPromiseResolve = resolve;
    });

    const redirect_uri = window.location.origin + '/handleLogin.html';
    const client_id = 'div_web_api';
    const response_type = 'id_token token';
    const scope = 'openid email webapi divdocumentstorewebapi';
    const nonce = 'iquw@87!9' + Date.now() + '' + Math.random();
    const state = Date.now() + '' + Math.random();

    const url =
      Config.stsAuthorizeUrl +
      '?client_id=' + encodeURI(client_id) +
      '&redirect_uri=' + encodeURI(redirect_uri) +
      '&response_type=' + encodeURI(response_type) +
      '&scope=' + encodeURI(scope) +
      '&state=' + encodeURI(state) +
      '&nonce=' + encodeURI(nonce) +
      '&acr_values=lang:' + encodeURI(locale.getCurrent()) +
      '+tenant:' + encodeURI(Config.tenant) +
      (identityProvider ? '+idp:' + encodeURI(identityProvider) : '');

    window.refreshPageAfterLogin = (id_token, access_token, windowCloseCallBack) => {
      const jsonIdToken = JSON.parse(atob(id_token.split('.')[1]));

      if (jsonIdToken.nonce === nonce) {
        const authUser = {
          id_token: id_token,
          access_token: access_token,
          jsonData: jsonIdToken
        };

        this.setLoggedInUser(authUser);
        loginPromiseResolve(authUser);
      } else {
        loginPromiseReject();
      }

      windowCloseCallBack();
    };

    window.open(url, 'newWindow', 'width=1000, height=800');

    return loginPromise;
  }

  logout (id_token) {
    const redirect_uri = window.location.origin + '/handleLogout.html';
    const url = Config.stsEndSessionUrl + '?id_token_hint=' + id_token +
      '&post_logout_redirect_uri=' + encodeURI(redirect_uri);

    window.open(url, 'newWindow', 'width=1000, height=800');

    userStorage.unsetStoredUser();
  }

  register ({email, password, passwordConfirm}) {
    return fetch(Config.stsRegisterUrl, {
      method: 'post',
      headers: this.contentTypeHeaders,
      body: Util.encodeParams({
        Email: email,
        Password: password,
        ConfirmPassword: passwordConfirm,
        SendConfirmationEmail: false,
        Tenant: Config.tenant,
        App: 'cfe'
      })
    }).then(res => {
      if (res.ok) return res.json();
      throw res.json();
    }).catch(err => {
      throw err;
    });
  }

  setLoggedInUser (authUser) {
    userStorage.setStoredUser(authUser);
  }
}

export default Auth;



