import React, {PropTypes} from 'react';
import {actions} from 'redux/modules/customer';
import {connect} from 'react-redux';
import userStorage from 'lib/userStorage';

export default (ComposedComponent) => {
  class DossierEnhancer extends React.Component {
    static propTypes = {
      isDossierFetched: PropTypes.bool,
      requestCustomerDossier: PropTypes.func
    };

    componentWillMount () {
      if (!this.props.isDossierFetched) {
        this.props.requestCustomerDossier();
      }
    }

    render = () => userStorage.isLoggedIn() ? <ComposedComponent {...this.props} /> : <div>Please login to see this page</div>;
  }

  const mapStateToProps = (state) => ({
    isDossierFetched: state.customer.isDossierFetched
  });

  const mapDispatchToProps = (dispatch) => ({
    requestCustomerDossier: () => dispatch(actions.requestCustomerDossier())
  });

  return connect(mapStateToProps, mapDispatchToProps)(DossierEnhancer);
};



