import moment from 'moment';

export default class Util {
  static camelCase (string) {
    string = string.replace(/[\-_\s]+(.)?/g, (match, chr) => chr ? chr.toUpperCase() : '');

    // Ensure 1st char is always lowercase
    return string.replace(/^([A-Z])/, (match, chr) => chr ? chr.toLowerCase() : '');
  };

  static toLowerCaseArray (arr) {
    return arr.map((elem) => {
      if (typeof elem === 'string') return elem.toLowerCase();
    });
  };

  static formatDate (date) {
    return moment(date).format('DD-MM-YYYY');
  }

  static unescapeHtml (htmlText) {
    const escapeEl = document.createElement('textarea');
    escapeEl.innerHTML = htmlText;
    return escapeEl.textContent;
  }

  static encodeParams (params) {
    return Object.keys(params)
      .map((key) => `${encodeURIComponent(key)}=${encodeURIComponent(params[key])}`)
      .join('&');
  }
}



