import {takeLatest} from 'redux-saga';
import {put} from 'redux-saga/effects';
import {api} from 'lib/api';
import {types, actions} from 'redux/modules/product';
import Logger from 'lib/logger';

function * requestProductList () {
  try {
    let productList = yield api.products.get();
    yield put(actions.receiveProductList(productList));
  } catch (error) {
    Logger.error(yield error);
  }
}

export function * productEvents () {
  while (true) {
    yield [
      takeLatest(types.PRODUCT_LIST_REQUEST, requestProductList)
    ];
  }
}



