import {handleActions} from 'redux-actions';

const START_PRODUCT_FLOW_REQUEST = 'START_PRODUCT_FLOW';
const START_CALCULATION_FLOW_REQUEST = 'START_CALCULATION_FLOW_REQUEST';
const CALCULATEABLE_RECEIVE = 'CALCULATEABLE_RECEIVE';
const UPDATE_CALCULATEABLE_OPTIONS = 'UPDATE_CALCULATEABLE_OPTIONS';
const START_CONTRACT_EDIT_FLOW_REQUEST = 'START_CONTRACT_EDIT_FLOW_REQUEST';

const requestStartProductFlow = (productId) => ({type: START_PRODUCT_FLOW_REQUEST, productId});
const requestStartCalculationFlow = (calculationId) => ({type: START_CALCULATION_FLOW_REQUEST, calculationId});
const requestStartContractEditFlow = () => ({type: START_CONTRACT_EDIT_FLOW_REQUEST});
const receiveCalculateable = (calculateable) => ({type: CALCULATEABLE_RECEIVE, calculateable});
const updateCalculateableOptions = (calculateableOptions) => ({type: UPDATE_CALCULATEABLE_OPTIONS, calculateableOptions});

const handleReceiveCalculateable = (state, action) => ({...state, calculateable: action.calculateable});
const handleUpdateCalculateableOptions = (state, action) => ({
  ...state,
  calculateable: {...state.calculateable, options: action.calculateableOptions}
});

export default handleActions({
  [CALCULATEABLE_RECEIVE]: handleReceiveCalculateable,
  [UPDATE_CALCULATEABLE_OPTIONS]: handleUpdateCalculateableOptions
}, {calculateable: {}});

export const types = {START_PRODUCT_FLOW_REQUEST, START_CALCULATION_FLOW_REQUEST, UPDATE_CALCULATEABLE_OPTIONS, START_CONTRACT_EDIT_FLOW_REQUEST};
export const actions = {
  requestStartProductFlow,
  requestStartCalculationFlow,
  receiveCalculateable,
  updateCalculateableOptions,
  requestStartContractEditFlow
};



