import {PropTypes} from 'react';
import CfeComponent from 'components/CfeComponent';
import {injectIntl} from 'react-intl';
import {connect} from 'react-redux';
import messages from './messages';
import CustomerClass from 'lib/customer';
import {actions as customerActions} from 'redux/modules/customer';
import {actions as stepperActions} from 'redux/modules/stepper';
import Customer from 'components/Customer';
import dynamicFormTypes from 'lib/dynamicFormTypes';
import RegisterForm from 'forms/RegisterForm';
import LoginForm from 'forms/LoginForm';

class CustomerStep extends CfeComponent {
  static displayName = 'CustomerStep';

  constructor (props) {
    super(props, 'views/ProductView/CustomerStep');
  }

  handleCreateEditCustomer = (newCustomerData) => this.props.requestSaveCustomer(this.props.customer, newCustomerData);

  getStepQuestions () {
    if (this.props.customer.options && this.props.customerConfig && this.props.customerConfig.questions) {
      return this.props.customerConfig.questions.map(q => this.props.customer.options.filter(o => o.id === q.id)[0]);
    }

    return this.props.customer.options;
  }

  render () {
    const {user, isSavingCustomer, customer, intl: {formatMessage}} = this.props;

    customer.options = this.getStepQuestions();

    const customerProps = {
      ...customer,
      formName: dynamicFormTypes.customer,
      handleSubmit: this.handleCreateEditCustomer,
      isLoading: isSavingCustomer
    };

    const context = {
      ...this,
      user,
      Customer,
      formatMessage,
      messages,
      customerProps,
      RegisterForm,
      LoginForm
    };

    return super.render(context);
  }
}

CustomerStep.propTypes = {
  customer: PropTypes.object,
  intl: PropTypes.object,
  user: PropTypes.object,
  activeStepIndex: PropTypes.number,
  setActiveStep: PropTypes.func,
  shouldReinitializeCustomerForm: PropTypes.bool,
  customerConfig: PropTypes.object,
  isSavingCustomer: PropTypes.bool
};

const mapStateToProps = (state) => {
  const customer = new CustomerClass(state.customer.activeCustomer || state.customer.customerTemplate);

  return {
    customer,
    customerToLoad: state.customer.customerToLoad,
    activeStepIndex: state.stepper.activeStepIndex,
    user: state.auth.user,
    customerConfig: state.config.customerConfig,
    isSavingCustomer: state.customer.isSavingCustomer
  };
};

const mapDispatchToProps = (dispatch) => ({
  setActiveStep: (stepIndex) => dispatch(stepperActions.setActiveStep(stepIndex)),
  requestSaveCustomer: (customer, customerData) => dispatch(customerActions.requestSaveCustomer(customer, customerData))
});

export default connect(mapStateToProps, mapDispatchToProps)(injectIntl(CustomerStep));



