import {combineReducers} from 'redux';
import {reducer as reduxFormReducer} from 'redux-form';
import validation from './modules/validation';
import auth from './modules/auth';
import customer from './modules/customer';
import stepper from './modules/stepper';
import calculation from './modules/calculate';
import paymentMethod from './modules/paymentMethod';
import contract from './modules/contract';
import product from './modules/product';
import incident from './modules/incident';
import fnol from './modules/fnol';
import lastAction from './modules/lastAction';
import lookupData from './modules/lookupData';
import feedback from './modules/feedback';
import calculations from './modules/calculation';
import emails from './modules/email';
import flow from './modules/flow';
import {reducer as toastr} from 'react-redux-toastr';
import localization from './modules/localization';
import offer from './modules/offer';
import reduxFormPlugin from 'redux/reduxFormPlugin';

export default combineReducers({
  product,
  form: reduxFormReducer.plugin(reduxFormPlugin),
  auth,
  customer,
  stepper,
  validation,
  calculation,
  paymentMethod,
  contract,
  incident,
  fnol,
  config: (state = {}) => state,
  lastAction,
  lookupData,
  feedback,
  calculations,
  emails,
  flow,
  toastr,
  localization,
  offer
});



