import React from 'react';
import CfeOptionComponent from 'components/CfeOptionComponent';
import InputWrapper from 'enhancers/InputWrapper';

class StringInput extends CfeOptionComponent {
  static displayName = 'StringInput';

  constructor (props) {
    super(props, 'components/InputTypes/String/index');
  }

  render () {
    const {props: {disabled, error, input, question, helpText, toggleHelpBlock, isHelpBlockOpened}} = this;
    const inputHtml = <input className='form-control' {...this.props.input} disabled={disabled} />;

    const context = {
      ...this,
      error,
      question,
      helpText,
      toggleHelpBlock,
      isHelpBlockOpened,
      input,
      disabled,
      field: inputHtml
    };

    return super.render(context);
  };
}

export default InputWrapper(StringInput);



