import React, {PropTypes} from 'react';
import DynamicOptionsFormEnhancer from 'enhancers/DynamicOptionsFormEnhancer';
import lookupData from 'enhancers/lookupData';

class Contract extends React.PureComponent {
  render = () => this.props.form;
}

Contract.propTypes = {
  form: PropTypes.any
};

export default lookupData(DynamicOptionsFormEnhancer(Contract));



