import {handleActions} from 'redux-actions';

// TYPES
const LOCALIZATIONS_REQUEST = 'LOCALIZATIONS_REQUEST';
const LOCALIZATIONS_RECEIVE = 'LOCALIZATIONS_RECEIVE';
const LOCALIZATIONS_FAILURE = 'LOCALIZATIONS_FAILURE';

// ACTIONS
const requestLocalizations = () => ({type: LOCALIZATIONS_REQUEST});
const receiveLocalizations = (localizations) => ({type: LOCALIZATIONS_RECEIVE, localizations});
const receiveLocalizationsFailure = (error) => ({type: LOCALIZATIONS_FAILURE, error});

// REDUCER
const handleRequestLocalizations = (state, action) => ({...state, isLoading: true});
const handleReceiveLocalizations = (state, action) => ({...state, list: action.localizations, isLoading: false});
const handleReceiveLocalizationsFailure = (state, action) => ({...state, error: action.error, isLoading: false});

const INITIAL_STATE = {
  list: [],
  selected: {},
  isLoading: false
};

export default handleActions({
  [LOCALIZATIONS_REQUEST]: handleRequestLocalizations,
  [LOCALIZATIONS_RECEIVE]: handleReceiveLocalizations,
  [LOCALIZATIONS_FAILURE]: handleReceiveLocalizationsFailure
}, INITIAL_STATE);

export const types = {
  LOCALIZATIONS_REQUEST,
  LOCALIZATIONS_RECEIVE,
  LOCALIZATIONS_FAILURE
};

export const actions = {
  requestLocalizations,
  receiveLocalizations,
  receiveLocalizationsFailure
};



