import moment from 'moment';
import constants from 'lib/constants';

export default class ContractClass {
  constructor (contractData, customer = null) {
    this.customer = customer;
    Object.assign(this, contractData);
  }

  get isStartDateInTheFuture () {
    return isDateInTheFuture(this.dateStart);
  }

  get isActive () {
    return !!this.currentActiveContractId;
  }

  get isTerminationDateInTheFuture () {
    return isDateInTheFuture(this.dateTerminated);
  }

  get isTerminationDateNotSpecified () {
    return this.dateTerminated === constants.noEndDateValue;
  }

  get contractHasFutureVersion () {
    return this.contractVersions.length > 1 && this.contractVersions.filter(c => isDateInTheFuture(c.dateActive)).length > 0;
  }

  get contractHasMultipleVersions () {
    return this.contractVersions.length > 1;
  }
}

const isDateInTheFuture = (date) => {
  return moment(date).diff(moment()) > 1;
};

export const getActiveContractsByDate = (contracts, selectedDate) => {
  return contracts.reduce((memo, c) => {
    let isActiveOnSelectedDate = moment(selectedDate, 'DD-MM-YYYY').isBetween(c.dateStart, c.dateTerminated, null, '[]');

    if (c.isTerminationDateNotSpecified) {
      isActiveOnSelectedDate = moment(selectedDate, 'DD-MM-YYYY').isSameOrAfter(c.dateStart);
    }

    if (isActiveOnSelectedDate && c.currentActiveContractId) {
      memo.push(c);
    }

    return memo;
  }, []);
};



