import constants from 'lib/constants';

export default class OptionIdentifier {
  constructor (idObj) {
    this.id = OptionIdentifier.identifierObjectToIdentifierString(idObj);
  }

  get () {
    return this.id;
  }

  static identifierObjectToIdentifierString (idObj) {
    if (typeof idObj === 'string') return idObj;
    return `${idObj.entity}${constants.optionIdSeparator}${idObj.entitySequenceNumber}${constants.optionIdSeparator}${idObj.subEntity}`;
  }

  static identifierStringToIdentifierObject = (idString) => {
    let idParts = idString.split(constants.optionIdSeparator);
    let idPartsFinal = idParts.splice(0, 2);
    idPartsFinal.push(idParts.join(constants.optionIdSeparator));
    return {
      entity: idPartsFinal[0],
      entitySequenceNumber: parseInt(idPartsFinal[1], 10),
      subEntity: idPartsFinal[2]
    };
  };
}

export const identifierStringToIdentifierObject = OptionIdentifier.identifierStringToIdentifierObject;
export const identifierObjectToIdentifierString = OptionIdentifier.identifierObjectToIdentifierString;



