import dynamicFormTypes from 'lib/dynamicFormTypes';

// TYPES
const MAP_OPTIONS_TO_FORM = 'MAP_OPTIONS_TO_FORM';

// ACTIONS
const mapOptionsToForm = (form, options) => ({type: MAP_OPTIONS_TO_FORM, form, options});

// REDUCER
const formReducer = formType => (state, action) => {
  if (action.form !== formType) return state;
  return {...state, options: action.options};
};

export default Object.values(dynamicFormTypes).reduce((acc, formType) =>
  ({...acc, [formType]: formReducer(formType)}), {});

export const types = {MAP_OPTIONS_TO_FORM};
export const actions = {mapOptionsToForm};



