import React from 'react';
import CfeOptionComponent from 'components/CfeOptionComponent';
import InputWrapper from 'enhancers/InputWrapper';

class IntegerInput extends CfeOptionComponent {
  static displayName = 'IntegerInput';

  constructor (props) {
    super(props, 'components/InputTypes/Integer/index');
  }

  render () {
    const {props: {error, onChange, input, question, helpText, toggleHelpBlock, isHelpBlockOpened, disabled}} = this;
    const inputHtml = <input className='form-control' {...this.props.input} onChange={onChange} disabled={disabled} />;

    const context = {
      ...this,
      error,
      input,
      question,
      helpText,
      toggleHelpBlock,
      isHelpBlockOpened,
      disabled,
      field: inputHtml
    };

    return super.render(context);
  };
}

export default InputWrapper(IntegerInput);



