import Http from 'lib/http';
import Resource from 'lib/resource';
import divApiConfig from 'config/api';

const getToken = () => {
  try {
    return JSON.parse(localStorage['authUser']).access_token;
  } catch (e) {
    return null;
  }
};

export let headers = {
  'Accept': 'application/json',
  'Content-Type': 'application/json',
  'Authorization': 'Bearer ' + getToken()
};

export const setApiKeyHeader = (apiKey) => {
  headers['x-api-key'] = apiKey;
};

export const setAcceptLanguageHeader = (locale) => {
  headers['Accept-Language'] = locale.replace('_', '-');
};

export const refreshAuthorizationHeader = () => {
  headers['Authorization'] = 'Bearer ' + getToken();
};

export default class Api {
  resourceNames = [];

  constructor (apiConfig) {
    this.http = new Http({
      headers
    });

    Object.keys(apiConfig).reduce((memo, resourceName) => {
      this.resourceNames.push(resourceName);
      this[resourceName] = new Resource(apiConfig[resourceName], this.http);
    }, {});
  }
}

export const api = new Api(divApiConfig);



