import {headers} from 'lib/api';
import {each, omit} from 'lodash';
import config from 'lib/config';

export default class File {
  static upload (dispatch, files, documentContext) {
    const filePromises = [];

    each(files, f => {
      let data = new FormData();

      data.append(f.name, f);
      data.append('DocumentContext', documentContext);
      let filePromise = fetch(config.documentStoreApiUrl + '/Upload', {
        headers: omit(headers, 'Content-Type'),
        method: 'post',
        body: data
      })
      .then(res => {
        if (res.ok) return res.json();
        throw res.json();
      })
      .then(guid => ({
        guid: guid,
        fileGuid: f.preview
      }));

      filePromises.push(filePromise);
    });
    return Promise.all(filePromises)
    .catch(err => {
      throw err;
    });
  }

  static getDocumentsMetaData (documentIds) {
    const url = config.documentStoreApiUrl + '/Download/GetMetaDataDocuments';

    return new Promise((resolve, reject) => {
      fetch(url, {
        headers,
        method: 'post',
        body: JSON.stringify(documentIds)
      })
      .then(response => resolve(response.json()));
    });
  };

  static openDocument (document) {
    const url = `${config.documentStoreApiUrl}/Download/${document.id}/Content`;
    this.getAndOpenDocument(url, document.type);
  };

  static getAndOpenDocument (url, type) {
    const newWindow = window.open();

    const xhr = new XMLHttpRequest();
    xhr.open('GET', url, true);
    xhr.setRequestHeader('x-api-key', headers['x-api-key']);
    xhr.setRequestHeader('Authorization', headers.Authorization);
    xhr.responseType = 'arraybuffer';

    xhr.onload = e => {
      const blob = new Blob([xhr.response], {type: type});
      newWindow.location = window.URL.createObjectURL(blob);
    };

    xhr.send();
  };
}




