import React, {PropTypes} from 'react';
import ProductView from 'views/ProductView/ProductView';
import {connect} from 'react-redux';
import {actions as flowActions} from 'redux/modules/flow';

export class DossierCalculationView extends React.Component {
  componentWillMount () {
    this.props.requestStartCalculationFlow(this.props.calculationToLoadById);
  }

  render () {
    return (
      <ProductView />
    );
  }
}

DossierCalculationView.propTypes = {
  calculationToLoadById: PropTypes.number,
  requestStartCalculationFlow: PropTypes.func
};

const mapDispatchToProps = (dispatch) => ({
  requestStartCalculationFlow: (calculationToLoadById) => dispatch(flowActions.requestStartCalculationFlow(calculationToLoadById))
});

export default connect(null, mapDispatchToProps)(DossierCalculationView);



