import Config from './config';
import File from 'lib/file';

export const getAndOpenPdfDocumentInNewTab = (customerId, documentId) => {
  const url = `${Config.divWebApiUrl}/Customers/${customerId}/Documents/${documentId}/Content`;
  getAndOpenPdf(url);
};

export const getAndOpenEmailAttachmentPdfInNewTab = (customerId, emailId, attachmentId) => {
  const url = `${Config.divWebApiUrl}/Customers/${customerId}/Emails/${emailId}/Attachments/${attachmentId}/Content`;
  getAndOpenPdf(url);
};

const getAndOpenPdf = (url) => {
  File.getAndOpenDocument(url, 'application/pdf');
};



