import {handleActions} from 'redux-actions';
import {REHYDRATE} from 'redux-persist/constants';

// TYPES
const CALCULATION_SAVE_REQUEST = 'CALCULATION_SAVE_REQUEST';
const CALCULATION_SAVE_RECEIVE = 'CALCULATION_SAVE_RECEIVE';

const CALCULATION_DELETE_REQUEST = 'CALCULATION_DELETE_REQUEST';

const CALCULATION_FAILURE = 'CALCULATION_FAILURE';

const CALCULATION_REQUEST = 'CALCULATION_REQUEST';
const CALCULATION_RECEIVE = 'CALCULATION_RECEIVE';

const CALCULATIONS_REQUEST = 'CALCULATIONS_REQUEST';
const CALCULATIONS_RECEIVE = 'CALCULATIONS_RECEIVE';
const CALCULATIONS_FAILURE = 'CALCULATIONS_FAILURE';

const SET_CALCULATION_TO_LOAD_BY_ID = 'SET_CALCULATION_TO_LOAD_BY_ID';
const CALCULATION_SAVE_RESET = 'CALCULATION_SAVE_RESET';

export const types = {
  CALCULATION_SAVE_REQUEST,
  CALCULATION_SAVE_RECEIVE,
  CALCULATION_DELETE_REQUEST,
  CALCULATION_FAILURE,
  CALCULATION_REQUEST,
  CALCULATION_RECEIVE,
  CALCULATIONS_REQUEST,
  CALCULATIONS_RECEIVE,
  CALCULATIONS_FAILURE,
  SET_CALCULATION_TO_LOAD_BY_ID,
  CALCULATION_SAVE_RESET
};

// ACTIONS
const requestSaveCalculation = (product) => ({type: CALCULATION_SAVE_REQUEST, product});
const receiveSaveCalculation = (calculation) => ({type: CALCULATION_SAVE_RECEIVE, calculation});
const receiveSaveCalculationFailure = (error) => ({type: CALCULATION_FAILURE, payload: error}); // uses payload for old action handler in validation reducer
const requestDeleteCalculation = (calculation) => ({type: CALCULATION_DELETE_REQUEST, calculation});
const requestCalculation = (calculationId) => ({type: CALCULATION_REQUEST, calculationId});
const receiveCalculation = (calculation) => ({type: CALCULATION_RECEIVE, calculation});

const requestCalculations = () => ({type: CALCULATIONS_REQUEST});
const receiveCalculations = (calculations) => ({type: CALCULATIONS_RECEIVE, calculations});
const receiveCalculationsFailure = (error) => ({type: CALCULATIONS_FAILURE, error});

const setCalculationToLoadById = (calculationId) => ({type: SET_CALCULATION_TO_LOAD_BY_ID, calculationId});
const resetSavedCalculationId = () => ({type: CALCULATION_SAVE_RESET});

export const actions = {
  requestSaveCalculation,
  receiveSaveCalculation,
  receiveSaveCalculationFailure,
  requestCalculations,
  receiveCalculations,
  receiveCalculationsFailure,
  requestDeleteCalculation,
  requestCalculation,
  receiveCalculation,
  setCalculationToLoadById,
  resetSavedCalculationId
};

// REDUCER
const handleRequestCalculations = (state, action) => ({...state, isListLoading: true});
const handleReceiveCalculations = (state, action) => ({...state, list: action.calculations, isListLoading: false});
const handleReceiveCalculationsFailure = (state, action) => ({...state, error: action.error});

const handleRequestSaveCalculation = (state, action) => ({...state, isSavingCalculation: true});
const handleReceiveSaveCalculation = (state, action) => ({...state, id: action.calculation.calculationId, isSavingCalculation: false});
const failure = (state, action) => ({...state, fetching: false, isSavingCalculation: false});
const handleRequestCalculation = (state, action) => ({...state, isListLoading: true});
const handleReceiveCalculation = (state, action) => ({...state, details: action.calculation});

const handleSetCalculationToLoadById = (state, action) => ({...state, calculationToLoadById: action.calculationId});
const handleRehydrate = (state, action) => {
  if (action.payload.calculation && action.payload.calculation.calculationToLoadById) {
    return {...state, calculationToLoadById: action.payload.calculation.calculationToLoadById};
  }

  return state;
};

const handleReSetSavedCalculationId = (state, action) => ({...state, id: null});

const INITIAL_STATE = {
  fetching: null,
  isSavingCalculation: false
};

export default handleActions({
  [CALCULATION_SAVE_REQUEST]: handleRequestSaveCalculation,
  [CALCULATION_SAVE_RECEIVE]: handleReceiveSaveCalculation,
  [CALCULATION_FAILURE]: failure,
  [CALCULATIONS_REQUEST]: handleRequestCalculations,
  [CALCULATIONS_RECEIVE]: handleReceiveCalculations,
  [CALCULATIONS_FAILURE]: handleReceiveCalculationsFailure,
  [CALCULATION_REQUEST]: handleRequestCalculation,
  [CALCULATION_RECEIVE]: handleReceiveCalculation,
  [SET_CALCULATION_TO_LOAD_BY_ID]: handleSetCalculationToLoadById,
  [CALCULATION_SAVE_RESET]: handleReSetSavedCalculationId,
  [REHYDRATE]: handleRehydrate
}, INITIAL_STATE);



