
		module.exports = function(context) {
			return (function() {
				

				if (!React) {
						var React = require("react");
					}

				return (
					<div>
  <table className='table table-hover'>
    <thead>
    <tr>
      <th><this.FormattedMessage {...this.messages.subject}/></th>
      <th><this.FormattedMessage {...this.messages.dateCreated}/></th>
      <th> </th>
    </tr>
    </thead>
    <tbody>
    {this.emails.length > 0 && this.emails.map((email, i) =>
      <tr key={i} onClick={() => this.props.handleClickOpenEmail(email)}>
        <td>{email.subject}</td>
        <td>{this.util.formatDate(email.dateCreated)}</td>
        <td>
          {email.hasAttachment && <span className='glyphicon glyphicon-paperclip'/>}
        </td>
      </tr>)}
    </tbody>
  </table>
</div>

				);
			}).apply(context);
		};
	


