import React, {PropTypes} from 'react';
import RootContainer from 'containers/RootContainer';
import ContractDetails from 'components/Contract/details';
import {connect} from 'react-redux';

export class DossierContractDetailsContainer extends React.Component {
  render () {
    return (
      <RootContainer {...this.props}>
        {this.props.contractToLoad && <ContractDetails contractToLoad={this.props.contractToLoad} />}
      </RootContainer>
    );
  }
}

DossierContractDetailsContainer.propTypes = {
  contractToLoad: PropTypes.object
};

const mapStateToProps = (state) => ({contractToLoad: state.contract.contractToLoad});

export default connect(mapStateToProps)(DossierContractDetailsContainer);




