
		module.exports = function(context) {
			return (function() {
				

				if (!React) {
						var React = require("react");
					}

				return (
					<div className='form-group'>
  <input
    {...attrs}
    ref={(c) => this.$dateInput = jQuery(c)}
    type='text'
    value={value}
    name={name}/>
</div>

				);
			}).apply(context);
		};
	


