import CfeComponent from 'components/CfeComponent';
import {connect} from 'react-redux';
import {PropTypes} from 'react';
import PaymentMethod from 'components/PaymentMethod';
import Loader from 'components/Loader';
import {head} from 'lodash';
import {defineMessages, FormattedMessage} from 'react-intl';
import dynamicFormTypes from 'lib/dynamicFormTypes';

class PaymentMethodCreator extends CfeComponent {
  static displayName = 'PaymentMethodCreator';

  static propTypes = {
    paymentMethodTemplates: PropTypes.array,
    handleSubmit: PropTypes.func,
    isSavingPaymentMethod: PropTypes.bool
  };

  constructor (props) {
    super(props, 'components/PaymentMethod/PaymentMethodCreator');
    this.state = {
      selectedPaymentMethodTemplate: head(props.paymentMethodTemplates)
    };
  }

  handleChange = (e) => {
    this.setState({
      selectedPaymentMethodTemplate: this.props.paymentMethodTemplates.filter(t => t.type.id === e.target.value)[0]
    });
  };

  render () {
    const {props: {paymentMethodTemplates}} = this;

    const selectedPaymentMethodTemplate = this.state.selectedPaymentMethodTemplate
      ? this.state.selectedPaymentMethodTemplate
      : head(paymentMethodTemplates);

    const context = {
      ...this,
      messages,
      Loader,
      PaymentMethod,
      FormattedMessage,
      paymentMethodTemplates,
      selectedPaymentMethodTemplate,
      dynamicFormTypes
    };
    return super.render(context);
  }
}

const mapStateToProps = (state) => ({
  isSavingPaymentMethod: state.customer.isSavingPaymentMethod
});

export default connect(mapStateToProps)(PaymentMethodCreator);

const messages = defineMessages({
  paymentMethodTypes: {
    id: 'paymentMethod.paymentMethodTypes',
    description: 'Label shown on type selector',
    defaultMessage: 'Payment method types'
  }
});



