import {handleActions} from 'redux-actions';

// TYPES
const REQUEST_INCIDENTS = 'REQUEST_INCIDENTS';
const RECEIVE_INCIDENTS = 'RECEIVE_INCIDENTS';
const RECEIVE_INCIDENTS_FAILURE = 'RECEIVE_INCIDENTS_FAILURE';
const REQUEST_INCIDENT_DOCUMENTS = 'REQUEST_INCIDENT_DOCUMENTS';
const RECEIVE_INCIDENT_DOCUMENTS = 'RECEIVE_INCIDENT_DOCUMENTS';
const RECEIVE_INCIDENT_DOCUMENTS_FAILURE = 'RECEIVE_INCIDENT_DOCUMENTS_FAILURE';

// ACTIONS
const requestIncidents = () => ({type: REQUEST_INCIDENTS});
const receiveIncidents = (incidents) => ({type: RECEIVE_INCIDENTS, incidents});
const receiveIncidentsFailure = (error) => ({type: RECEIVE_INCIDENTS_FAILURE, error});
const requestIncidentDocuments = (documentIds) => ({type: REQUEST_INCIDENT_DOCUMENTS, documentIds});
const receiveIncidentDocuments = (documents) => ({type: RECEIVE_INCIDENT_DOCUMENTS, documents});
const receiveIncidentDocumentsFailure = (error) => ({type: RECEIVE_INCIDENT_DOCUMENTS_FAILURE, error});

export const actions = {
  requestIncidents,
  receiveIncidents,
  receiveIncidentsFailure,
  requestIncidentDocuments,
  receiveIncidentDocuments,
  receiveIncidentDocumentsFailure
};

// REDUCER
const handleRequestIncidents = (state, action) => ({...state, isListLoading: true});
const handleReceiveIncidents = (state, action) => ({...state, list: action.incidents, isListLoading: false});
const handleReceiveIncidentsFailure = (state, action) => ({...state, error: action.error, isListLoading: false});
const handleRequestIncidentDocuments = (state, action) => ({...state, isDocumentListLoading: true});
const handleReceiveIncidentDocuments = (state, action) => ({...state, documents: action.documents, isDocumentListLoading: false});
const handleReceiveIncidentDocumentsFailure = (state, action) => ({...state, error: action.error, isDocumentListLoading: false});

const INITIAL_STATE = {
  list: [],
  documents: [],
  isDocumentListLoading: false
};

export default handleActions({
  [REQUEST_INCIDENTS]: handleRequestIncidents,
  [RECEIVE_INCIDENTS]: handleReceiveIncidents,
  [RECEIVE_INCIDENTS_FAILURE]: handleReceiveIncidentsFailure,
  [REQUEST_INCIDENT_DOCUMENTS]: handleRequestIncidentDocuments,
  [RECEIVE_INCIDENT_DOCUMENTS]: handleReceiveIncidentDocuments,
  [RECEIVE_INCIDENT_DOCUMENTS_FAILURE]: handleReceiveIncidentDocumentsFailure
}, INITIAL_STATE);

export const types = {
  REQUEST_INCIDENTS,
  RECEIVE_INCIDENTS,
  RECEIVE_INCIDENTS_FAILURE,
  REQUEST_INCIDENT_DOCUMENTS,
  RECEIVE_INCIDENT_DOCUMENTS,
  RECEIVE_INCIDENT_DOCUMENTS_FAILURE
};



