
		module.exports = function(context) {
			return (function() {
				

				if (!React) {
						var React = require("react");
					}

				return (
					<table className='table'>
  <thead>
  <tr>
    <th><this.FormattedMessage {...this.messages.contractVersionNumber} /></th>
    <th><this.FormattedMessage {...this.messages.contractActivationDate} /></th>
    <th><this.FormattedMessage {...this.messages.contractDateInactive} /></th>
  </tr>
  </thead>
  <tbody>
  {this.props.contractVersions && this.props.contractVersions.map((contract) => {
    return <tr key={contract.contractId}>
      <td>{contract.contractId}</td>
      <td>{this.util.formatDate(contract.dateActive)}</td>
      <td>{contract.dateInactive === this.constants.noEndDateValue ? null : this.util.formatDate(contract.dateTerminated)}</td>
    </tr>;
  })}
  </tbody>
</table>

				);
			}).apply(context);
		};
	


