import {createAction, handleActions} from 'redux-actions';
import Auth from 'lib/auth';
import userStorage from 'lib/userStorage';

// TYPES
const LOGIN_REQUEST = 'LOGIN_REQUEST';
const LOGIN_RECEIVE = 'LOGIN_RECEIVE';
const LOGIN_RECEIVE_FAILURE = 'LOGIN_RECEIVE_FAILURE';
const EXTERNAL_LOGIN_REQUEST = 'EXTERNAL_LOGIN_REQUEST';
const EXTERNAL_LOGIN_RECEIVE = 'EXTERNAL_LOGIN_RECEIVE';
const EXTERNAL_LOGIN_RECEIVE_FAILURE = 'EXTERNAL_LOGIN_RECEIVE_FAILURE';
const SET_USER = 'SET_USER';
const LOGOUT = 'LOGOUT';
const GET_SETTINGS = 'GET_SETTINGS';
const REGISTER_REQUEST = 'REGISTER_REQUEST';
const REGISTER_RECEIVE = 'REGISTER_RECEIVE';
const REGISTER_FAILURE = 'REGISTER_FAILURE';

// ACTIONS
const requestLogin = (credentials) => ({type: LOGIN_REQUEST, credentials});
const receiveLogin = () => ({type: LOGIN_RECEIVE});
const receiveLoginFailure = (error) => ({type: LOGIN_RECEIVE_FAILURE, error});
const setUser = (user) => ({type: SET_USER, user});
const logout = (accessToken) => ({type: LOGOUT, accessToken});
const requestRegister = (credentials) => ({type: REGISTER_REQUEST, credentials});
const receiveRegister = () => ({type: REGISTER_RECEIVE});
const receiveRegisterFailure = (error) => ({type: REGISTER_FAILURE, error});
const requestExternalLogin = (credentials) => ({type: EXTERNAL_LOGIN_REQUEST, credentials});
const receiveExternalLogin = () => ({type: EXTERNAL_LOGIN_RECEIVE});
const receiveExternalLoginFailure = (error) => ({type: EXTERNAL_LOGIN_RECEIVE_FAILURE, error});

const getStsSettings = createAction(GET_SETTINGS, async () => {
  return await Auth.getStsSettings();
});

// REDUCER
const handleSetUser = (state, action) => ({...state, user: action.user});
const handleRequestRegister = (state, action) => ({...state, isRegisterLoading: true, registerError: null});
const handleReceiveRegister = (state, action) => ({...state, isRegisterLoading: false, registerError: null});
const handleReceiveRegisterFailure = (state, action) => ({
  ...state,
  isRegisterLoading: false,
  registerError: action.error
});

const handleRequestLogin = (state, action) => ({...state, isLoginLoading: true, loginError: null});
const handleReceiveLogin = (state, action) => ({...state, isLoginLoading: false, loginError: null});
const handleReceiveLoginFailure = (state, action) => ({...state, isLoginLoading: false, loginError: action.error});
const handleRequestExternalLogin = (state, action) => ({...state, isLoginLoading: true, loginError: null});
const handleReceiveExternalLogin = (state, action) => ({...state, isLoginLoading: false, loginError: null, user: action.user});
const handleReceiveExternalLoginFailure = (state, action) => ({...state, isLoginLoading: false, loginError: action.error});

const INITIAL_STATE = {
  user: userStorage.getStoredUser(),
  isLoginLoading: false,
  isRegisterLoading: false
};

export default handleActions({
  [SET_USER]: handleSetUser,
  [GET_SETTINGS]: (state, {payload}) => ({...state, stsSettings: payload}),
  [LOGIN_REQUEST]: handleRequestLogin,
  [LOGIN_RECEIVE]: handleReceiveLogin,
  [LOGIN_RECEIVE_FAILURE]: handleReceiveLoginFailure,
  [EXTERNAL_LOGIN_REQUEST]: handleRequestExternalLogin,
  [EXTERNAL_LOGIN_RECEIVE]: handleReceiveExternalLogin,
  [EXTERNAL_LOGIN_RECEIVE_FAILURE]: handleReceiveExternalLoginFailure,
  [REGISTER_REQUEST]: handleRequestRegister,
  [REGISTER_RECEIVE]: handleReceiveRegister,
  [REGISTER_FAILURE]: handleReceiveRegisterFailure
}, INITIAL_STATE);

export const types = {
  LOGIN_REQUEST,
  LOGIN_RECEIVE,
  LOGIN_RECEIVE_FAILURE,
  SET_USER,
  LOGOUT,
  REGISTER_REQUEST,
  REGISTER_RECEIVE,
  REGISTER_FAILURE,
  EXTERNAL_LOGIN_REQUEST,
  EXTERNAL_LOGIN_RECEIVE,
  EXTERNAL_LOGIN_RECEIVE_FAILURE
};

export const actions = {
  setUser,
  requestExternalLogin,
  receiveExternalLogin,
  receiveExternalLoginFailure,
  getStsSettings,
  logout,
  requestRegister,
  receiveRegister,
  receiveRegisterFailure,
  requestLogin,
  receiveLogin,
  receiveLoginFailure
};



