
		module.exports = function(context) {
			return (function() {
				

				if (!React) {
						var React = require("react");
					}

				return (
					<div>
  <this.EmailsList
    emails={this.props.emails}
    handleClickOpenEmail={this.handleClickOpenEmail}
  />
  <this.Modal
    title={this.state.activeEmail.subject}
    open={this.state.isConfirmationModalOpen}
    closeCallback={this.modalClosedCallback}
    id='email-body-modal'>
    <div className='modal-body'>
      <ul>
        {this.props.emailBodyAttachmentFiles.attachmentFiles
        && this.props.emailBodyAttachmentFiles.attachmentFiles.length > 0
        && this.props.emailBodyAttachmentFiles.attachmentFiles.map((attachmentFile, i) =>
          <li key={i}><a onClick={() => this.handleClickOpenAttachment(attachmentFile)}>{attachmentFile.fileName}</a></li>
        )}
      </ul>
      <div dangerouslySetInnerHTML={{__html: this.props.emailBodyAttachmentFiles.emailBody}}/>
    </div>
  </this.Modal>
</div>
				);
			}).apply(context);
		};
	


