import React, {PropTypes} from 'react';
import RootContainer from 'containers/RootContainer';
import ContractEditView from 'views/DossierContractEditView/DossierContractEditView';
import {connect} from 'react-redux';

export class DossierContractEditContainer extends React.Component {
  render () {
    return (
      <RootContainer {...this.props}>
        {this.props.contractToLoad && <ContractEditView contractToLoad={this.props.contractToLoad}/>}
      </RootContainer>
    );
  }
}

DossierContractEditContainer.propTypes = {
  contractToLoad: PropTypes.object
};

const mapStateToProps = (state) => ({contractToLoad: state.contract.contractToLoad});

export default connect(mapStateToProps)(DossierContractEditContainer);




