import {PropTypes} from 'react';
import {defineMessages, FormattedMessage, injectIntl} from 'react-intl';
import {reset} from 'redux-form';
import {connect} from 'react-redux';
import Modal from 'components/Modal';
import PaymentMethod from 'components/PaymentMethod';
import PaymentMethodCreator from 'components/PaymentMethod/PaymentMethodCreator';
import CfeComponent from 'components/CfeComponent';
import dynamicFormTypes from 'lib/dynamicFormTypes';

class PaymentMethodList extends CfeComponent {
  static displayName = 'PaymentMethodList';

  static propTypes = {
    customer: PropTypes.object,
    dispatch: PropTypes.func.isRequired,
    handleEditPaymentMethod: PropTypes.func.isRequired,
    handleCreatePaymentMethod: PropTypes.func.isRequired,
    paymentMethodTemplates: PropTypes.array.isRequired,
    intl: PropTypes.object.isRequired,
    isLoadingPaymentMethod: PropTypes.bool.isRequired,
    customerPaymentMethods: PropTypes.array
  };

  componentWillReceiveProps (nextProps) {
    if (this.props.isLoadingPaymentMethod && !nextProps.isLoadingPaymentMethod) {
      this.closeModals();
    }
  }

  constructor (props) {
    super(props, 'components/PaymentMethod/list');
    this.state = {
      activePaymentMethod: {type: {}},
      isDetailsModalOpen: false,
      isAddPaymentMethodModalOpen: false
    };
  }

  handleClickRow = (paymentMethod) => {
    this.setState({
      activePaymentMethod: paymentMethod,
      isDetailsModalOpen: true
    });
  };

  handleAddPaymentMethod = () => {
    this.setState({
      isAddPaymentMethodModalOpen: true
    });
  };

  handleEditPaymentMethod = (input) => {
    const {state: {activePaymentMethod}} = this;
    const {props: {handleEditPaymentMethod}} = this;

    return handleEditPaymentMethod(input, activePaymentMethod);
  };

  handleCreatePaymentMethod = (...args) => {
    this.props.handleCreatePaymentMethod(...args);
  };

  render () {
    const context = {
      ...this,
      FormattedMessage,
      messages,
      Modal,
      PaymentMethod,
      PaymentMethodCreator
    };

    return super.render(context);
  }

  closeModals = () => {
    const {props: {dispatch}} = this;

    this.setState({isDetailsModalOpen: false, isAddPaymentMethodModalOpen: false});
    if (dispatch) {
      dispatch(reset(dynamicFormTypes.paymentMethod));
    }
  };
}

export default connect()(injectIntl(PaymentMethodList));

const messages = defineMessages({
  name: {
    id: 'paymentMethod.name',
    description: 'Table column',
    defaultMessage: 'Name'
  },
  number: {
    id: 'paymentMethod.accountNumber',
    description: 'Table column',
    defaultMessage: 'Account number'
  },
  addPaymentMethod: {
    id: 'paymentMethod.addPaymentMethod',
    description: 'Button label',
    defaultMessage: 'Add payment method'
  },
  addPaymentMethodModalTitle: {
    id: 'paymentMethod.addPaymentMethodModalTitle',
    description: 'Modal title label',
    defaultMessage: 'Add payment method'
  }
});



