import {PropTypes} from 'react';
import CfeComponent from 'components/CfeComponent';
import {Field} from 'redux-form';
import {defineMessages, FormattedMessage, injectIntl} from 'react-intl';
import {connect} from 'react-redux';
import c from 'classnames';
import Loader from 'components/Loader';
import dynamicFormTypes from 'lib/dynamicFormTypes';
import {actions as authActions} from 'redux/modules/auth';
import formEnhancer from 'enhancers/formEnhancer';
import LoadingButton from 'components/buttons/LoadingButton';

const rules = {
  email: {
    presence: true,
    email: true
  },
  password: {
    presence: true
  },
  passwordConfirm: {
    presence: true,
    equality: 'password'
  }
};

class RegisterForm extends CfeComponent {
  static displayName = 'RegisterForm';

  constructor (props) {
    super(props, 'forms/RegisterForm/index');
  }

  handleSubmit = (input) => this.props.requestRegister(input);

  render () {
    return super.render({...this, c, Loader, Field, FormattedMessage, messages, LoadingButton});
  }
}

RegisterForm.propTypes = {
  requestRegister: PropTypes.func,
  registerError: PropTypes.string
};

const mapDispatchToProps = (dispatch) => ({
  requestRegister: (credentials) => dispatch(authActions.requestRegister(credentials))
});

const mapStateToProps = (state) => ({
  isRegisterLoading: state.auth.isRegisterLoading,
  registerError: state.auth.registerError,
  user: state.auth.user
});

const messages = defineMessages({
  emailAddress: {
    id: 'registerForm.emailAddress',
    description: 'email address field description',
    defaultMessage: 'Email Address'
  },
  password: {
    id: 'registerForm.password',
    description: 'password field description',
    defaultMessage: 'Password'
  },
  passwordConfirm: {
    id: 'registerForm.passwordConfirm',
    description: 'password repeat field description',
    defaultMessage: 'Confirm password'
  },
  submit: {
    id: 'registerForm.submitButton',
    description: 'button text',
    defaultMessage: 'Register account'
  }
});

export default connect(mapStateToProps, mapDispatchToProps)(injectIntl(formEnhancer(RegisterForm, {
  form: dynamicFormTypes.register,
  rules
})));



