import React, {PropTypes} from 'react';
import {reduxForm} from 'redux-form';
import Input from '../components/InputTypes/Input';
import {isEqual, values} from 'lodash';
import {defineMessages} from 'react-intl';
import LoadingButton from 'components/buttons/LoadingButton';

export class DynamicOptionsForm extends React.PureComponent {
  // submit when form is valid
  componentWillReceiveProps (nextProps) {
    const {props: {questionsToRender, answers, isQuestionRelevant, shouldSubmitWhenValid}} = this;

    if (shouldSubmitWhenValid && !isEqual(answers, nextProps.answers)) {
      const input = questionsToRender
        .filter(q => isQuestionRelevant(q))
        .reduce((memo, q) => ({...memo, [q.id]: nextProps.answers[q.id]}), {});

      if (this.isRelevantInputValid(values(input))) {
        return this.props.onSubmit();
      }
    }
  }

  // don't do anything when a null value is in input (make your choice option in dropdown)
  isRelevantInputValid (input) {
    return !input.some(i => i === 'null' || i === undefined);
  }

  render () {
    return (
      <form>
        {this.renderInputs()}
        {!this.props.readOnly &&
        <div>
          {!this.props.hideSubmitButton &&
          <LoadingButton
            className='btn btn-primary'
            onClick={this.props.handleSubmit}
            message={messages.submitButtonLabel}
            isLoading={this.props.isLoading}
            disabled={!this.props.valid}>
            <i className='glyphicon glyphicon-send'/>
          </LoadingButton>}
        </div>}
      </form>
    );
  }

  renderInputs = () => {
    const {props: {questionsToRender, readOnly, isQuestionRelevant}} = this;

    return questionsToRender.reduce((acc, q) =>
        [...acc, isQuestionRelevant(q) &&
        <Input key={q.id}
               name={q.id}
               disabled={readOnly || q.isReadonly}
               question={q}/>],
      []);
  };
}

DynamicOptionsForm.propTypes = {
  questionsToRender: PropTypes.array.isRequired,
  handleSubmit: PropTypes.func,
  answers: PropTypes.object,
  hideSubmitButton: PropTypes.bool,
  valid: PropTypes.bool,
  onSubmit: PropTypes.func,
  isQuestionRelevant: PropTypes.func,
  readOnly: PropTypes.bool,
  shouldSubmitWhenValid: PropTypes.bool,
  isLoading: PropTypes.bool
};

const messages = defineMessages({
  submitButtonLabel: {
    id: 'dynamicForm.submitButtonLabel',
    description: 'Label shown on submit button',
    defaultMessage: 'Submit'
  }
});

export default reduxForm()(DynamicOptionsForm);



