import React, {PropTypes} from 'react';
import RootContainer from 'containers/RootContainer';
import {defineMessages} from 'react-intl';
import {connect} from 'react-redux';
import {actions as calculationActions} from 'redux/modules/calculation';
import {isValid} from 'redux-form';
import CalculationClass from 'lib/calculation';
import userStorage from 'lib/userStorage';
import LoadingButton from 'components/buttons/LoadingButton';
import dynamicFormTypes from 'lib/dynamicFormTypes';

export class SaveCalculationContainer extends React.Component {
  static propTypes = {
    store: PropTypes.object,
    locale: PropTypes.string,
    messages: PropTypes.object,
    product: PropTypes.object,
    requestSaveCalculation: PropTypes.func,
    isCalculationFormValid: PropTypes.bool,
    calculation: PropTypes.object,
    calculations: PropTypes.object
  };

  handleSaveCalculation = () => this.props.requestSaveCalculation(this.props.product);

  saveButtonEnabled = () => {
    const {props: {calculation, isCalculationFormValid}} = this;
    return calculation.paymentIntervalPremium && isCalculationFormValid;
  };

  render () {
    const saveButtonEnabled = this.saveButtonEnabled();
    const isLoggedOn = userStorage.isLoggedIn();
    const isLoading = this.props.calculations.isSavingCalculation;

    return (
      <div>
        {isLoggedOn &&
        <RootContainer {...this.props}>
            <LoadingButton className='btn btn-primary' onClick={this.handleSaveCalculation}
                           isLoading={isLoading} disabled={!saveButtonEnabled}
                           message={messages.saveCalculationButtonLabel}/>
        </RootContainer>
        }
      </div>);
  }
}

const messages = defineMessages({
  saveCalculationButtonLabel: {
    id: 'calculation.saveCalculationButtonLabel',
    description: 'Label shown on save calculation button',
    defaultMessage: 'Save calculation'
  }
});

const mapStateToProps = (state) => ({
  product: state.product.selected,
  calculation: new CalculationClass(state.calculation),
  isCalculationFormValid: isValid(dynamicFormTypes.calculation)(state),
  calculations: state.calculations
});

const mapDispatchToProps = (dispatch) => ({
  requestSaveCalculation: (product) => dispatch(calculationActions.requestSaveCalculation(product))
});

export default connect(mapStateToProps, mapDispatchToProps)(SaveCalculationContainer);



