import React from 'react';
import CfeOptionComponent from 'components/CfeOptionComponent';
import InputWrapper from 'enhancers/InputWrapper';

class CurrencyInput extends CfeOptionComponent {
  static displayName = 'CurrencyInput';

  constructor (props) {
    super(props, 'components/InputTypes/Currency/index');
  }

  render () {
    const {props: {error, input, onChange, question, helpText, toggleHelpBlock, isHelpBlockOpened, disabled}} = this;
    const inputHtml = <input className='form-control' {...this.props.input} onChange={onChange} disabled={disabled} />;

    const context = {
      ...this,
      error,
      question,
      helpText,
      toggleHelpBlock,
      isHelpBlockOpened,
      input,
      disabled,
      field: inputHtml
    };

    return super.render(context);
  };
}

export default InputWrapper(CurrencyInput);



