
		module.exports = function(context) {
			return (function() {
				

				if (!React) {
						var React = require("react");
					}

				return (
					<div>
  <button className='btn btn-primary' onClick={this.handleAddPaymentMethod}>
    <this.FormattedMessage {...this.messages.addPaymentMethod} />
  </button>
  <div>
    <table className='table table-hover'>
      <thead>
      <tr>
        <th>
          <this.FormattedMessage {...this.messages.name} />
        </th>
        <th>
          <this.FormattedMessage {...this.messages.number} />
        </th>
        <th> </th>
      </tr>
      </thead>
      <tbody>
      { this.props.customerPaymentMethods && this.props.customerPaymentMethods.length > 0 && this.props.customerPaymentMethods.map((paymentMethod, i) =>
        <tr key={i}>
          <td>{paymentMethod.type.name}</td>
          <td>{paymentMethod.options[0] ? paymentMethod.options[0].value : ''}</td>
          <td><span onClick={() => this.handleClickRow(paymentMethod)} className='glyphicon glyphicon-pencil'></span></td>
        </tr>)
      }
      </tbody>
    </table>
    <this.Modal
      title={this.state.activePaymentMethod.type.name}
      open={this.state.isDetailsModalOpen}
      closeCallback={this.closeModals}
      id='create-payment-detail-modal'>
      <div className='modal-body'>
        <this.PaymentMethod {...this.state.activePaymentMethod}
          handleSubmit={this.handleEditPaymentMethod}
          enableReinitialize
          formName='payment-method-edit' />
      </div>
    </this.Modal>
    <this.Modal
      title={this.props.intl.formatMessage(this.messages.addPaymentMethodModalTitle)}
      open={this.state.isAddPaymentMethodModalOpen}
      closeCallback={this.closeModals}
      id='add-payment-method-modal'>
      <div className='modal-body'>
        <this.PaymentMethodCreator
          paymentMethodTemplates={this.props.paymentMethodTemplates}
          handleSubmit={this.handleCreatePaymentMethod} />
      </div>
    </this.Modal>
  </div>
</div>

				);
			}).apply(context);
		};
	


