import React, {PropTypes} from 'react';
import RootContainer from 'containers/RootContainer';
import CalculationView from 'views/DossierCalculationView/DossierCalculationView';
import {connect} from 'react-redux';

export class DossierCalculationContainer extends React.Component {
  render () {
    return (
      <RootContainer {...this.props}>
        {this.props.calculationToLoadById && <CalculationView calculationToLoadById={this.props.calculationToLoadById}/>}
      </RootContainer>
    );
  }
}

DossierCalculationContainer.propTypes = {
  calculationToLoadById: PropTypes.number
};

const mapStateToProps = (state) => ({calculationToLoadById: state.calculations.calculationToLoadById});

export default connect(mapStateToProps)(DossierCalculationContainer);




