import CfeComponent from 'components/CfeComponent';
import OptionId from 'lib/optionIdentifier';

export default class CfeOptionComponent extends CfeComponent {
  constructor (props, componentPath) {
    super(props, componentPath);
  }

  componentWillReceiveProps (newProps) {
    if (newProps && newProps.question) {
      const idAsString = OptionId.identifierObjectToIdentifierString(newProps.question.id);

      if (window.cfe && window.cfe.options && window.cfe.options[idAsString]) {
        this.templateOverride = window.cfe.options[idAsString];
      }
    }

    super.componentWillReceiveProps(newProps);
  }
};



