import React, {PropTypes} from 'react';
import RootContainer from 'containers/RootContainer';
import DossierContractsView from 'views/DossierContractsView/DossierContractsView';

export default class DossierContractsContainer extends React.Component {
  static propTypes = {
    store: PropTypes.object.isRequired,
    locale: PropTypes.string.isRequired,
    messages: PropTypes.object.isRequired
  };

  render () {
    return (
      <RootContainer {...this.props}>
        <DossierContractsView />
      </RootContainer>
    );
  }
}



