import React, {PropTypes} from 'react';
import {connect} from 'react-redux';
import Validator from 'lib/validator';
import {reduxForm, getFormSyncErrors} from 'redux-form';
import {mapValues} from 'lodash';

export default (ComposedComponent, {form, rules}) => {
  class WrappedForm extends React.PureComponent {
    getFields = () => Object.keys(rules).reduce((acc, fieldName) => ({
      ...acc,
      [fieldName]: {error: this.getFieldError(fieldName)}
    }), {});

    getFieldError = (fieldName) =>
      this.props.errors
        ? (this.props.fields && this.props.fields[fieldName] && this.props.fields[fieldName].touched) && this.props.errors[fieldName]
        : null;

    render = () => <ComposedComponent {...this.props} fields={this.getFields()}/>;
  }

  WrappedForm.propTypes = {
    errors: PropTypes.object,
    fields: PropTypes.object
  };

  const mapStateToProps = (state) => ({
    errors: getFormSyncErrors(form)(state),
    fields: state.form[form] ? state.form[form].fields || {} : null
  });

  const validate = (values) => mapValues(Validator.check(rules, values), e => e[0]);
  const ReduxFormComponent = reduxForm({form, validate})(WrappedForm);
  return connect(mapStateToProps)(ReduxFormComponent);
};



