import React, {PropTypes} from 'react';
import {map, find} from 'lodash';
import util from 'lib/util';
import {defineMessages, injectIntl} from 'react-intl';
import CfeOptionComponent from 'components/CfeOptionComponent';
import InputWrapper from 'enhancers/InputWrapper';

class SelectionInput extends CfeOptionComponent {
  static displayName = 'SelectionInput';

  static propTypes = {
    question: PropTypes.object.isRequired,
    intl: PropTypes.any,
    readOnly: PropTypes.bool,
    handleFocusInputWrapper: PropTypes.func,
    handleBlurInputWrapper: PropTypes.func
  };

  constructor (props) {
    super(props, 'components/InputTypes/Selection/index');
  }

  prependMakeAChoiceOption (items) {
    const {props: {readOnly, intl: {formatMessage}, question}} = this;

    if (!readOnly && !question.isReadonly && !find(items, {key: 'null'})) {
      items.unshift({key: 'null', value: formatMessage(messages.makeChoice)});
    }

    return items;
  }

  getErrorMessage () {
    const {props: {validationMessage, disabled, meta: {touched, error}}} = this;
    let errorMessage = null;

    if (validationMessage) {
      // server side error
      errorMessage = validationMessage;
    } else if (error && !disabled) {
      // client side error
      errorMessage = touched && error;
    }

    return errorMessage;
  }

  _onChange = (e) => {
    this.props.input.onChange(e);
  };

  render () {
    const {props: {input, question, helpText, toggleHelpBlock, isHelpBlockOpened}} = this;
    let items = this.prependMakeAChoiceOption(question.items);
    let options = map(items, (i) => <option key={i.key + question.id}
                                            value={i.key}>{util.unescapeHtml(i.value)}</option>);
    return super.render({
      ...this,
      options,
      input,
      error: this.getErrorMessage(),
      question,
      helpText,
      onChange: this._onChange,
      toggleHelpBlock,
      isHelpBlockOpened
    });
  }
}

export default InputWrapper(injectIntl(SelectionInput));

const messages = defineMessages({
  makeChoice: {
    id: 'dropdown.makeChoice',
    description: 'First option in dropdown',
    defaultMessage: 'Make a choice'
  }
});



