import {takeLatest} from 'redux-saga';
import {put} from 'redux-saga/effects';
import {api} from 'lib/api';
import {types, actions as localizationActions} from 'redux/modules/localization';
import Logger from 'lib/logger';

function * requestLocalizations () {
  try {
    let localizations = yield api.localizations.get();
    yield put(localizationActions.receiveLocalizations(localizations));
  } catch (error) {
    Logger.error(yield error);
  }
}

export function * localizationEvents () {
  while (true) {
    yield [
      takeLatest(types.LOCALIZATIONS_REQUEST, requestLocalizations)
    ];
  }
}



