
		module.exports = function(context) {
			return (function() {
				

				if (!React) {
						var React = require("react");
					}

				return (
					<div className={this.c('stepper-section', this.isOpen && 'open', this.sectionClass)}>
  <h3 className='section-title' onClick={() => this.onHeaderClick(this.index, this.isVisited)}>
    {this.title}
    {this.hasErrors && <small>{this.errors.map((e, i) => <div key={i}> { e.message } </div>)}</small> }
  </h3>
  <div className='section-content'>
    {this.children}
  </div>
</div>

				);
			}).apply(context);
		};
	


