import {getKeyValuePairs} from 'lib/option';
import {types, actions as calculationActions} from 'redux/modules/calculate';
import {actions as flowActions} from 'redux/modules/flow';
import {actions as validationActions} from 'redux/modules/validation';
import {takeLatest} from 'redux-saga';
import {call, put, select} from 'redux-saga/effects';
import {api} from 'lib/api';
import {getFormValues, isValid, change} from 'redux-form';
import {identifierObjectToIdentifierString} from 'lib/optionIdentifier';
import Logger from 'lib/logger';
import dynamicFormTypes from 'lib/dynamicFormTypes';

const delay = (ms) => new Promise(resolve => setTimeout(resolve, ms));

export function * calculateRequest ({calculateable, calculationContext}) {
  yield call(delay, 200);

  try {
    const [productCalculationAnswers, isCalculationFormValid, activeCustomer] = yield [
      select(state => getFormValues(dynamicFormTypes.calculation)(state)),
      select(state => isValid(dynamicFormTypes.calculation)(state)),
      select(state => state.customer.activeCustomer)
    ];

    let calculationInput = getKeyValuePairs(productCalculationAnswers, calculateable.options);

    if (calculationContext === dynamicFormTypes.calculation && isCalculationFormValid) {
      const calculation = calculateable.contractNr
        ? yield api.contract.recalculate.post({
          customerId: activeCustomer.id,
          contractNr: calculateable.contractNr,
          contractId: calculateable.contractId
        }, calculationInput)
        : yield api.calculate.post({productId: calculateable.productId}, calculationInput);

      if (calculation.newAnswers && calculation.newAnswers.length) {
        const updatedCalculateableOptions = updateCalculateableOptionsWithNewAnswers(calculateable.options, calculation.newAnswers);
        yield put(flowActions.updateCalculateableOptions(updatedCalculateableOptions));

        const optionsToUpdate = getOptionsToUpdate(updatedCalculateableOptions, calculation.newAnswers);
        yield optionsToUpdate.map(o => put(change(dynamicFormTypes.calculation, o.id, o.value)));
      }

      return yield put(calculationActions.receiveCalculate(calculation));
    }
  } catch (error) {
    const e = yield error;
    yield put(validationActions.receiveValidation(dynamicFormTypes.calculation, e));
    Logger.error(e);
  }
}

const getOptionsToUpdate = (options, newAnswers) => {
  const optionsToReset = [];

  newAnswers.forEach(a => {
    const option = options.find(o => o.id === identifierObjectToIdentifierString(a.key));
    if (option) {
      optionsToReset.push(option);
    }
  });

  return optionsToReset;
};

const updateCalculateableOptionsWithNewAnswers = (options, newAnswers) => {
  return options.map(o => {
    const correspondingNewAnswer = newAnswers.find(a => identifierObjectToIdentifierString(a.key) === o.id);
    if (correspondingNewAnswer) {
      o.value = correspondingNewAnswer.value;
      o.format();
    }
    return o;
  });
};

export function * watchCalculateRequest () {
  yield takeLatest(types.CALCULATE_REQUEST, calculateRequest);
}



