import React, {PropTypes} from 'react';
import RootContainer from 'containers/RootContainer';
import DossierCalculationsView from 'views/DossierCalculationsView/DossierCalculationsView';

export default class DossierCalculationsContainer extends React.Component {
    static propTypes = {
      store: PropTypes.object.isRequired,
      locale: PropTypes.string.isRequired,
      messages: PropTypes.object.isRequired
    };

    render () {
      return (
        <RootContainer {...this.props}>
          <DossierCalculationsView />
        </RootContainer>
      );
    }
}



