import React, {PropTypes} from 'react';
import util from 'lib/util';

export default class SelectionInput extends React.PureComponent {
  static propTypes = {
    options: PropTypes.array.isRequired,
    onChange: PropTypes.func,
    name: PropTypes.string,
    value: PropTypes.any
  };

  handleChange = (e) => {
    const {props: {onChange}} = this;
    if (onChange) {
      onChange(e.target.value);
    }
  };

  render () {
    const {props: {options, value, name, ...attrs}} = this;

    return (
      <div className='form-group'>
        <select
          {...attrs}
          value={value}
          onChange={this.handleChange}
          name={name}>
          {options.map((item, i) =>
            <option key={i} value={item.value}>{util.unescapeHtml(item.text)}</option>)}
        </select>
      </div>);
  }
}



