import userStorage from 'lib/userStorage';
import clientTypes from 'lib/clientTypes';
import config from 'lib/config';
import {persistStore} from 'redux-persist';

export default (() => {
  let instance;
  let isRehydrated = false;
  let persistenceConfig = {
    whitelist: []
  };

  const init = (store) => {
    const rehydrate = () => {
      if (isRehydrated) return Promise.resolve();

      return new Promise(resolve => {
        // persist customer if logged in
        persistenceConfig.whitelist = ['customer', 'contract', 'calculations'];

        // do not persist customers when integrated in service centre, this caused caching and retrieving the same customer
        if (config.clientType === clientTypes.serviceCenter) persistenceConfig.whitelist = ['contract'];

        const persistor = persistStore(store, persistenceConfig, () => {
          isRehydrated = true;
          resolve();
        });

        if (!userStorage.isLoggedIn()) {
          persistor.purge();
        }
      });
    };

    return {rehydrate};
  };

  return {getInstance: (store) => instance || init(store)};
})();



