
		module.exports = function(context) {
			return (function() {
				

				if (!React) {
						var React = require("react");
					}

				return (
					<div>
  {this.user
    ? <this.Customer {...this.customerProps} />
    : <div className='row'>
      <div className='col-sm-6'>
        <this.LoginForm />
      </div>
      <div className='col-sm-6'>
        <this.RegisterForm />
      </div>
    </div>
  }
</div>

				);
			}).apply(context);
		};
	


