import {handleActions} from 'redux-actions';
import {REHYDRATE} from 'redux-persist/constants';

// TYPES
const CONTRACTS_REQUEST = 'CONTRACTS_REQUEST';
const CONTRACTS_RECEIVE = 'CONTRACTS_RECEIVE';
const CONTRACTS_FAILURE = 'CONTRACTS_FAILURE';
const CONTRACT_VERSION_REQUEST = 'CONTRACT_VERSION_REQUEST';
const CONTRACT_VERSION_RECEIVE = 'CONTRACT_VERSION_RECEIVE';
const CONTRACT_EDIT_REQUEST = 'CONTRACT_EDIT_REQUEST';
const CONTRACT_EDIT_RECEIVE = 'CONTRACT_EDIT_RECEIVE';
const CONTRACT_EDIT_FAILURE = 'CONTRACT_EDIT_FAILURE';
const CONTRACT_VERSION_FAILURE = 'CONTRACT_VERSION_FAILURE';
const CONTRACT_SAVE_REQUEST = 'CONTRACT_SAVE_REQUEST';
const CONTRACT_SAVE_RECEIVE = 'CONTRACT_SAVE_RECEIVE';
const CONTRACT_SAVE_FAILURE = 'CONTRACT_SAVE_FAILURE';
const CONTRACT_TERMINATE_REQUEST = 'CONTRACT_TERMINATE_REQUEST';
const CONTRACT_EDIT_SAVE_REQUEST = 'CONTRACT_EDIT_SAVE_REQUEST';
const CONTRACT_EDIT_SAVE_RECEIVE = 'CONTRACT_EDIT_SAVE_RECEIVE';
const CONTRACT_EDIT_SAVE_RECEIVE_FAILURE = 'CONTRACT_EDIT_SAVE_RECEIVE_FAILURE';
const SET_CONTRACT_TO_LOAD = 'SET_CONTRACT_TO_LOAD';

const CONTRACT_SAVE_AND_SIGN_REQUEST = 'CONTRACT_SAVE_AND_SIGN_REQUEST';
const CONTRACT_SAVE_AND_SIGN_RECEIVE = 'CONTRACT_SAVE_AND_SIGN_RECEIVE';
const CONTRACT_SAVE_AND_SIGN_FAILURE = 'CONTRACT_SAVE_AND_SIGN_FAILURE';

const CONTRACT_TERMINATE_REASONS_REQUEST = 'CONTRACT_TERMINATE_REASONS_REQUEST';
const CONTRACT_TERMINATE_REASONS_RECEIVE = 'CONTRACT_TERMINATE_REASONS_RECEIVE';

// ACTIONS
const requestContracts = () => ({type: CONTRACTS_REQUEST});
const receiveContracts = (contracts) => ({type: CONTRACTS_RECEIVE, contracts});
const receiveContractsFailure = (error) => ({type: CONTRACTS_FAILURE, payload: error}); // uses payload for old action handler in validation reducer
const requestSaveContract = (product, customer) => ({type: CONTRACT_SAVE_REQUEST, product, customer});
const receiveSaveContract = (insertedContractId) => ({type: CONTRACT_SAVE_RECEIVE, insertedContractId});
const receiveSaveContractFailure = (error) => ({type: CONTRACT_SAVE_FAILURE, payload: error}); // uses payload for old action handler in validation reducer

const requestContractTerminateReasons = () => ({type: CONTRACT_TERMINATE_REASONS_REQUEST});
const receiveContractTerminateReasons = (contractTerminateReasons) => ({
  type: CONTRACT_TERMINATE_REASONS_RECEIVE,
  contractTerminateReasons
});
const requestContractVersion = (contract) => ({type: CONTRACT_VERSION_REQUEST, contract});
const receiveContractVersion = (contractVersion, paymentMethod) => ({
  type: CONTRACT_VERSION_RECEIVE,
  contractVersion,
  paymentMethod
});
const receiveSaveContractVersionFailure = (error) => ({type: CONTRACT_SAVE_FAILURE, payload: error}); // uses payload for old action handler in validation reducer
const requestContractEdit = (contract) => ({type: CONTRACT_EDIT_REQUEST, contract});
const receiveContractEdit = (contractEdit, paymentMethod) => ({type: CONTRACT_EDIT_RECEIVE, contractEdit, paymentMethod});
const receiveContractEditFailure = (error) => ({type: CONTRACT_EDIT_FAILURE, payload: error});
const requestContractTerminate = (contractId) => ({type: CONTRACT_TERMINATE_REQUEST, contractId});
const requestContractEditSave = (newContract) => ({type: CONTRACT_EDIT_SAVE_REQUEST, newContract});
const receiveContractEditSave = () => ({type: CONTRACT_EDIT_SAVE_RECEIVE});
const receiveContractEditSaveFailure = (error) => ({type: CONTRACT_EDIT_SAVE_RECEIVE_FAILURE, payload: error});
const setContractToLoad = (contract) => ({type: SET_CONTRACT_TO_LOAD, contract});

const requestContractSaveAndSign = (selectedPaymentMethod) => ({type: CONTRACT_SAVE_AND_SIGN_REQUEST, selectedPaymentMethod});
const receiveContractSaveAndSign = () => ({type: CONTRACT_SAVE_AND_SIGN_RECEIVE});
const receiveContractSaveAndSignFailure = (error) => ({type: CONTRACT_SAVE_AND_SIGN_FAILURE, payload: error});

// REDUCER
const handleReceiveSaveContract = (state, action) => ({
  ...state,
  id: action.insertedContractId,
  isSavingContract: false
});
const handleRequestSaveContract = (state, action) => ({...state, isSavingContract: true});
const handleRequestSaveFailureContract = (state, action) => ({...state, isSavingContract: false});

const handleReceiveContracts = (state, action) => ({
  ...state,
  list: action.contracts,
  isLoadingList: false
});
const handleRequestContractVersion = (state, action) => ({...state, isLoadingContractDetails: true});
const handleReceiveContractVersion = (state, action) => ({
  ...state,
  details: action.contractVersion,
  paymentMethodDetails: action.paymentMethod,
  isLoadingContractDetails: false
});
const handleRequestContractEdit = (state, action) => ({
  ...state,
  isLoadingContractEditDetails: true,
  isEditFailed: false
});
const handleRequestContractEditSave = (state, action) => ({
  ...state,
  isSavingContract: true
});
const handleReceiveContractEditSave = (state, action) => ({
  ...state,
  isSavingContract: false,
  contractToLoad: null,
  isSavingAndSigningContract: false
});
const handleReceiveContractEditSaveFailure = (state, action) => ({
  ...state,
  isSavingContract: false,
  isSavingAndSigningContract: false
});
const handleReceiveContractEdit = (state, action) => ({
  ...state,
  contractEditDetails: action.contractEdit,
  isLoadingContractEditDetails: false,
  paymentMethod: action.paymentMethod
});
const handleReceiveContractEditFailure = (state, action) => ({
  ...state,
  isLoadingContractEditDetails: false,
  isEditFailed: true,
  isSavingContract: false
});

const handleRequestContractTerminateReasons = (state, action) => ({...state, isFetchingTerminateReasons: true});
const handleReceiveContractTerminateReasons = (state, action) => ({
  ...state,
  terminateReasons: action.contractTerminateReasons,
  isFetchingTerminateReasons: false
});

const handleSetContractToLoad = (state, action) => ({...state, contractToLoad: action.contract});
const handleRehydrate = (state, action) => {
  if (action.payload.contract && action.payload.contract.contractToLoad) {
    return {...state, contractToLoad: action.payload.contract.contractToLoad};
  }

  return state;
};

const handleReceiveContractSaveAndSign = (state, action) => ({...state, actionUrl: action.actionUrl, paymentProviderData: action.paymentProviderData, isSavingAndSigningContract: false});
const handleRequestContractSaveAndSign = (state, action) => ({...state, isSavingAndSigningContract: true});
const handleReceiveContractSaveAndSignFailure = (state, action) => ({...state, isSavingAndSigningContract: false});

const INITIAL_STATE = {
  isLoadingCalculation: false,
  isLoadingContractDetails: false,
  isLoadingContractEditDetails: false,
  isEditFailed: false,
  isSavingContract: false,
  isSavingAndSigningContract: false,
  isFetchingTerminateReasons: false
};

export default handleActions({
  [CONTRACTS_RECEIVE]: handleReceiveContracts,
  [CONTRACT_SAVE_RECEIVE]: handleReceiveSaveContract,
  [CONTRACT_SAVE_REQUEST]: handleRequestSaveContract,
  [CONTRACT_SAVE_FAILURE]: handleRequestSaveFailureContract,
  [CONTRACT_VERSION_REQUEST]: handleRequestContractVersion,
  [CONTRACT_VERSION_RECEIVE]: handleReceiveContractVersion,
  [CONTRACT_EDIT_REQUEST]: handleRequestContractEdit,
  [CONTRACT_EDIT_RECEIVE]: handleReceiveContractEdit,
  [CONTRACT_EDIT_SAVE_REQUEST]: handleRequestContractEditSave,
  [CONTRACT_EDIT_SAVE_RECEIVE]: handleReceiveContractEditSave,
  [CONTRACT_EDIT_SAVE_RECEIVE_FAILURE]: handleReceiveContractEditSaveFailure,
  [CONTRACT_VERSION_RECEIVE]: handleReceiveContractVersion,
  [CONTRACT_EDIT_FAILURE]: handleReceiveContractEditFailure,
  [SET_CONTRACT_TO_LOAD]: handleSetContractToLoad,
  [CONTRACT_SAVE_AND_SIGN_REQUEST]: handleRequestContractSaveAndSign,
  [CONTRACT_SAVE_AND_SIGN_RECEIVE]: handleReceiveContractSaveAndSign,
  [CONTRACT_SAVE_AND_SIGN_FAILURE]: handleReceiveContractSaveAndSignFailure,
  [CONTRACT_TERMINATE_REASONS_REQUEST]: handleRequestContractTerminateReasons,
  [CONTRACT_TERMINATE_REASONS_RECEIVE]: handleReceiveContractTerminateReasons,
  [REHYDRATE]: handleRehydrate
}, INITIAL_STATE);

export const types = {
  CONTRACTS_REQUEST,
  CONTRACTS_RECEIVE,
  CONTRACTS_FAILURE,
  CONTRACT_SAVE_REQUEST,
  CONTRACT_SAVE_RECEIVE,
  CONTRACT_SAVE_FAILURE,
  CONTRACT_VERSION_REQUEST,
  CONTRACT_VERSION_RECEIVE,
  CONTRACT_VERSION_FAILURE,
  CONTRACT_EDIT_REQUEST,
  CONTRACT_EDIT_RECEIVE,
  CONTRACT_TERMINATE_REQUEST,
  CONTRACT_EDIT_SAVE_REQUEST,
  CONTRACT_EDIT_SAVE_RECEIVE,
  CONTRACT_EDIT_SAVE_RECEIVE_FAILURE,
  CONTRACT_SAVE_AND_SIGN_REQUEST,
  CONTRACT_SAVE_AND_SIGN_RECEIVE,
  CONTRACT_SAVE_AND_SIGN_FAILURE,
  CONTRACT_TERMINATE_REASONS_REQUEST,
  CONTRACT_TERMINATE_REASONS_RECEIVE
};
export const actions = {
  requestContracts,
  receiveContracts,
  receiveContractsFailure,
  requestSaveContract,
  receiveSaveContract,
  receiveSaveContractFailure,
  requestContractVersion,
  receiveContractVersion,
  receiveSaveContractVersionFailure,
  requestContractEdit,
  receiveContractEdit,
  receiveContractEditFailure,
  requestContractTerminate,
  requestContractEditSave,
  receiveContractEditSave,
  receiveContractEditSaveFailure,
  setContractToLoad,
  requestContractSaveAndSign,
  receiveContractSaveAndSign,
  receiveContractSaveAndSignFailure,
  requestContractTerminateReasons,
  receiveContractTerminateReasons
};



