import React, {PropTypes} from 'react';
import {connect} from 'react-redux';

export default (ComposedComponent) => {
  class WrappedInput extends React.Component {
    getErrorMessage () {
      const {props: {serverValidation, disabled, meta: {touched, error}}} = this;
      let errorMessage = null;

      if (serverValidation) {
        // server side error
        errorMessage = serverValidation.message;
      } else if (error && !disabled) {
        // client side error
        errorMessage = touched && error;
      }

      return errorMessage;
    }

    onChange = (event) => {
      const {input: {onChange}} = this.props;

      onChange(event);
    };

    render () {
      return <ComposedComponent {...this.props} onChange={this.onChange} error={this.getErrorMessage()}/>;
    }
  }

  WrappedInput.propTypes = {
    input: PropTypes.any,
    question: PropTypes.any,
    meta: PropTypes.object,
    disabled: PropTypes.bool,
    serverValidation: PropTypes.object
  };

  const mapStateToProps = (state, ownProps) =>
    ({serverValidation: (state.validation[ownProps.meta.form] || []).find(msg => msg.id === ownProps.question.id)});

  return connect(mapStateToProps)(WrappedInput);
};



