
		module.exports = function(context) {
			return (function() {
				

				if (!React) {
						var React = require("react");
					}

				return (
					<div>
  <table className='table table-hover'>
    <thead>
    <tr>
      <th><this.FormattedMessage {...this.messages.contractNumber}/></th>
      <th><this.FormattedMessage {...this.messages.productName}/></th>
      <th><this.FormattedMessage {...this.messages.dateCreated}/></th>
      <th> </th>
    </tr>
    </thead>
    <tbody>
    {this.calculations.length > 0 && this.calculations.map((calculation, i) =>
      <tr key={i}>
        <td>{calculation.calculationId}</td>
        <td>{calculation.productName}</td>
        <td>{this.util.formatDate(calculation.dateCreated)}</td>
        <td>
          <span onClick={() => this.props.handleResumeCalculation(calculation)} className='glyphicon glyphicon-eye-open' />
         <span onClick={() => this.props.handleClickRemove(calculation)} className='glyphicon glyphicon-remove'/>
        </td>
      </tr>)}
    </tbody>
  </table>
</div>

				);
			}).apply(context);
		};
	


