import {first} from 'lodash';
import moment from 'moment';
import OptionId, {identifierObjectToIdentifierString} from './optionIdentifier';
import DateUtil from './date';

export default class Option {
  constructor (data) {
    Object.assign(this, data);
    this.buildIds();
    this.format();
    this.optionUses = this.optionUses || ['Calculation']; // todo: remove once API returns correct values
  }

  buildIds () {
    this.id = new OptionId(this.id).get();
    this.isRelevant = true;

    this.optionRules.forEach((optionRule, k) => {
      optionRule.id = new OptionId(optionRule.id).get();
      this.optionRules[k] = optionRule;
    });
  }

  static initializeResourceOptions = (resourceOptions) => {
    return resourceOptions.map(option => new Option(option));
  };

  getValue () {
    return this.type === 'Selection' && !this.value ? 'null' : this.value;
  }

  format () {
    switch (this.type.toLowerCase()) {
      case 'datetime':
        if (this.value) {
          this.value = DateUtil.format(this.value, 'DD-MM-YYYY');
        }
        break;
      case 'integer':
        if (this.value !== null) {
          this.value = this.value.toString();
        }
        break;
    }
  }

  static getKeyValuePairs = (answers, questions) => {
    let keyValuePairs = [];

    Object.keys(answers).forEach(answerId => {
      const question = questions.find(o => o.id === answerId);
      const key = OptionId.identifierStringToIdentifierObject(answerId);
      const value = Option.getCorrectOptionValueForType(answers[answerId], question.type);
      keyValuePairs.push({key, value});
    });

    return keyValuePairs;
  };

  static getCorrectOptionValueForType = (value, type) => {
    switch (type.toLowerCase()) {
      case 'datetime':
        return moment(value, 'DD-MM-YYYY').toDate();
      case 'decimal':
      case 'currency':
        return parseFloat(value);
      case 'integer':
        return parseInt(value, 10);
      default:
        return value;
    }
  };

  static mergeOptions (options = [], optionsToMerge = []) {
    return options.map(o => {
      const overrideOption = optionsToMerge.filter(ao => identifierObjectToIdentifierString(ao.id) === o.id)[0];
      return overrideOption || o;
    });
  }

  isQuestionRelevant (values, options) {
    let isRelevantRule = this.optionRules.length === 0;

    this.optionRules.forEach(rule => {
      if (!values || values[rule.id] == null || isRelevantRule) {
        return;
      }

      const correspondingOption = first(options.filter(o => o.id === rule.id));
      if (rule.id !== this.id && (!correspondingOption || !correspondingOption.isRelevant)) {
        return;
      }

      let optionValue = values[rule.id].toString().toLowerCase();
      let ruleValue = rule.value.toString().toLowerCase();
      switch (rule.operator.toLowerCase()) {
        case 'iscontaining':
          isRelevantRule = optionValue.indexOf(ruleValue) !== -1;
          break;
        case 'isequalto':
          isRelevantRule = optionValue === ruleValue;
          break;
        case 'islargerthen':
          isRelevantRule = this.compareValues(ruleValue, optionValue) === 1;
          break;
        case 'issmallerthen':
          isRelevantRule = this.compareValues(ruleValue, optionValue) === -1;
          break;
        case 'isnotequalto':
          isRelevantRule = this.compareValues(ruleValue, optionValue) !== 0;
          break;
        case 'isnotolderthen':
          isRelevantRule = optionValue <= new Date(ruleValue);
          break;
        case 'isolderthen':
          isRelevantRule = optionValue > new Date(ruleValue);
          break;
      }
    });

    this.isRelevant = isRelevantRule;

    return this.isRelevant;
  }

  compareValues (v1, v2) {
    let cmpV1 = v1.replace('.', '').replace(',', '.');
    let cmpV2 = v2.replace('.', '').replace(',', '.');
    let rv = '';

    if (isNaN(cmpV1) || v1.trim() === '') {
      cmpV1 = v1;
      cmpV2 = v2;
    } else {
      if (isNaN(cmpV2) || v2.trim() === '') {
        return 0;
      }
      cmpV1 = parseFloat(cmpV1);
      cmpV2 = parseFloat(cmpV2);
    }

    if (cmpV2 === cmpV1) {
      rv = 0;
    } else if (cmpV2 > cmpV1) {
      rv = 1;
    } else if (cmpV2 < cmpV1) {
      rv = -1;
    } else {
      rv = 'Fail to compare.';
    }
    return rv;
  }
}

export const getKeyValuePairs = Option.getKeyValuePairs;



