import {handleActions} from 'redux-actions';
import {identifierObjectToIdentifierString} from 'lib/optionIdentifier';
import dynamicFormTypes from 'lib/dynamicFormTypes';

// TYPES
const CLEAR_SERVER_VALIDATION_MESSAGE = 'CFE/VALIDATION/CLEAR_SERVER_VALIDATION_MESSAGE';
const VALIDATION_RECEIVE = 'CFE/VALIDATION/RECEIVE';

// ACTIONS
const clearServerValidationMessage = (form, fieldId) => ({type: CLEAR_SERVER_VALIDATION_MESSAGE, form, fieldId});
const receiveValidation = (form, error) => ({type: VALIDATION_RECEIVE, form, error});

// REDUCER
const handleReceiveValidation = (state, action) => {
  if (action.error && action.error.messages) {
    const validationMessages = action.error.messages.filter(m => m.type === 'Error').map(msg => {
      msg.id = identifierObjectToIdentifierString(msg.identifier);
      return msg;
    });

    return {...state, [action.form]: validationMessages};
  }

  return state;
};

const handleClearServerValidationMessage = (state, action) => {
  const newState = {...state};
  newState[action.form] = newState[action.form].filter(msg => msg.id !== action.fieldId);
  newState[action.form] = newState[action.form].filter(msg => msg.identifier.subEntity !== '');
  return newState;
};

const INITIAL_STATE = Object.values(dynamicFormTypes).reduce((acc, formType) => ({...acc, [formType]: []}), {});

export default handleActions({
  [VALIDATION_RECEIVE]: handleReceiveValidation,
  [CLEAR_SERVER_VALIDATION_MESSAGE]: handleClearServerValidationMessage
}, INITIAL_STATE);

export const types = {VALIDATION_RECEIVE, CLEAR_SERVER_VALIDATION_MESSAGE};
export const actions = {receiveValidation, clearServerValidationMessage};



