
		module.exports = function(context) {
			return (function() {
				

				if (!React) {
						var React = require("react");
					}

				return (
					<div className='checkbox'>
  <label>
    <input type='checkbox'
           checked={this.state.isChecked}
           name={this.props.name}
           onChange={this.handleChange}
    />
    {this.props.label}
  </label>
</div>

				);
			}).apply(context);
		};
	


