
		module.exports = function(context) {
			return (function() {
				

				if (!React) {
						var React = require("react");
					}

				return (
					<div className='cfe-account'>
  <ul className='nav navbar-nav navbar-right'>
    <li className='navbar-text'>
      <span> { this.props.customer && this.props.customer.getFullName() } </span>
    </li>
    <li className='dropdown'>
      <a href='#' className='dropdown-toggle' data-toggle='dropdown' role='button' aria-haspopup='true' aria-expanded='false'>
        Account <span className='caret'/>
      </a>
      {
        this.props.user
          ? <ul className='dropdown-menu'>
              <li>
                <a href='/dossier/customer-details'>
                  <this.FormattedMessage {...this.messages.customerDetails} />
                </a>
              </li>
              <li>
                <a href='/dossier/incidents'>
                  <this.FormattedMessage {...this.messages.incidents} />
                </a>
              </li>
              <li>
                <a href='/dossier/payment-methods'>
                  <this.FormattedMessage {...this.messages.paymentMethods} />
                </a>
              </li>
              <li>
                <a href='/dossier/contracts'>
                  <this.FormattedMessage {...this.messages.contracts} />
                </a>
              </li>
              <li>
                <a href='/dossier/documents'>
                  <this.FormattedMessage {...this.messages.documents} />
                </a>
              </li>
              <li>
                <a href='/dossier/calculations'>
                  <this.FormattedMessage {...this.messages.calculations} />
                </a>
              </li>
              <li>
                <a href='/dossier/emails'>
                  <this.FormattedMessage {...this.messages.emails} />
                </a>
              </li>
              <li><a onClick={this.logout.bind(this)}>Logout</a></li>
            </ul>
          : <ul className='dropdown-menu'>
              <li><a onClick={() => {window.location.href = '/login'}}>Login</a></li>
            </ul>
      }
    </li>
  </ul>
</div>

				);
			}).apply(context);
		};
	


