import React, {PropTypes} from 'react';
import {reduxForm} from 'redux-form';
import Input from 'components/InputTypes/Input';
import {defineMessages} from 'react-intl';
import LoadingButton from 'components/buttons/LoadingButton';
import {connect} from 'react-redux';
import {actions as stepperActions} from 'redux/modules/stepper';

export class StepByStepQuestionsForm extends React.Component {
  componentWillReceiveProps (nextProps) {
    if (nextProps.activeQuestion && nextProps.activeQuestion.isReadonly) {
      // mark readonly questions as valid, as they don't need to be validated to go to the next question
      this.props.markQuestionAsValid(nextProps.activeStepIndex, nextProps.activeQuestionIndex);

      if (nextProps.activeQuestionIndex > this.props.activeQuestionIndex) {
        this.props.goToNextQuestion(nextProps.activeStepIndex);
      }

      if (nextProps.activeQuestionIndex < this.props.activeQuestionIndex) {
        this.props.goToPreviousQuestion(this.props.activeStepIndex);
      }
    }

    if (this.props.isFetchingLookupData && !nextProps.isFetchingLookupData) {
      this.props.goToNextQuestion(nextProps.activeStepIndex);
    }
  }

  render () {
    return (
      <form>
        {this.renderInputs()}
        {!this.props.readOnly &&
        <div>
          {!this.props.hideSubmitButton &&
          <LoadingButton
            className='btn btn-primary'
            onClick={this.props.handleSubmit}
            message={messages.submitButtonLabel}
            isLoading={this.props.isLoading}
            disabled={!this.props.valid}>
            <i className='glyphicon glyphicon-send'/>
          </LoadingButton>}
        </div>}
      </form>
    );
  }

  renderInputs = () => this.props.activeQuestion && <Input key={this.props.activeQuestion.id}
                                                           name={this.props.activeQuestion.id}
                                                           disabled={this.props.readOnly || this.props.activeQuestion.isReadonly}
                                                           question={this.props.activeQuestion} />;
}

StepByStepQuestionsForm.propTypes = {
  handleSubmit: PropTypes.func,
  goToNextQuestion: PropTypes.func,
  goToPreviousQuestion: PropTypes.func,
  markQuestionAsValid: PropTypes.func,
  hideSubmitButton: PropTypes.bool,
  valid: PropTypes.bool,
  readOnly: PropTypes.bool,
  shouldSubmitWhenValid: PropTypes.bool,
  isFetchingLookupData: PropTypes.bool,
  isLoading: PropTypes.bool,
  activeStepIndex: PropTypes.integer,
  activeQuestionIndex: PropTypes.integer,
  activeQuestion: PropTypes.object
};

const messages = defineMessages({
  submitButtonLabel: {
    id: 'dynamicForm.submitButtonLabel',
    description: 'Label shown on submit button',
    defaultMessage: 'Submit'
  }
});

const mapStateToProps = (state, ownProps) => {
  const activeQuestionIndex = state.stepper.steps[state.stepper.activeStepIndex].activeQuestionIndex;

  return {
    activeQuestionIndex,
    activeStepIndex: state.stepper.activeStepIndex,
    activeQuestion: ownProps.questionsToRender[activeQuestionIndex],
    isFetchingLookupData: state.lookupData.isLoading
  };
};

const mapDispatchToProps = (dispatch) => ({
  goToNextQuestion: activeStepIndex => dispatch(stepperActions.goToNextQuestion(activeStepIndex)),
  goToPreviousQuestion: activeStepIndex => dispatch(stepperActions.goToPreviousQuestion(activeStepIndex)),
  markQuestionAsValid: (activeStepIndex, activeQuestionIndex) => dispatch(stepperActions.markQuestionAsValid(activeStepIndex, activeQuestionIndex))
});

export default connect(mapStateToProps, mapDispatchToProps)(reduxForm()(StepByStepQuestionsForm));



