
		module.exports = function(context) {
			return (function() {
				

				if (!React) {
						var React = require("react");
					}

				return (
					<div>
  <this.Product
    {...this.props.product}
    formName={this.dynamicFormTypes.extraQuestions}
    handleSubmit={this.nextStep}
    options={this.options}
    answers={this.props.saveContractAnswers}/>
</div>


				);
			}).apply(context);
		};
	


