import {PropTypes} from 'react';
import {injectIntl, defineMessages} from 'react-intl';
import {actions as contractActions} from 'redux/modules/contract';
import dossierFetcher from 'enhancers/dossierFetcher';
import ContractClass from 'lib/contract';
import CustomerClass from 'lib/customer';
import TerminateReasonsClass from 'lib/terminateReasons';
import {reset} from 'redux-form';
import {connect} from 'react-redux';
import Modal from 'components/Modal';
import ContractDetailsModal from 'components/Contract/detailsModal';
import ContractList from 'components/Contract/list';
import TerminateReasons from 'components/TerminateReasons';
import Loader from 'components/Loader';
import CfeComponent from 'components/CfeComponent';
import dynamicFormTypes from 'lib/dynamicFormTypes';

export class DossierContractsView extends CfeComponent {
  static displayName = 'DossierContractsView';

  constructor (props) {
    super(props, 'views/DossierContractsView/DossierContractsView');

    this.state = {
      activeContract: {},
      isDetailsModalOpen: false,
      isDisplayingInactiveContracts: false
    };
  }

  componentWillMount () {
    this.props.requestContractTerminateReasons();
  }

  handleClickView = (contract) => {
    this.props.requestContractVersion(contract);

    if (this.props.config.shouldUseModals) {
      this.setState({
        activeContract: contract,
        isDetailsModalOpen: true,
        isConfirmationModalOpen: false
      });
    } else {
      window.location = this.props.config.urls.contractDetails;
    }
  };

  openTerminateModal = (contract) => {
    this.props.resetContractTerminateForm();

    this.setState({
      activeContract: contract,
      isConfirmationModalOpen: true
    });
  };

  modalClosedCallback = () => {
    this.setState({isDetailsModalOpen: false, isConfirmationModalOpen: false});
  };

  terminateContract = () => {
    this.props.requestContractTerminate(this.state.activeContract.currentActiveContractId);
    this.setState({isConfirmationModalOpen: false});
  };

  render () {
    const context = {
      ...this,
      ContractList,
      Loader,
      ContractDetailsModal,
      messages,
      TerminateReasons,
      Modal,
      dynamicFormTypes
    };
    return super.render(context);
  }
}

DossierContractsView.propTypes = {
  contracts: PropTypes.array,
  customer: PropTypes.object,
  calculation: PropTypes.object,
  terminateReasonsAnswers: PropTypes.object,
  terminateReasons: PropTypes.object,
  isLoadingContractList: PropTypes.bool
};

const mapStateToProps = (state) => {
  const customer = new CustomerClass(state.customer.activeCustomer);

  return {
    contracts: (state.contract.list || []).map(c => new ContractClass(c, customer)),
    customer,
    isLoadingContractList: state.contract.isLoadingList,
    terminateReasonsAnswers: state.form.terminate,
    terminateReasons: new TerminateReasonsClass(state.contract.terminateReasons),
    config: state.config.dossierConfig.contractsOverview
  };
};

const mapDispatchToProps = (dispatch) => ({
  resetContractTerminateForm: () => dispatch(reset(dynamicFormTypes.contractTerminate)),
  requestContractTerminateReasons: () => dispatch(contractActions.requestContractTerminateReasons()),
  requestContractVersion: (contractVersionId) => dispatch(contractActions.requestContractVersion(contractVersionId)),
  requestContractTerminate: (contractId, reason) => dispatch(contractActions.requestContractTerminate(contractId, reason))
});

const messages = defineMessages({
  terminateContract: {
    id: 'contractsView.terminateContract',
    description: 'Dialog title for terminate contract',
    defaultMessage: 'Terminate contract'
  }
});

export default connect(mapStateToProps, mapDispatchToProps)(dossierFetcher(injectIntl(DossierContractsView)));



