import Option from './option';

export default class TerminateReasons {
  constructor (terminateReasonsData = {}) {
    if (terminateReasonsData.options) terminateReasonsData.options = Option.initializeResourceOptions(terminateReasonsData.options);
    Object.assign(this, terminateReasonsData);
  }
}



