import React, {PropTypes} from 'react';
import {injectIntl} from 'react-intl';
import PaymentMethodClass from 'lib/paymentMethod';
import CustomerClass from 'lib/customer';
import dossierFetcher from 'enhancers/dossierFetcher';
// Redux
import {connect} from 'react-redux';
import {actions as customerActions} from 'redux/modules/customer';

// Components
import PaymentMethodList from 'components/PaymentMethod/list';

export class DossierPaymentMethodsView extends React.Component {
  static displayName = 'DossierPaymentMethodsView';

  static propTypes = {
    customer: PropTypes.object,
    requestSaveCustomerPaymentMethod: PropTypes.func,
    paymentMethodTemplates: PropTypes.array,
    intl: PropTypes.object,
    customerPaymentMethods: PropTypes.array,
    isLoadingPaymentMethod: PropTypes.bool
  };

  handleSavePaymentMethod = (newPaymentMethodData, paymentMethod) => {
    return this.props.requestSaveCustomerPaymentMethod(paymentMethod, newPaymentMethodData);
  };

  render () {
    const {props: {customerPaymentMethods, paymentMethodTemplates, isLoadingPaymentMethod}} = this;

    return <PaymentMethodList
      isLoadingPaymentMethod={isLoadingPaymentMethod}
      customerPaymentMethods={customerPaymentMethods}
      paymentMethodTemplates={paymentMethodTemplates}
      handleEditPaymentMethod={this.handleSavePaymentMethod}
      handleCreatePaymentMethod={this.handleSavePaymentMethod} />;
  }
}

const mapStateToProps = (state) => ({
  customer: new CustomerClass(state.customer.activeCustomer),
  paymentMethodTemplates: (state.paymentMethod.list || []).map(p => new PaymentMethodClass(p)),
  customerPaymentMethods: (state.customer.paymentMethods || []).map(p => new PaymentMethodClass(p)),
  isLoadingPaymentMethod: state.customer.isLoadingPaymentMethod
});

const mapDispatchToProps = (dispatch) => ({
  requestSaveCustomerPaymentMethod: (paymentMethod, newPaymentMethodData) => dispatch(customerActions.requestSaveCustomerPaymentMethod(paymentMethod, newPaymentMethodData))
});

export default connect(mapStateToProps, mapDispatchToProps)(dossierFetcher(injectIntl(DossierPaymentMethodsView)));



