import {actions as productActions} from 'redux/modules/product';
import {types as flowTypes, actions as flowActions} from 'redux/modules/flow';
import {actions as stepperActions} from 'redux/modules/stepper';
import Calculateable from 'lib/calculateable';
import {takeLatest} from 'redux-saga';
import {put, select} from 'redux-saga/effects';
import {api} from 'lib/api';
import {actions as lookupDataActions} from 'redux/modules/lookupData';
import {actions as paymentMethodActions} from 'redux/modules/paymentMethod';
import {actions as calculationActions} from 'redux/modules/calculation';
import LookupService from 'lib/lookupService';
import {omit} from 'lodash';
import Logger from 'lib/logger';
import {requestContractEdit} from 'redux/sagas/contractSaga';
import formComponentTypes from 'lib/formComponentTypes';

export function * startProductFlowRequest ({productId}) {
  const productConfig = yield select(state => state.config.productConfig);
  const productDetails = yield api.products.details.get({productId});
  const lookupDataGroups = LookupService.serviceDefinitionsToLookupDataGroups(productDetails.lookupDataServiceDefinitions);

  yield [
    put(lookupDataActions.receiveLookupData(lookupDataGroups)),
    put(productActions.receiveProductDetails(omit(productDetails, 'options'))),
    put(calculationActions.resetSavedCalculationId())
  ];

  yield initializeFlow({
    productId,
    options: productDetails.options
  }, productConfig);
}

function * startCalculationFlowRequest ({calculationId}) {
  try {
    const [calculationConfig, customer] = yield [
      select(state => state.config.calculationConfig),
      select(state => state.customer.activeCustomer)
    ];

    const calculation = yield api.calculations.get({calculationId, customerId: customer.id});
    const productDetails = yield api.products.details.get({productId: calculation.insuranceId});
    const lookupDataGroups = LookupService.serviceDefinitionsToLookupDataGroups(calculation.lookupDataServiceDefinitions);

    yield [
      put(lookupDataActions.receiveLookupData(lookupDataGroups)),
      put(productActions.receiveProductDetails(omit(productDetails, 'options')))
    ];

    yield initializeFlow({
      productId: calculation.insuranceId,
      calculationId: calculation.calculationId,
      options: calculation.options
    }, calculationConfig);
  } catch (error) {
    Logger.error(yield error);
  }
}

export function * startContractEditFlowRequest () {
  try {
    const contract = yield select(state => state.contract.contractToLoad);
    const productDetails = yield api.products.details.get({productId: contract.productId});

    yield requestContractEdit({contract});

    const paymentMethod = yield select(state => state.contract.paymentMethod);

    yield [
      put(productActions.receiveProductDetails(omit(productDetails, 'options'))),
      put(paymentMethodActions.selectPaymentMethod(paymentMethod))
    ];

    let options = yield select(state => state.contract.contractEditDetails.options);

    yield initializeFlow({
      contractNr: contract.contractNr,
      contractId: contract.currentActiveContractId,
      productId: contract.productId,
      options
    });
  } catch (error) {
    Logger.error(yield error);
  }
}

function * initializeFlow (calculateable, config = null) {
  let defaultFlow;

  if (!config || !config.flow || !config.flow.steps) {
    defaultFlow = getDefaultFlow(calculateable);
  }

  const steps = buildSteps(defaultFlow ? defaultFlow.steps : config.flow.steps);

  yield [
    put(flowActions.receiveCalculateable(calculateable)),
    put(stepperActions.initializeSteps(steps))
  ];
}

const buildSteps = steps => steps.map((s, i) => {
  let isLastStepOfType = steps[i + 1] ? steps[i + 1].type !== steps[i].type : false;

  if (s.formComponent === formComponentTypes.stepByStepQuestions) {
    s.activeQuestionIndex = 0;
    s.questions = s.questions.map(s => ({...s, isValid: false}));
  }

  return {
    ...s,
    stepIndex: i,
    isVisited: i === 0,
    isOpen: i === 0,
    isValid: false,
    shouldSubmitWhenValid: isLastStepOfType,
    isLastStepOfType
  };
});

const getDefaultFlow = (calculateableData) => {
  const calculateable = new Calculateable(calculateableData);

  return {
    steps: [
      {
        type: 'calculation',
        questions: calculateable.getQuestionSet('calculation').map(q => ({id: q.id}))
      },
      {type: 'customer'},
      {
        type: 'saveContract',
        questions: calculateable.getQuestionSet('saveproduct').map(q => ({id: q.id}))
      },
      {type: 'summary'},
      {type: 'payment'},
      {type: 'closing'}
    ]
  };
};

export function * flowEvents () {
  yield [
    takeLatest(flowTypes.START_PRODUCT_FLOW_REQUEST, startProductFlowRequest),
    takeLatest(flowTypes.START_CALCULATION_FLOW_REQUEST, startCalculationFlowRequest),
    takeLatest(flowTypes.START_CONTRACT_EDIT_FLOW_REQUEST, startContractEditFlowRequest)
  ];
}



