import {handleActions} from 'redux-actions';
import {isString} from 'lodash';

// TYPES
const INITIALIZE_STEPS = 'INITIALIZE_STEPS';
const SET_ACTIVE_STEP = 'SET_ACTIVE_STEP';
const SET_STEP_VALID = 'SET_STEP_VALID';
const SET_STEP_INVALID = 'SET_STEP_INVALID';
const SHOW_STEP = 'SHOW_STEP';
const HIDE_STEP = 'HIDE_STEP';
const GO_TO_NEXT_QUESTION = 'GO_TO_NEXT_QUESTION';
const GO_TO_PREVIOUS_QUESTION = 'GO_TO_PREVIOUS_QUESTION';
const SET_QUESTION_VALID = 'SET_QUESTION_VALID';
const SET_QUESTION_INVALID = 'SET_QUESTION_INVALID';

// ACTIONS
const initializeSteps = (steps) => ({type: INITIALIZE_STEPS, steps});
const setActiveStep = (stepIndex, shouldHideOtherSteps = true) => ({type: SET_ACTIVE_STEP, stepIndex, shouldHideOtherSteps});
const markStepAsValid = (stepIndex) => ({type: SET_STEP_VALID, stepIndex});
const markStepAsInvalid = (stepIndex) => ({type: SET_STEP_INVALID, stepIndex});
const showStep = (stepIndex) => ({type: SHOW_STEP, stepIndex});
const hideStep = (stepIndex) => ({type: HIDE_STEP, stepIndex});
const goToNextQuestion = (stepIndexOrStepKey) => ({type: GO_TO_NEXT_QUESTION, stepIndexOrStepKey});
const goToPreviousQuestion = (stepIndexOrStepKey) => ({type: GO_TO_PREVIOUS_QUESTION, stepIndexOrStepKey});
const markQuestionAsValid = (stepIndex, questionIndex) => ({type: SET_QUESTION_VALID, stepIndex, questionIndex});
const markQuestionAsInvalid = (stepIndex, questionIndex) => ({type: SET_QUESTION_INVALID, stepIndex, questionIndex});

const stepperHistory = [];

const handleSetActiveStep = (state, {stepIndex, shouldHideOtherSteps}) => {
  stepperHistory.push(state);
  const newState = getNewStepState(state, stepIndex, shouldHideOtherSteps);
  window.history.pushState(newState, 'stepper');
  return newState;
};

const handlePopStepperHistory = (state, action) => {
  let newState;
  let stepIndexInEvent = action.event.state && action.event.state.activeStepIndex || 0;

  if (stepIndexInEvent > state.activeStepIndex) {
    newState = stepperHistory.pop();
  } else {
    newState = getNewStepState(state, stepIndexInEvent);
    stepperHistory.push(state);
  }

  if (!stepperHistory.length) window.history.back();

  window.history.replaceState(newState, 'stepper');
  return {...state, ...newState};
};

const getNewStepState = (state, stepIndex, shouldHideOtherSteps) => {
  let nextState = {steps: state.steps.slice()};

  if (nextState.steps[stepIndex] && !state.completed) {
    if (shouldHideOtherSteps) {
      nextState.steps = nextState.steps.map(s => ({...s, isOpen: false}));
    }

    nextState.activeStepIndex = stepIndex;
    nextState.steps[stepIndex].isVisited = true;
    nextState.steps[stepIndex].isOpen = true;
  }

  if (stepIndex === nextState.steps.length - 1) {
    nextState.completed = true;
  }

  return {...state, ...nextState};
};

const handleInitializeSteps = (state, action) => {
  const newState = {...state, steps: action.steps};
  window.history.replaceState(newState, 'stepper');
  return newState;
};

const handleMarkStepAsValid = (state, {stepIndex}) => {
  let nextState = {steps: state.steps.slice()};
  nextState.steps[stepIndex].isValid = true;
  return {...state, ...nextState};
};

const handleMarkStepAsInvalid = (state, {stepIndex}) => {
  let nextState = {steps: state.steps.slice()};
  nextState.steps[stepIndex].isValid = false;
  return {...state, ...nextState};
};

const handleShowStep = (state, {stepIndex}) => {
  let nextState = {steps: state.steps.slice()};
  nextState.steps[stepIndex].isOpen = true;
  return {...state, ...nextState};
};

const handleHideStep = (state, {stepIndex}) => {
  let nextState = {steps: state.steps.slice()};
  nextState.steps[stepIndex].isOpen = false;
  return {...state, ...nextState};
};

const handleGoToNextQuestion = (state, {stepIndexOrStepKey}) => {
  let nextState = {steps: state.steps.slice()};
  const stepIndex = isString(stepIndexOrStepKey)
    ? nextState.steps.findIndex(s => s.stepKey === stepIndexOrStepKey)
    : stepIndexOrStepKey;
  const activeStep = nextState.steps[stepIndex];

  if (activeStep.activeQuestionIndex < activeStep.questions.length - 1 &&
    activeStep.questions[activeStep.activeQuestionIndex].isValid) {
    nextState.steps[stepIndex].activeQuestionIndex++;
  }

  return {...state, ...nextState};
};

const handleGoToPreviousQuestion = (state, {stepIndexOrStepKey}) => {
  let nextState = {steps: state.steps.slice()};
  const stepIndex = isString(stepIndexOrStepKey)
    ? nextState.steps.findIndex(s => s.stepKey === stepIndexOrStepKey)
    : stepIndexOrStepKey;

  if (nextState.steps[stepIndex].activeQuestionIndex !== 0) {
    nextState.steps[stepIndex].activeQuestionIndex--;
  }

  return {...state, ...nextState};
};

const handleMarkQuestionAsValid = (state, {stepIndex, questionIndex}) => {
  let nextState = {steps: state.steps.slice()};
  nextState.steps[stepIndex].questions[questionIndex].isValid = true;
  return {...state, ...nextState};
};

const handleMarkQuestionAsInvalid = (state, {stepIndex, questionIndex}) => {
  let nextState = {steps: state.steps.slice()};
  nextState.steps[stepIndex].questions[questionIndex].isValid = false;
  return {...state, ...nextState};
};

// REDUCER
const INITIAL_STATE = {
  activeStepIndex: 0,
  steps: []
};

export default handleActions({
  [INITIALIZE_STEPS]: handleInitializeSteps,
  [SET_ACTIVE_STEP]: handleSetActiveStep,
  [SET_STEP_VALID]: handleMarkStepAsValid,
  [SET_STEP_INVALID]: handleMarkStepAsInvalid,
  [SET_QUESTION_VALID]: handleMarkQuestionAsValid,
  [SET_QUESTION_INVALID]: handleMarkQuestionAsInvalid,
  [SHOW_STEP]: handleShowStep,
  [HIDE_STEP]: handleHideStep,
  'WINDOW_ON_POP_STATE': handlePopStepperHistory,
  [GO_TO_NEXT_QUESTION]: handleGoToNextQuestion,
  [GO_TO_PREVIOUS_QUESTION]: handleGoToPreviousQuestion
}, INITIAL_STATE);

export const actions = {
  initializeSteps,
  markStepAsValid,
  markStepAsInvalid,
  setActiveStep,
  showStep,
  hideStep,
  goToNextQuestion,
  goToPreviousQuestion,
  markQuestionAsValid,
  markQuestionAsInvalid
};

export const types = {
  INITIALIZE_STEPS,
  SET_ACTIVE_STEP,
  SET_STEP_VALID,
  SET_STEP_INVALID,
  SHOW_STEP,
  HIDE_STEP,
  GO_TO_NEXT_QUESTION
};



