import {defineMessages} from 'react-intl';

export default class CalculationClass {
  constructor (calculationData = {}) {
    Object.assign(this, calculationData);
  }

  getSummary (startDate, paymentInterval, transactionCosts, product, formatMessage) {
    return [{
      key: formatMessage(messages.summaryPremiumLine, {paymentInterval}),
      value: ' ' + this.paymentIntervalPremium.amountBeforeTax
    }, {
      key: formatMessage(messages.summaryTaxLine),
      value: ' ' + this.paymentIntervalPremium.tax
    }, {
      key: formatMessage(messages.summaryTotalPremiumLine, {paymentInterval}),
      value: ' ' + this.paymentIntervalPremium.amountAfterTax
    }, {
      key: product.signee.signText,
      value: product.signee.signNameCompany + ' ' + product.signee.signPlace + ', ' + startDate
    }, {
      key: '',
      value: formatMessage(messages.summaryTransactionCosts, {
        date: startDate,
        transactionCosts: transactionCosts.afterTax
      })
    }];
  }
}

const messages = defineMessages({
  summaryPremiumLine: {
    id: 'summary.premiumLine',
    description: 'Summary premium line',
    defaultMessage: 'Your premium'
  },
  summaryTaxLine: {
    id: 'summary.taxLine',
    description: 'Summary tax line',
    defaultMessage: 'Insurance tax (21%)'
  },
  summaryTotalPremiumLine: {
    id: 'summary.totalPremiumLine',
    description: 'Summary total premium line',
    defaultMessage: 'Your total premium'
  },
  summaryTransactionCosts: {
    id: 'summary.transactionCostsLine',
    description: 'Summary transactioncosts line',
    defaultMessage: 'The premiums are per {date} and exclude  {transactionCosts} transaction- and administrationcosts per invoice'
  }
});



