import Product from 'components/Product';
import CfeComponent from 'components/CfeComponent';
import {injectIntl} from 'react-intl';
import {connect} from 'react-redux';
import LoadingButton from 'components/buttons/LoadingButton';
import {getFormValues, isValid} from 'redux-form';
import {actions as calculationActions} from 'redux/modules/calculate';
import CalculationClass from 'lib/calculation';
import {actions as stepperActions} from 'redux/modules/stepper';
import localMessages from 'views/ProductView/messages';
import dynamicFormTypes from 'lib/dynamicFormTypes';
import formComponentTypes from 'lib/formComponentTypes';
import {isString} from 'lodash';

class CalculationStep extends CfeComponent {
  static displayName = 'CalculationStep';

  constructor (props) {
    super(props, 'views/ProductView/CalculationStep');
    this.state = {
      isStepByStepQuestionsFormCompleted: false,
      canGoToNextQuestion: false
    };
  }

  componentWillReceiveProps (nextProps) {
    if (this.props.hasStepByStepQuestionsFormComponent) {
      this.setState({
        isStepByStepQuestionsFormCompleted: this.props.step.questions.every(q => q.isValid),
        canGoToNextQuestion: this.props.step.questions[this.props.step.activeQuestionIndex].isValid
      });
    }

    if (this.isTemplateOverriden) {
      super.componentWillReceiveProps(nextProps);
    }
  }

  handleCalculation = () => this.props.requestCalculate(this.props.calculateable, dynamicFormTypes.calculation);

  nextStep = () => this.props.setActiveStep(this.props.step.stepIndex + 1);

  canContinueAfterCalculationStep = () => this.props.calculation.paymentIntervalPremium && this.props.isCalculationFormValid;

  getStepQuestions = () => this.props.calculateable.getStepQuestions(this.props.step);

  goToPreviousQuestion = stepKey => this.props.hasStepByStepQuestionsFormComponent && isString(stepKey) ? this.props.goToPreviousQuestion(stepKey) : null;

  goToNextQuestion = stepKey => this.props.hasStepByStepQuestionsFormComponent && isString(stepKey) ? this.props.goToNextQuestion(stepKey) : null;

  render () {
    const canContinueAfterCalculationStep = this.canContinueAfterCalculationStep();

    const handleGoToCustomerDataStep = () => {
      if (canContinueAfterCalculationStep) {
        this.nextStep();
      }
    };

    let options = this.getStepQuestions();

    const context = {
      ...this,
      Product,
      stepKey: this.props.step.stepKey || 'default',
      formComponent: this.props.step.formComponent || formComponentTypes.dynamicOptionsForm,
      canContinueAfterCalculationStep,
      handleGoToCustomerDataStep,
      LoadingButton,
      options,
      localMessages,
      dynamicFormTypes
    };

    return super.render(context);
  }
}

const mapStateToProps = (state, ownProps) => ({
  calculation: new CalculationClass(state.calculation),
  calculationAnswers: getFormValues(dynamicFormTypes.calculation)(state),
  isCalculationFormValid: isValid(dynamicFormTypes.calculation)(state),
  hasStepByStepQuestionsFormComponent: ownProps.step.formComponent === formComponentTypes.stepByStepQuestions
});

const mapDispatchToProps = (dispatch) => ({
  setActiveStep: (stepIndex) => dispatch(stepperActions.setActiveStep(stepIndex)),
  requestCalculate: (product, calculationContext) => dispatch(calculationActions.requestCalculate(product, calculationContext)),
  goToPreviousQuestion: stepKey => dispatch(stepperActions.goToPreviousQuestion(stepKey)),
  goToNextQuestion: stepKey => dispatch(stepperActions.goToNextQuestion(stepKey))
});

export default connect(mapStateToProps, mapDispatchToProps)(injectIntl(CalculationStep));



