import {takeLatest} from 'redux-saga';
import {put, select} from 'redux-saga/effects';
import {api} from 'lib/api';
import {getFormValues} from 'redux-form';
import {getKeyValuePairs} from 'lib/option';
import {types, actions as contractActions} from 'redux/modules/contract';
import {actions as validationActions} from 'redux/modules/validation';
import LookupService from 'lib/lookupService';
import {actions as lookupDataActions} from 'redux/modules/lookupData';
import Logger from 'lib/logger';
import dynamicFormTypes from 'lib/dynamicFormTypes';
import {defineMessages} from 'react-intl';
import {toastr} from 'react-redux-toastr';
import locale from 'lib/locale';
import moment from 'moment';

export function * requestContractEdit ({contract}) {
  try {
    const customer = yield select(state => state.customer.activeCustomer);

    const contractEditn = yield api.contract.edit.get({
      customerId: customer.id,
      contractId: contract.currentActiveContractId,
      contractNr: contract.contractNr
    });

    const paymentMethod = yield api.paymentMethods.get({customerId: customer.id, paymentMethodId: contractEditn.paymentMethodId});

    const lookupDataGroups = LookupService.serviceDefinitionsToLookupDataGroups(contractEditn.lookupDataServiceDefinitions);

    yield [
      put(contractActions.receiveContractEdit(contractEditn, paymentMethod)),
      put(lookupDataActions.receiveLookupData(lookupDataGroups))
    ];
  } catch (error) {
    const e = yield error;
    yield put(contractActions.receiveContractEditFailure(e));
    Logger.error(e);
  }
}

export function * requestContractVersion ({contract}) {
  try {
    const [customer, contractToLoad] = yield [
      select(state => state.customer.activeCustomer),
      select(state => state.contract.contractToLoad)
    ];

    // in case no modals are used, set the contract to load for later usage
    if (!contractToLoad || contractToLoad.contractNr !== contract.contractNr) {
      yield put(contractActions.setContractToLoad(contract));
    }

    const contractVersion = yield api.contract.version.get({
      customerId: customer.id,
      contractId: contract.currentActiveContractId,
      contractNr: contract.contractNr
    });

    const paymentMethod = yield api.paymentMethods.get({customerId: customer.id, paymentMethodId: contractVersion.paymentMethodId});

    yield put(contractActions.receiveContractVersion({contractNr: contract.contractNr, ...contractVersion}, paymentMethod));
  } catch (error) {
    Logger.error(yield error);
  }
}

function * saveContractRequest ({product, customer}) {
  try {
    const productCalculationAnswers = yield select(state => getFormValues(dynamicFormTypes.calculation)(state));
    const productSaveAnswers = yield select(state => getFormValues(dynamicFormTypes.extraQuestions)(state));
    const productSaveInput = getKeyValuePairs({...productCalculationAnswers, ...productSaveAnswers}, product.options);
    const contract = yield api.contracts.post({customerId: customer.id}, {
      answers: productSaveInput,
      productId: product.id
    });

    yield put(contractActions.receiveSaveContract(contract.messages[0].insertedKey));
  } catch (error) {
    const e = yield error;
    yield put(validationActions.receiveValidation(dynamicFormTypes.extraQuestions, yield e));
    yield put(contractActions.receiveSaveContractFailure(e));
    Logger.error(e);
  }
}

function * terminateContractRequest ({contractId}) {
  try {
    const [activeCustomer, terminateAnswers] = yield [
      select(state => state.customer.activeCustomer),
      select(state => getFormValues(dynamicFormTypes.contractTerminate)(state))
    ];

    const terminationReason = {
      terminationDate: moment(terminateAnswers.Contract_1_DateTerminated, 'DD-MM-YYYY').toDate(),
      terminateReasonId: terminateAnswers.Contract_1_TerminateReason
    };

    yield api.contracts.terminate.post({customerId: activeCustomer.id, contractId}, terminationReason);
    const contracts = yield api.contracts.get({customerId: activeCustomer.id});

    yield put(contractActions.receiveContracts(contracts));
    toastr.success(locale.getTranslatedText(messages.contractTerminatedSuccess));
  } catch (error) {
    const e = yield error;
    yield put(validationActions.receiveValidation(dynamicFormTypes.contractTerminate, e));
    toastr.error(locale.getTranslatedText(messages.contractTerminatedError));
    Logger.error(e);
  }
}

function * requestContractEditSave ({answers}) {
  try {
    const [activeCustomer, calculateable] = yield [
      select(state => state.customer.activeCustomer),
      select(state => state.flow.calculateable)
    ];

    const res = yield api.contract.edit.post({
      customerId: activeCustomer.id,
      contractNr: calculateable.contractNr,
      contractId: calculateable.contractId
    }, answers);

    const newContractId = (res[0] && res[0].insertedKey) || res.messages[0].insertedKey;

    yield api.contract.sign.post({customerId: activeCustomer.id, contractNr: calculateable.contractNr, contractId: newContractId});

    yield put(contractActions.receiveContractEditSave());
    toastr.success(locale.getTranslatedText(messages.contractUpdatedSuccess));
  } catch (error) {
    const e = yield error;
    yield put(validationActions.receiveValidation(dynamicFormTypes.calculation, e));
    yield put(contractActions.receiveContractEditSaveFailure(e));
    toastr.error(locale.getTranslatedText(messages.contractUpdatedError));
    Logger.error(e);
  }
}

export function * requestSaveAndSignContract ({selectedPaymentMethod}) {
  try {
    const [productCalculationAnswers, productExtraQuestions, calculateable, activeCustomer] = yield [
      select(state => getFormValues(dynamicFormTypes.calculation)(state)),
      select(state => getFormValues(dynamicFormTypes.extraQuestions)(state)),
      select(state => state.flow.calculateable),
      select(state => state.customer.activeCustomer)
    ];

    const answers = getKeyValuePairs({...productExtraQuestions, ...productCalculationAnswers}, calculateable.options);

    if (calculateable.contractNr) {
      return yield requestContractEditSave({answers});
    }

    const contract = yield api.contracts.post({customerId: activeCustomer.id}, {
      answers,
      productId: calculateable.productId
    });

    const contractId = contract.messages[0].insertedKey;

    yield put(contractActions.receiveSaveContract(contractId));
    yield api.contracts.sign.post({
      customerId: activeCustomer.id,
      contractId
    }, {paymentMethodId: selectedPaymentMethod.id});
    yield put(contractActions.receiveContractSaveAndSign(selectedPaymentMethod));
  } catch (error) {
    const e = yield error;
    yield put(validationActions.receiveValidation(dynamicFormTypes.contract, e));
    yield put(validationActions.receiveValidation(dynamicFormTypes.paymentMethod, e));
    yield put(contractActions.receiveContractSaveAndSignFailure(e));
    Logger.error(e);
  }
}

function * requestContractTerminateReasons () {
  try {
    const terminateReasons = yield api.terminateReasons.get();
    yield put(contractActions.receiveContractTerminateReasons(terminateReasons));
  } catch (error) {
    const e = yield error;
    Logger.error(e);
  }
}

export function * contractEvents () {
  while (true) {
    yield [
      takeLatest(types.CONTRACT_EDIT_REQUEST, requestContractEdit),
      takeLatest(types.CONTRACT_VERSION_REQUEST, requestContractVersion),
      takeLatest(types.CONTRACT_SAVE_REQUEST, saveContractRequest),
      takeLatest(types.CONTRACT_TERMINATE_REQUEST, terminateContractRequest),
      takeLatest(types.CONTRACT_EDIT_SAVE_REQUEST, requestContractEditSave),
      takeLatest(types.CONTRACT_SAVE_AND_SIGN_REQUEST, requestSaveAndSignContract),
      takeLatest(types.CONTRACT_TERMINATE_REASONS_REQUEST, requestContractTerminateReasons)
    ];
  }
}

const messages = defineMessages({
  contractTerminatedSuccess: {
    id: 'contract.contractTerminatedSuccess',
    description: 'Message when contract terminated successfully',
    defaultMessage: 'Contract terminated successfully'
  },
  contractTerminatedError: {
    id: 'contract.contractTerminatedError',
    description: 'Message when contract could not be terminated',
    defaultMessage: 'Contract could not be terminated'
  },
  contractUpdatedSuccess: {
    id: 'contract.contractUpdatedSuccess',
    description: 'Message when contract updated successfully',
    defaultMessage: 'Contract updated successfully'
  },
  contractUpdatedError: {
    id: 'contract.contractUpdatedError',
    description: 'Message when contract could not be updated',
    defaultMessage: 'Contract could not be updated'
  }
});



