
		module.exports = function(context) {
			return (function() {
				

				if (!React) {
						var React = require("react");
					}

				return (
					<div>
  {this.props.errorMessage && <div>{this.props.errorMessage}</div>}
  { this.props.isChecking
  ? <this.Loader />
  : !this.props.isFeedbackSend
  ? <div>
      <this.FormattedMessage {...this.messages.intro} values={{customerName: this.props.customerName, caseName: this.props.caseName}}/>
      <form onSubmit={this.props.handleSubmit(input => this.handleSubmit(input))}>
        <div
          className={this.c('form-group', 'rating', this.props.fields.rating && this.props.fields.rating.touched && this.props.errors && this.props.errors.rating && 'has-error')}>
          <this.Field id='star5' name='rating' component='input' type='radio' value='5' /><label htmlFor='star5'>5</label>
          <this.Field id='star4' name='rating' component='input' type='radio' value='4' /><label htmlFor='star4'>4</label>
          <this.Field id='star3' name='rating' component='input' type='radio' value='3' /> <label htmlFor='star3'>3</label>
          <this.Field id='star2' name='rating' component='input' type='radio' value='2' /><label htmlFor='star2'>2</label>
          <this.Field id='star1' name='rating' component='input' type='radio' value='1' /><label htmlFor='star1'>1</label>
          {this.props.fields.rating && this.props.fields.rating.touched && this.props.errors && this.props.errors.rating && <p className='help-block'>{errors.rating}</p>}
        </div>
        <div className='form-group'>
          <label><this.FormattedMessage {...this.messages.remarksLabel}/></label>
          <this.Field id='feedback' className='form-control' name='comment' component='textarea'/>
        </div>
        <button type='submit' className='btn btn-primary' disabled={this.props.pristine || this.props.submitting || this.props.invalid}>Submit</button>
      </form>
    </div>
  : <this.FormattedMessage {...this.messages.thanks}/>}
</div>

				);
			}).apply(context);
		};
	


