import {takeLatest} from 'redux-saga';
import {put, select} from 'redux-saga/effects';
import {api} from 'lib/api';
import {types, actions as incidentActions} from 'redux/modules/incident';
import Logger from 'lib/logger';
import File from 'lib/file';

function * requestIncidents () {
  try {
    const customer = yield select(state => state.customer.activeCustomer);
    let incidents = yield api.incidents.get({customerId: customer.id});

    yield put(incidentActions.receiveIncidents(incidents));
  } catch (error) {
    const e = yield error;
    yield put(incidentActions.receiveIncidentsFailure(e));
    Logger.error(e);
  }
}

function * requestIncidentDocuments ({documentIds}) {
  try {
    let documents = yield File.getDocumentsMetaData(documentIds);
    yield put(incidentActions.receiveIncidentDocuments(documents));
  } catch (error) {
    const e = yield error;
    yield put(incidentActions.receiveIncidentDocumentsFailure(e));
    Logger.error(e);
  }
}

export function * incidentEvents () {
  while (true) {
    yield [
      takeLatest(types.REQUEST_INCIDENTS, requestIncidents),
      takeLatest(types.REQUEST_INCIDENT_DOCUMENTS, requestIncidentDocuments)
    ];
  }
}



