
		module.exports = function(context) {
			return (function() {
				

				if (!React) {
						var React = require("react");
					}

				return (
					<div>
  <div className='center-block' style={{width: '100px'}}>
    <i className='glyphicon glyphicon-refresh'></i>
    <b><this.FormattedMessage {...this.messages.loading} /></b>
  </div>
</div>

				);
			}).apply(context);
		};
	


