
		module.exports = function(context) {
			return (function() {
				

				if (!React) {
						var React = require("react");
					}

				return (
					<div>
  {this.props.calculation.paymentIntervalPremium
    ? <div className='cfe-premium'>
        <div>
          <this.FormattedMessage {...this.messages.amountBeforeTax} />:
          <this.FormattedNumber
            value={Math.round(this.props.calculation.paymentIntervalPremium.amountBeforeTax * 100) / 100}
            style='currency'
            currency='EUR'/>
        </div>
        <div>
          <this.FormattedMessage {...this.messages.tax} />:
          <this.FormattedNumber
            value={Math.round(this.props.calculation.paymentIntervalPremium.tax * 100) / 100}
            style='currency'
            currency='EUR'/>
        </div>
        <div>
          <this.FormattedMessage {...this.messages.amountAfterTax} />:
          <this.FormattedNumber
            value={Math.round(this.props.calculation.paymentIntervalPremium.amountAfterTax * 100) / 100}
            style='currency'
            currency='EUR'/>
        </div>
      </div>
    : <this.FormattedMessage {...this.messages.yourPremium} />}
</div>

				);
			}).apply(context);
		};
	


