
		module.exports = function(context) {
			return (function() {
				

				if (!React) {
						var React = require("react");
					}

				return (
					<div>
  <div className='clearfix'></div>
    <div>
      <label>
        <input onChange={this.changeContractTypeVisiblity} type='checkbox'></input>
        {this.formatMessage(this.messages.inactiveContractsSwitchMessage)}
      </label>
    </div>
  { this.contractsToDisplay.length > 0
    ? <div>
        <table className='table table-hover'>
          <thead>
          <tr>
            <th></th>
            <th>{this.formatMessage(this.messages.contractNumber)}</th>
            <th>{this.formatMessage(this.messages.contractType)}</th>
            <th>{this.formatMessage(this.messages.contractStartDate)}</th>
            <th>{this.formatMessage(this.messages.contractTerminationDate)}</th>
            <th> </th>
          </tr>
          </thead>
          <tbody>
          {this.contractsToDisplay.map((contract) => {
            const isCurrentRowExpanded = this.state.openedContractNumber === contract.contractNr;

            return [
              <tr key={contract.contractId}>
                <td>
                  <span
                    onClick={() => this.openContractVersions(contract.contractNr)}
                    className={this.c('glyphicon',
                                     {'hidden': !contract.contractHasMultipleVersions},
                                     {'glyphicon-menu-right': !isCurrentRowExpanded},
                                     {'glyphicon-menu-down': isCurrentRowExpanded})}>
                  </span>
                </td>
                <td>{contract.contractNr}</td>
                <td>
                  {contract.productName}
                  {contract.contractHasFutureVersion &&
                  <this.Tooltip  id={contract.contractNr + 'futureVersion'} title={this.formatMessage(this.messages.contractHasFutureVersionMessage)}>
                    <span className='glyphicon glyphicon-info-sign'/>
                  </this.Tooltip >}
                </td>
                <td>
                  {this.util.formatDate(contract.dateStart)}
                  {contract.isTerminationDateInTheFuture &&
                  <this.Tooltip  id={contract.contractNr + 'futureTermination'} title={this.formatMessage(this.messages.contractHasTerminationDateInTheFutureMessage)}>
                    <span className='glyphicon glyphicon-alert'/>
                  </this.Tooltip >
                  }
                </td>
                <td>
                  {!contract.isTerminationDateNotSpecified && this.util.formatDate(contract.dateTerminated)}
                </td>
                <td>
                  {contract.isActive && <span onClick={() => this.props.handleClickView(contract)} className='glyphicon glyphicon-eye-open'/>}
                  {contract.isActive && <span onClick={() => this.props.handleClickRemove(contract)} className='glyphicon glyphicon-remove'/>}
                </td>
              </tr>,
              <tr key={contract.contractNumber + 'sub-items'} className={this.c({'hidden': !isCurrentRowExpanded})}>
                <td colSpan='6'>
                  <this.ContractVersionsList contractVersions={contract.contractVersions}/>
                </td>
              </tr>];
          })}
          </tbody>
        </table>
      </div>
    : this.formatMessage(this.messages.noContractsMessage)
  }
</div>

				);
			}).apply(context);
		};
	


