import React from 'react';
import DynamicOptionsFormEnhancer from 'enhancers/DynamicOptionsFormEnhancer';
import lookupData from 'enhancers/lookupData';

class Product extends React.PureComponent {
  static displayName = 'Product';

  render () {
    return this.props.form;
  }
}

export default lookupData(DynamicOptionsFormEnhancer(Product));



