import React, {PropTypes} from 'react';
import DossierDocumentsView from 'views/DossierDocumentsView/DossierDocumentsView';
import RootContainer from 'containers/RootContainer';

export default class DossierDocumentsContainer extends React.Component {
  static propTypes = {
    store: PropTypes.object.isRequired,
    locale: PropTypes.string.isRequired,
    messages: PropTypes.object.isRequired
  };

  render () {
    return (
      <RootContainer {...this.props}>
        <DossierDocumentsView />
      </RootContainer>
    );
  }
}



