import {PropTypes} from 'react';
import {connect} from 'react-redux';
import CfeComponent from 'components/CfeComponent';
import Customer from 'components/Customer';
import RegisterForm from 'forms/RegisterForm';
import LoginForm from 'forms/LoginForm';
import Calculateable from 'lib/calculateable';
import CustomerClass from 'lib/customer';
import dynamicFormTypes from 'lib/dynamicFormTypes';
import {actions as offerActions} from 'redux/modules/offer';
import {actions as customerActions} from 'redux/modules/customer';

class OfferRegistrationView extends CfeComponent {
  static displayName = 'OfferRegistrationView';

  constructor (props) {
    super(props, 'views/OfferRegistrationView/OfferRegistrationView');
  }

  componentWillMount () {
    this.props.checkForOfferHash();
  }

  handleSubmitCustomer = (newCustomerData) => this.props.requestSaveCustomer(this.props.customer, newCustomerData);

  getStepQuestions () {
    if (this.props.customer.options && this.props.customerConfig && this.props.customerConfig.questions) {
      return this.props.customerConfig.questions.map(q => this.props.customer.options.filter(o => o.id === q.id)[0]);
    }

    return this.props.customer.options;
  }

  render () {
    const {customer} = this.props;
    customer.options = this.getStepQuestions();

    const context = {
      ...this,
      Customer,
      LoginForm,
      RegisterForm,
      dynamicFormTypes
    };

    return super.render(context);
  }
}

OfferRegistrationView.propTypes = {
  customer: PropTypes.object,
  checkForOfferHash: PropTypes.func,
  customerConfig: PropTypes.object
};

const mapStateToProps = (state) => ({
  calculateable: new Calculateable(state.flow.calculateable),
  customer: new CustomerClass(state.customer.activeCustomer || state.customer.customerTemplate),
  user: state.auth.user,
  customerConfig: state.config.customerConfig,
  isSavingCustomer: state.customer.isSavingCustomer,
  hasActiveCustomer: !!state.customer.activeCustomer,
  offer: state.offer
});

const mapDispatchToProps = (dispatch) => ({
  checkForOfferHash: () => dispatch(offerActions.checkForOfferHash()),
  requestSaveCustomer: (customer, newCustomerData) => dispatch(customerActions.requestSaveCustomer(customer, newCustomerData))
});

export default connect(mapStateToProps, mapDispatchToProps)(OfferRegistrationView);



