import {PropTypes} from 'react';
import c from 'classnames';
import util from 'lib/util';
import {injectIntl, defineMessages} from 'react-intl';
import Tooltip from 'components/Tooltip';
import ContractVersionsList from 'components/Contract/versionsList';
import CfeComponent from 'components/CfeComponent';
import moment from 'moment';

class ContractList extends CfeComponent {
  constructor (props) {
    super(props, 'components/Contract/list');
    this.state = {
      isDisplayingInactiveContracts: false
    };
  }

  openContractVersions = (contractNumber) => {
    const contractToOpen = this.state.openedContractNumber !== contractNumber ? contractNumber : null;
    this.setState({
      openedContractNumber: contractToOpen
    });
  };

  changeContractTypeVisiblity = () => {
    this.setState({isDisplayingInactiveContracts: !this.state.isDisplayingInactiveContracts});
  };

  render () {
    const {intl: {formatMessage}, contracts} = this.props;
    const sortedContracts = contracts.sort((c1, c2) => {
      const dateDiff = moment.utc(c2.dateStart).diff(moment.utc(c1.dateStart));
      if (dateDiff === 0) {
        return c2.contractNr - c1.contractNr;
      } else {
        return dateDiff;
      }
    });
    const contractsToDisplay = this.state.isDisplayingInactiveContracts ? sortedContracts : sortedContracts.filter(c => c.isActive);

    const context = {
      ...this,
      contractsToDisplay,
      messages,
      c,
      formatMessage,
      ContractVersionsList,
      Tooltip,
      util
    };

    return super.render(context);
  }
}

ContractList.propTypes = {
  contracts: PropTypes.array.isRequired,
  intl: PropTypes.object.isRequired,
  handleClickView: PropTypes.func,
  handleClickRemove: PropTypes.func
};

export default injectIntl(ContractList);

const messages = defineMessages({
  contractHasFutureVersionMessage: {
    id: 'contract.futureVersion',
    description: 'Message shown when a contract has a future version',
    defaultMessage: 'This contract has a future version defined'
  },
  contractHasTerminationDateInTheFutureMessage: {
    id: 'contract.futureTerminationDate',
    description: 'Message shown when a contract has a termination date in the future',
    defaultMessage: 'This contract has a termination date in the future'
  },
  inactiveContractsSwitchMessage: {
    id: 'contract.inactiveContractsSwitchMessage',
    description: 'Label shown on the switch for showing active/inactive contracts',
    defaultMessage: 'Show inactive contracts'
  },
  contractNumber: {
    id: 'contract.number',
    description: 'Table title',
    defaultMessage: 'Contract number'
  },
  contractType: {
    id: 'contract.type',
    description: 'Table title',
    defaultMessage: 'Contract type'
  },
  contractStartDate: {
    id: 'contract.startDate',
    description: 'Table title',
    defaultMessage: 'Start date'
  },
  contractTerminationDate: {
    id: 'contract.terminationDate',
    description: 'Table title',
    defaultMessage: 'Termination date'
  },
  noContractsMessage: {
    id: 'contract.noContractMessage',
    description: 'Shown when the user has no contracts',
    defaultMessage: 'You don\'t have any contracts'
  }
});



