import {PropTypes} from 'react';
import CfeComponent from 'components/CfeComponent';

export default class Modal extends CfeComponent {
  static propTypes = {
    id: PropTypes.string.isRequired,
    children: PropTypes.element.isRequired,
    title: PropTypes.string,
    closeCallback: PropTypes.func
  };

  constructor (props) {
    super(props, 'components/Modal/index');
  }

  componentWillReceiveProps (nextProps) {
    if (this.isTemplateOverriden) {
      super.componentWillReceiveProps(nextProps);
    } else {
      let $modal = jQuery(`#${this.props.id}`);

      if (nextProps.open) {
        $modal.modal();
      } else {
        $modal.modal('hide');
      }
    }
  }

  componentDidMount () {
    if (this.isTemplateOverriden) {
      super.componentDidMount();
    } else {
      jQuery(`#${this.props.id}`).on('hidden.bs.modal', this.props.closeCallback);
    }
  }

  render () {
    return super.render(this);
  }
}



