import {takeLatest} from 'redux-saga';
import {put, select} from 'redux-saga/effects';
import {api} from 'lib/api';
import {types, actions as emailActions} from 'redux/modules/email';
import Logger from 'lib/logger';

function * requestEmails () {
  try {
    const customer = yield select(state => state.customer.activeCustomer);
    let emails = yield api.emails.get({customerId: customer.id});

    yield put(emailActions.receiveEmails(emails));
  } catch (error) {
    const e = yield error;
    yield put(emailActions.receiveEmailsFailure(e));
    Logger.error(e);
  }
}

function * requestEmailBody ({email}) {
  try {
    const customer = yield select(state => state.customer.activeCustomer);
    let emailBodyAttachmentFiles = yield api.emails.get({customerId: customer.id, emailId: email.emailId});

    yield put(emailActions.receiveEmailBody(emailBodyAttachmentFiles));
  } catch (error) {
    Logger.error(yield error);
  }
}

export function * emailEvents () {
  while (true) {
    yield [
      takeLatest(types.EMAILS_REQUEST, requestEmails),
      takeLatest(types.EMAIL_BODY_REQUEST, requestEmailBody)
    ];
  }
}



