import React, {PropTypes, Component} from 'react';
import DynamicOptionsForm from 'forms/DynamicOptionsForm';
import StepByStepQuestionsForm from 'forms/StepByStepQuestionsForm';
import Loader from 'components/Loader';
import Validator from 'lib/validator';
import {mapValues} from 'lodash';
import Option from 'lib/option';
import formComponentTypes from 'lib/formComponentTypes';
import {initialize} from 'redux-form';
import {connect} from 'react-redux';

const formComponents = {
  [formComponentTypes.dynamicOptionsForm]: DynamicOptionsForm,
  [formComponentTypes.stepByStepQuestions]: StepByStepQuestionsForm
};

export default (ComposedComponent) => {
  class DynamicOptionsFormEnhanced extends Component {
    componentWillMount () {
      this.props.initializeForm(this.getInitialValues());
    }

    getQuestions = () => this.props.additionalOptions
      ? Option.mergeOptions(this.props.options, this.props.additionalOptions)
      : this.props.options || [];

    getInitialValues = () => this.getQuestions().reduce((memo, q) => ({...memo, [q.id]: q.getValue()}), {});

    isQuestionRelevant = (question) => question.isQuestionRelevant(this.props.answers, this.getQuestions());

    // don't show readOnly fields except from serviceLocator additionalOptions
    getRelevantQuestions = () =>
      this.getQuestions()
        .filter(q => this.isQuestionRelevant(q) && (!q.isReadonly || (this.props.additionalOptions || []).map(o => o.id).includes(q.id)));

    handleValidation = (answers) => {
      const rules = Validator.buildOptionRules(this.getRelevantQuestions());
      return mapValues(Validator.check(rules, answers), errors => errors[0]);
    };

    render () {
      if (!(this.getQuestions() && this.props.formComponent)) return <Loader />;

      const form = React.createElement(formComponents[this.props.formComponent], {
        isQuestionRelevant: this.isQuestionRelevant,
        answers: this.props.answers,
        enableReinitialize: this.props.enableReinitialize || false,
        shouldSubmitWhenValid: this.props.shouldSubmitWhenValid,
        hideSubmitButton: this.props.hideSubmitButton,
        readOnly: this.props.readOnly,
        onSubmit: this.props.handleSubmit,
        validate: this.handleValidation,
        isLoading: this.props.isLoading,
        form: this.props.formName,
        questionsToRender: this.getRelevantQuestions()
      });

      return <ComposedComponent {...this.props} form={form}/>;
    }
  }

  DynamicOptionsFormEnhanced.propTypes = {
    options: PropTypes.array,
    additionalOptions: PropTypes.array,
    handleSubmit: PropTypes.func,
    formName: PropTypes.string.isRequired,
    readOnly: PropTypes.bool,
    hideSubmitButton: PropTypes.bool,
    answers: PropTypes.object,
    shouldSubmitWhenValid: PropTypes.bool,
    enableReinitialize: PropTypes.bool,
    isLoading: PropTypes.bool,
    formComponent: PropTypes.string,
    initializeForm: PropTypes.func
  };

  const mapDispatchToProps = (dispatch, ownProps) => ({
    initializeForm: initialValues => dispatch(initialize(ownProps.formName, initialValues))
  });

  return connect(null, mapDispatchToProps)(DynamicOptionsFormEnhanced);
};



